const { Model, DataTypes } = require('../services/database');
const { unifiedDatabase } = require('../services/database-connection-manager');

//   Sequelize 
let sequelizeInstance = null;
async function getSequelize() {
  if (!sequelizeInstance) {
    const result = await unifiedDatabase.getSequelizeInstance();
    sequelizeInstance = result.db;
  }
  return sequelizeInstance;
}

class InventoryItem extends Model {
  static associate(models) {
    //     
    InventoryItem.belongsTo(models.User, {
      foreignKey: 'userId',
      as: 'user'
    });
  }
}

// :   super   
InventoryItem.init = async function() {
  const sequelize = await getSequelize();
  
  //   init  
  return Model.init.call(this, {
  //   ID   
  id: {
    type: DataTypes.INTEGER,
    autoIncrement: true,
    primaryKey: true,
    allowNull: false
  },
  userId: {
    type: DataTypes.INTEGER,
    references: {
      model: 'users', //    lowercase
      key: 'id'
    },
    onUpdate: 'CASCADE',
    onDelete: 'CASCADE'
    //  userId  primaryKey   unique,      
  },
  //  itemId ,      
  name: { //  name  
    type: DataTypes.STRING,
    allowNull: false
  },
  description: { //   description  
    type: DataTypes.TEXT
  },
  type: { //  type  
    type: DataTypes.ENUM('', '', '', '', ''),
    allowNull: false
  },
  rarity: { //  rarity  
    type: DataTypes.ENUM('', '', '', '', ''),
    defaultValue: ''
  },
  stats: { //   properties,  stats  
    type: DataTypes.JSONB, //  JSONB  PostgreSQL
    defaultValue: {}
  },
  quantity: { //  quantity  
    type: DataTypes.INTEGER,
    defaultValue: 1
  },
  equipped: { //  equipped  
    type: DataTypes.BOOLEAN,
    defaultValue: false
  }
  //  calculatedBonuses 
  // Sequelize   createdAt  updatedAt
  }, {
    sequelize,
    modelName: 'InventoryItem',
    tableName: 'inventory_items', // :    snake_case
    timestamps: true,           //     
    underscored: true           //  snake_case  createdAt  updatedAt (created_at, updated_at)
  });
};

//      
(async () => {
  try {
    await InventoryItem.init();
    console.log('InventoryItem  ');
  } catch (error) {
    console.error('   InventoryItem:', error);
    console.error(error.stack);
  }
})();

module.exports = InventoryItem;
