/**
 *           
 * @param {number} basePrice   
 * @param {number} merchantReputation  
 * @param {number} playerReputation    
 * @returns {number}   
 */
function calculatePrice(basePrice, merchantReputation, playerReputation) {  
  //       
  const basePriceMultiplier = 1 + (100 - merchantReputation) / 100;
  const adjustedBasePrice = Math.round(basePrice * basePriceMultiplier);
  
  //    /   
  const { finalPrice } = applyLoyaltyDiscount(adjustedBasePrice, playerReputation);
  
  //  
  return Math.round(finalPrice);
}

/**
 *           
 * @param {number} currentGameTime    ( )
 * @returns {Promise<boolean>}  true,    
 */
async function restockMerchantItems(currentGameTime) {
  try {
    //   Sequelize
    const seq = await getSequelizeInstance();

    let restockHappened = false;
    
    //      
    const allItems = await unifiedDatabase.query(
      'SELECT * FROM merchant_inventories',
      { type: seq.QueryTypes.SELECT }
    );
    
    //   
    for (const item of allItems) {
      //   
      if (item.quantity === -1) continue;
      
      //  ,        
      if (item.maxQuantity === undefined || item.restockRate === undefined || item.lastRestockTime === undefined) continue;
      
      //    , 
      if (item.quantity >= item.maxQuantity) continue;
      
      // ,       ( )
      const daysSinceLastRestock = (currentGameTime - item.lastRestockTime) / 24;
      
      //     0.1 , 
      if (daysSinceLastRestock < 0.1) continue;
      
      // ,    
      const unitsToRestock = Math.floor(daysSinceLastRestock * item.restockRate);
      
      if (unitsToRestock > 0) {
        //  ,     
        const oldQuantity = item.quantity;
        item.quantity = Math.min(item.maxQuantity, item.quantity + unitsToRestock);
        
        //    
        item.lastRestockTime = currentGameTime;
        
        //  
        await unifiedDatabase.query(
          'UPDATE merchant_inventories SET quantity = :quantity, "lastRestockTime" = :lastRestockTime WHERE id = :id',
          {
            replacements: { quantity: item.quantity, lastRestockTime: item.lastRestockTime, id: item.id },
            type: seq.QueryTypes.UPDATE
          }
        );
        
        // ,   
        if (oldQuantity !== item.quantity) {
          restockHappened = true;
        }
      }
    }
    
    //   ,   
    if (merchants && merchants.length > 0 && restockHappened) {
      merchants = await getAllMerchants();
    }
    
    return restockHappened;
  } catch (error) {
    console.error('   :', error);
    
    //       
    let cacheRestockHappened = false;
    
    merchants.forEach(merchant => {
      if (merchant.items && merchant.items.length > 0) {
        merchant.items.forEach(item => {
          if (item.maxQuantity !== undefined && 
              item.restockRate !== undefined && 
              item.lastRestockTime !== undefined &&
              item.quantity < item.maxQuantity) {
            
            const daysSinceLastRestock = (currentGameTime - item.lastRestockTime) / 24;
            
            if (daysSinceLastRestock >= 0.1) {
              const unitsToRestock = Math.floor(daysSinceLastRestock * item.restockRate);
              
              if (unitsToRestock > 0) {
                const oldQuantity = item.quantity;
                item.quantity = Math.min(item.maxQuantity, item.quantity + unitsToRestock);
                item.lastRestockTime = currentGameTime;
                
                if (oldQuantity !== item.quantity) {
                  cacheRestockHappened = true;
                }
              }
            }
          }
        });
      }
    });
    
    return cacheRestockHappened;
  }
}

/**
 *        
 * @param {Object} merchant ,    
 * @returns {Promise<Object>}   
 */
async function addEquipmentToMerchant(merchant) {
  console.log(`  addEquipmentToMerchant  : ${merchant?.name || ''}`);
  
  if (!merchant) {
    console.warn('   ,   ');
    return merchant;
  }
  
  try {
    //   Sequelize
    const seq = await getSequelizeInstance();

    //    
    const [merchantFromDB] = await unifiedDatabase.query(
      'SELECT * FROM merchants WHERE id = :id',
      {
        replacements: { id: merchant.id },
        type: seq.QueryTypes.SELECT
      }
    );
    
    if (!merchantFromDB) {
      console.warn(`  ID ${merchant.id}    ,   `);
      return merchant;
    }
    
    //   
    return convertMerchantFromDB(merchantFromDB);
  } catch (error) {
    console.error(`      ${merchant.name}:`, error);
    return merchant;
  }
}

/**
 *         
 * @param {Array} merchantsArray  
 * @returns {Promise<Array>}       
 */
async function addAlchemyItemsToMerchants(merchantsArray) {
  try {
    //     
    return await getAllMerchants();
  } catch (error) {
    console.error('    :', error);
    return merchantsArray;
  }
}

//  
async function initMerchantData() {
  try {
    merchants = await getAllMerchants();
    return merchants;
  } catch (error) {
    console.error('    :', error);
    return merchants;
  }
}

//     
initMerchantData().catch(error => {
  console.error('    :', error);
});

//      
let merchants = [];

module.exports = {
  merchants,
  getAllMerchants,
  getMerchantById,
  getMerchantsByLocation,
  getMerchantsBySpecialization,
  calculatePrice,
  restockMerchantItems,
  addEquipmentToMerchant,
  addAlchemyItemsToMerchants
};
