import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { useGame } from '../../context/GameContext';
import ACTION_TYPES from '../../context/actions/actionTypes';

const Container = styled.div`
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 20px;
  overflow-y: auto;
  max-height: calc(100vh - 150px);
`;

const SectInfo = styled.div`
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 8px;
  padding: 20px;
`;

const SectHeader = styled.div`
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(212, 175, 55, 0.2);
  text-align: center;
`;

const SectName = styled.h2`
  color: #d4af37;
  margin: 0 0 5px;
`;

const SectRank = styled.div`
  color: #aaa;
  font-size: 0.9rem;
  margin-bottom: 10px;
`;

const SectStats = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin-bottom: 20px;
`;

const StatItem = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
`;

const StatLabel = styled.span`
  color: #aaa;
`;

const StatValue = styled.span`
  color: #f0f0f0;
`;

const MembersList = styled.div`
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 8px;
  padding: 20px;
`;

const MembersHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(212, 175, 55, 0.2);
`;

const MembersTitle = styled.h3`
  color: #d4af37;
  margin: 0;
`;

const MembersCount = styled.span`
  color: #aaa;
`;

//  MemberItem    ,   

const MemberInfo = styled.div`
  display: flex;
  flex-direction: column;
`;

const MemberName = styled.div`
  color: #f0f0f0;
  margin-bottom: 5px;
`;

const MemberRole = styled.div`
  color: #aaa;
  font-size: 0.8rem;
`;

const MemberLevel = styled.div`
  color: #d4af37;
`;

const ActionButton = styled.button`
  width: 100%;
  padding: 10px;
  background: rgba(212, 175, 55, 0.2);
  border: 1px solid #d4af37;
  border-radius: 4px;
  color: #d4af37;
  font-size: 0.9rem;
  cursor: pointer;
  margin-bottom: 10px;
  transition: all 0.2s ease;
  
  &:hover {
    background: rgba(212, 175, 55, 0.3);
  }
  
  &:disabled {
    background: #333;
    border-color: #666;
    color: #666;
    cursor: not-allowed;
  }
`;

const ContributionPanel = styled.div`
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid rgba(212, 175, 55, 0.2);
`;

const ContributionTitle = styled.h3`
  color: #d4af37;
  margin: 0 0 10px;
`;

const ContributionDescription = styled.p`
  color: #f0f0f0;
  font-size: 0.9rem;
  line-height: 1.4;
  margin: 0 0 15px;
`;

//    ,     
const defaultSect = {
  name: '  ',
  rank: '',
  influence: 100,
  resources: 50,
  territories: 1,
  benefits: [
    {type: 'cultivation_speed', modifier: 5},
    {type: 'resource_gathering', modifier: 3},
    {type: 'energy_regen', modifier: 1},
    {type: 'technique_discount', modifier: 2},
    {type: 'max_energy', modifier: 10}
  ],
  members: [
    { id: 1, name: ' ', role: ' ', cultivationLevel: 30 },
    { id: 2, name: ' ', role: '', cultivationLevel: 25 },
    { id: 3, name: ' ', role: ' ', cultivationLevel: 15 },
    { id: 4, name: ' ', role: ' ', cultivationLevel: 12 },
    { id: 5, name: ' ', role: ' ', cultivationLevel: 8 }
  ]
};

function SectTab() {
  const { state, actions } = useGame();
  const [selectedMember, setSelectedMember] = useState(null);
  
  //        
  const sect = state?.player?.sect || defaultSect;
  const cultivation = state?.player?.cultivation || {};
  
  //       
  useEffect(() => {
    const loadSectData = async () => {
      try {
        //        ,  
        if (!state.sect?.sect && actions.loadUserSect) {
          await actions.loadUserSect();
        }
      } catch (error) {
        console.error('     :', error);
        actions.addNotification({
          message: '     ',
          type: 'error'
        });
      }
    };
    
    loadSectData();
  }, []);
  
  //    
  const sectState = state.sect || {};
  const sectData = sectState.sect || defaultSect;
  
  //       
  const { collectAllSectBenefits, getBenefitValueByType } = require('../../utils/sectUtils');
  
  //    
  const allSectBenefits = collectAllSectBenefits(sectState);
  
  //       
  const getBenefitValue = (type) => getBenefitValueByType(allSectBenefits, type, 0);
  
  //     
  const handleContribute = async () => {
    const energyAmount = 20;
    
    // ,   
    if ((cultivation.energy || 0) < energyAmount) {
      actions.addNotification({
        message: '      ',
        type: 'error'
      });
      return;
    }
    
    try {
      if (actions.contributeToSect) {
        //    
        const result = await actions.contributeToSect(state.player.id, sectData.id, energyAmount);
        
        //    
        actions.addNotification({
          message: result.message || '     ',
          type: 'success'
        });
        
        //    ,   
        if (result.leveledUp) {
          actions.addNotification({
            message: ` "${sectData.name}"  ${result.newLevel} !`,
            type: 'achievement'
          });
          
          //      UI   
          const updatedSectData = { 
            ...sectData, 
            level: result.newLevel,
            experience: result.sectExperience,
            requiredExperience: result.sectRequiredExperience,
            rank: result.sectRank,
            influence: result.sectInfluence
          };
          
          //   Redux-
          actions.dispatch({ 
            type: ACTION_TYPES.UPDATE_SECT, 
            payload: updatedSectData 
          });
          
          //      
          actions.dispatch({
            type: ACTION_TYPES.UPDATE_RELATIONSHIP_AND_LOYALTY,
            payload: {
              name: sectData.name,
              value: updatedSectData.loyalty || 0
            }
          });
        }
      }
    } catch (error) {
      console.error('     :', error);
      actions.addNotification({
        message: error.message || '     ',
        type: 'error'
      });
    }
  };
  
  //     
  const handleMemberClick = (member) => {
    console.log(`[DEBUG]   :`, member);
    
    // ,      localStorage
    if (typeof window !== 'undefined') {
      try {
        const savedData = localStorage.getItem(`sect_member_${member.id}`);
        if (savedData) {
          const savedMember = JSON.parse(savedData);
          console.log(`[DEBUG]    localStorage:`, savedMember);
          
          //    localStorage
          member = {
            ...member,
            level: savedMember.level || member.level,
            experience: savedMember.experience || member.experience,
            requiredExperience: savedMember.requiredExperience || member.requiredExperience,
            loyalty: savedMember.loyalty || member.loyalty
          };
          
          console.log(`[DEBUG]       localStorage:`, member);
        }
      } catch (e) {
        console.error('       localStorage:', e);
      }
    }
    
    setSelectedMember(member);
    
    if (actions.selectSectMember) {
      actions.selectSectMember(member);
    }
  };
  
    //     
    const handleTrainMember = async (memberId) => {
      if (!selectedMember) return;
      
      const duration = 1; //  
      const energyAmount = duration * 5; // 5       (    sect-service.js)
      
      // ,   
      if ((cultivation.energy || 0) < energyAmount) {
        actions.addNotification({
          message: '    ',
          type: 'error'
        });
        return;
      }
    
    try {
      if (actions.trainSectMember) {
        console.log('[DEBUG]     :', selectedMember);
        
        //   
        const result = await actions.trainSectMember(state.player.id, memberId, duration);
        
        console.log('[DEBUG]  :', result);
        
        //    
        actions.addNotification({
          message: result.message || `    ${selectedMember.name}`,
          type: 'success'
        });
        
        //   
        if (result.userGainedXP > 0) {
          actions.addNotification({
            message: `  ${result.userGainedXP}    `,
            type: 'info'
          });
        }
        
        //     ,   
        if (result.memberLeveledUp) {
          actions.addNotification({
            message: `${result.memberName}    ${result.memberNewLevel}!`,
            type: 'achievement'
          });
        }
        
        //        
        const updatedMember = {
          ...selectedMember,
          level: result.memberLevel,
          experience: result.memberExperience,
          requiredExperience: result.memberRequiredExperience,
          loyalty: result.memberLoyalty,
          role: result.memberRole
        };
        
        console.log('[DEBUG]      UI:', updatedMember);
        
        //     
        setSelectedMember(updatedMember);
        
        //       Redux
        //       
        const updatedSectData = {
          ...sectData,
          members: sectData.members.map(member => 
            member.id === memberId 
              ? {
                  ...member,
                  level: result.memberLevel,
                  experience: result.memberExperience,
                  requiredExperience: result.memberRequiredExperience,
                  loyalty: result.memberLoyalty,
                  role: result.memberRole
                }
              : member
          )
        };
        
        //   Redux-
        actions.dispatch({ 
          type: ACTION_TYPES.UPDATE_SECT, 
          payload: updatedSectData 
        });
      }
    } catch (error) {
      console.error('    :', error);
      actions.addNotification({
        message: error.message || '   ',
        type: 'error'
      });
    }
  };
  
  return (
    <Container>
      <SectInfo>
        <SectHeader>
          <SectName>{sectData.name}</SectName>
          <SectRank>: {sectData.rank}</SectRank>
        </SectHeader>
        
        <SectStats>
          <StatItem>
            <StatLabel></StatLabel>
            <StatValue>{sectData.level || 1}</StatValue>
          </StatItem>
          <StatItem>
            <StatLabel></StatLabel>
            <StatValue>{sectData.experience || 0}/{sectData.requiredExperience || 100}</StatValue>
          </StatItem>
          <StatItem>
            <StatLabel></StatLabel>
            <StatValue>{sectData.influence || 0}</StatValue>
          </StatItem>
          <StatItem>
            <StatLabel></StatLabel>
            <StatValue>{sectData.resources || 0}</StatValue>
          </StatItem>
          <StatItem>
            <StatLabel></StatLabel>
            <StatValue>{sectData.territories || 0}</StatValue>
          </StatItem>
          <StatItem>
            <StatLabel></StatLabel>
            <StatValue>{(sectData.members || []).length}</StatValue>
          </StatItem>
        </SectStats>
        
        {/*     */}
        <BenefitsSection>
          <BenefitsTitle> </BenefitsTitle>
          <BenefitsList>
            <BenefitItem>
              <BenefitLabel> </BenefitLabel>
              <BenefitValue>+{getBenefitValue('cultivation_speed')}%</BenefitValue>
            </BenefitItem>
            <BenefitItem>
              <BenefitLabel> </BenefitLabel>
              <BenefitValue>+{getBenefitValue('resource_gathering')}%</BenefitValue>
            </BenefitItem>
            <BenefitItem>
              <BenefitLabel> </BenefitLabel>
              <BenefitValue>+{getBenefitValue('energy_regen')} /</BenefitValue>
            </BenefitItem>
            <BenefitItem>
              <BenefitLabel>  </BenefitLabel>
              <BenefitValue>-{getBenefitValue('technique_discount')}%</BenefitValue>
            </BenefitItem>
            <BenefitItem>
              <BenefitLabel>. </BenefitLabel>
              <BenefitValue>+{getBenefitValue('max_energy')} </BenefitValue>
            </BenefitItem>
          </BenefitsList>
        </BenefitsSection>
        
        <ContributionPanel>
          <ContributionTitle>  </ContributionTitle>
          <ContributionDescription>
                 ,   .
                    .
          </ContributionDescription>
          
          <ActionButton
            onClick={handleContribute}
            disabled={(cultivation.energy || 0) < 20}
          >
              (20 )
          </ActionButton>
        </ContributionPanel>
      </SectInfo>
      
      <MembersList>
        <MembersHeader>
          <MembersTitle> </MembersTitle>
          <MembersCount>{(sectData.members || []).length} </MembersCount>
        </MembersHeader>
        
        {(sectData.members || []).map(member => (
          <MemberItem
            key={member.id}
            onClick={() => handleMemberClick(member)}
            selected={selectedMember && selectedMember.id === member.id}
          >
            <MemberInfo>
              <MemberName>{member.name}</MemberName>
              <MemberRole>{member.role}</MemberRole>
            </MemberInfo>
            <MemberLevel>
              {member.level || 1} 
              {member.loyalty && <MemberLoyalty>: {member.loyalty}%</MemberLoyalty>}
            </MemberLevel>
          </MemberItem>
        ))}
        
        {selectedMember && (
          <MemberDetails>
            <MemberDetailsHeader>
              <MemberDetailName>{selectedMember.name}</MemberDetailName>
              <MemberDetailRole>{selectedMember.role}</MemberDetailRole>
            </MemberDetailsHeader>
            
            <MemberProgress>
              <MemberProgressLabel>: {selectedMember.experience || 0}/{selectedMember.requiredExperience || 100}</MemberProgressLabel>
              <ProgressBar 
                current={selectedMember.experience || 0} 
                max={selectedMember.requiredExperience || 100} 
              />
            </MemberProgress>
            
            <MemberTrainingInfo>
              <TrainingInfoText>
                         .
                        .
              </TrainingInfoText>
            </MemberTrainingInfo>
            
            <ActionButton
              onClick={() => handleTrainMember(selectedMember.id)}
              disabled={(cultivation.energy || 0) < 5 || sectState.loading}
            >
              {sectState.loading ? '...' : ` ${selectedMember.name} (5 )`}
            </ActionButton>
          </MemberDetails>
        )}
      </MembersList>
    </Container>
  );
}

//  
const BenefitsSection = styled.div`
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid rgba(212, 175, 55, 0.2);
`;

const BenefitsTitle = styled.h3`
  color: #d4af37;
  margin: 0 0 10px;
  font-size: 1rem;
`;

const BenefitsList = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 8px;
`;

const BenefitItem = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
`;

const BenefitLabel = styled.span`
  color: #aaa;
  font-size: 0.8rem;
`;

const BenefitValue = styled.span`
  color: #d4af37;
  font-size: 0.8rem;
`;

const MemberDetails = styled.div`
  margin-top: 20px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  border: 1px solid rgba(212, 175, 55, 0.3);
`;

const MemberDetailsHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
`;

const MemberDetailName = styled.h3`
  color: #d4af37;
  margin: 0;
  font-size: 1.1rem;
`;

const MemberDetailRole = styled.span`
  color: #aaa;
  font-size: 0.9rem;
`;

const MemberProgress = styled.div`
  margin-bottom: 15px;
`;

const MemberProgressLabel = styled.div`
  color: #f0f0f0;
  font-size: 0.9rem;
  margin-bottom: 5px;
`;

const ProgressBar = styled.div`
  height: 8px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  overflow: hidden;
  position: relative;
  
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: ${props => Math.min(100, (props.current / props.max) * 100)}%;
    background: #d4af37;
    border-radius: 4px;
  }
`;

const MemberTrainingInfo = styled.div`
  margin-bottom: 15px;
`;

const TrainingInfoText = styled.p`
  color: #aaa;
  font-size: 0.85rem;
  line-height: 1.4;
  margin: 0 0 10px;
`;

const MemberLoyalty = styled.div`
  color: #aaa;
  font-size: 0.75rem;
  margin-top: 5px;
`;

//   
const MemberItem = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background: ${props => props.selected ? 'rgba(212, 175, 55, 0.2)' : 'rgba(0, 0, 0, 0.2)'};
  border: 1px solid ${props => props.selected ? 'rgba(212, 175, 55, 0.5)' : 'transparent'};
  border-radius: 4px;
  margin-bottom: 10px;
  cursor: pointer;
  transition: all 0.2s ease;
  
  &:hover {
    background: rgba(212, 175, 55, 0.1);
  }
  
  &:last-child {
    margin-bottom: 0;
  }
`;

export default SectTab;
