import React, { useState } from 'react';
import styled from 'styled-components';
import { useGame } from '../../context/GameContext';
import { quests as defaultQuests, questCategories } from '../../data/quests';

const Container = styled.div`
  display: flex;
  gap: 20px;
  height: 100%;
`;

const QuestsList = styled.div`
  flex: 1;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  padding: 20px;
  overflow-y: auto;
`;

const QuestCategories = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
`;

const CategoryButton = styled.button`
  background: ${props => props.active ? 'rgba(212, 175, 55, 0.2)' : 'rgba(0, 0, 0, 0.3)'};
  color: ${props => props.active ? '#d4af37' : '#f0f0f0'};
  border: 1px solid ${props => props.active ? '#d4af37' : 'transparent'};
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
  
  &:hover {
    background: rgba(212, 175, 55, 0.1);
    border-color: #d4af37;
  }
`;

const QuestCard = styled.div`
  background: rgba(30, 30, 30, 0.95);
  border-radius: 6px;
  padding: 15px;
  margin-bottom: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 1px solid transparent;
  
  &:hover {
    border-color: #d4af37;
    transform: translateX(5px);
  }
  
  ${props => props.selected && `
    border-color: #d4af37;
    background: rgba(212, 175, 55, 0.1);
  `}
`;

const QuestTitle = styled.h3`
  color: #d4af37;
  margin: 0 0 10px 0;
  font-size: 1.1rem;
`;

const QuestInfo = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  font-size: 0.9rem;
`;

const QuestDifficulty = styled.span`
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.8rem;
  
  ${props => {
    switch (props.level) {
      case '':
        return 'background: rgba(76, 175, 80, 0.2); color: #4caf50;';
      case '':
        return 'background: rgba(255, 152, 0, 0.2); color: #ff9800;';
      case '':
        return 'background: rgba(244, 67, 54, 0.2); color: #f44336;';
      default:
        return '';
    }
  }}
`;

const QuestDetails = styled.div`
  flex: 1;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  padding: 20px;
  display: flex;
  flex-direction: column;
`;

const DetailHeader = styled.div`
  margin-bottom: 20px;
`;

const DetailTitle = styled.h2`
  color: #d4af37;
  margin: 0 0 10px 0;
`;

const DetailDescription = styled.p`
  color: #aaa;
  line-height: 1.6;
  margin-bottom: 20px;
`;

const Objectives = styled.div`
  margin-bottom: 20px;
`;

const Objective = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  padding: 10px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  
  ${props => props.completed && `
    color: #4caf50;
    text-decoration: line-through;
  `}
`;

const Rewards = styled.div`
  margin-top: auto;
`;

const RewardItem = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px;
  background: rgba(212, 175, 55, 0.1);
  border-radius: 4px;
  margin-bottom: 8px;
`;

const ActionButton = styled.button`
  background: ${props => props.disabled ? 'rgba(0, 0, 0, 0.3)' : 'rgba(212, 175, 55, 0.2)'};
  color: ${props => props.disabled ? '#666' : '#d4af37'};
  border: 1px solid ${props => props.disabled ? 'transparent' : '#d4af37'};
  padding: 12px 24px;
  border-radius: 4px;
  cursor: ${props => props.disabled ? 'not-allowed' : 'pointer'};
  margin-top: 20px;
  transition: all 0.3s ease;
  
  &:hover:not(:disabled) {
    background: rgba(212, 175, 55, 0.3);
  }
`;

function QuestsTab() {
  const { state, actions } = useGame();
  const [selectedCategory, setSelectedCategory] = useState('');
  const [selectedQuest, setSelectedQuest] = useState(null);
  
  //      
  const allQuests = [
    ...defaultQuests.filter(q => !state.player.progress.quests.completed.find(cq => cq.id === q.id)),
    ...state.player.progress.quests.active,
    ...state.player.progress.quests.completed
  ];
  
  const filteredQuests = allQuests.filter(quest => 
    selectedCategory === '' || quest.category === selectedCategory
  );
  
  const handleQuestSelect = (quest) => {
    setSelectedQuest(quest);
  };
  
  const handleAcceptQuest = () => {
    if (selectedQuest) {
      //   
      if (state.player.cultivation.level < selectedQuest.requiredLevel) {
        actions.addNotification({
          message: `   ${selectedQuest.requiredLevel}`,
          type: 'error'
        });
        return;
      }
      
      actions.acceptQuest(selectedQuest.id);
      actions.addNotification({
        message: `   "${selectedQuest.title}"`,
        type: 'success'
      });
    }
  };
  
  const handleCompleteQuest = () => {
    if (selectedQuest) {
      if (!selectedQuest.objectives.every(obj => obj.completed)) {
        actions.addNotification({
          message: '    ',
          type: 'error'
        });
        return;
      }
      
      actions.completeQuest(selectedQuest.id);
    }
  };
  
  const canCompleteQuest = selectedQuest && 
    selectedQuest.status === 'active' && 
    selectedQuest.objectives.every(obj => obj.completed);
  
  return (
    <Container>
      <QuestsList>
        <QuestCategories>
          {questCategories.map(category => (
            <CategoryButton
              key={category.id}
              active={selectedCategory === category.name}
              onClick={() => setSelectedCategory(category.name)}
            >
              {category.name.charAt(0).toUpperCase() + category.name.slice(1)}
            </CategoryButton>
          ))}
        </QuestCategories>
        
        {filteredQuests.map(quest => (
          <QuestCard
            key={quest.id}
            selected={selectedQuest?.id === quest.id}
            onClick={() => handleQuestSelect(quest)}
          >
            <QuestTitle>{quest.title}</QuestTitle>
            <div>{quest.description}</div>
            <QuestInfo>
              <QuestDifficulty level={quest.difficulty}>
                {quest.difficulty}
              </QuestDifficulty>
              <span>
                {quest.status === 'completed' && ''}
                {quest.status === 'active' && ' '}
                {quest.status === 'available' && ` ${quest.requiredLevel}`}
              </span>
            </QuestInfo>
          </QuestCard>
        ))}
      </QuestsList>
      
      {selectedQuest && (
        <QuestDetails>
          <DetailHeader>
            <DetailTitle>{selectedQuest.title}</DetailTitle>
            <QuestDifficulty level={selectedQuest.difficulty}>
              {selectedQuest.difficulty}
            </QuestDifficulty>
          </DetailHeader>
          
          <DetailDescription>{selectedQuest.description}</DetailDescription>
          
          <Objectives>
            <h3>:</h3>
            {selectedQuest.objectives.map(objective => (
              <Objective key={objective.id} completed={objective.completed}>
                {objective.completed ? '' : ''} {objective.text}
              </Objective>
            ))}
          </Objectives>
          
          <Rewards>
            <h3>:</h3>
            {selectedQuest.rewards.map((reward, index) => (
              <RewardItem key={index}>
                <span>{reward.icon}</span>
                {reward.type === 'currency' && (
                  typeof reward.amount === 'object' 
                  ? Object.entries(reward.amount).map(([currency, value]) => {
                      let name = currency;
                      if (currency === 'gold') name = '';
                      if (currency === 'silver') name = '';
                      if (currency === 'copper') name = '';
                      return `${value} ${name}`;
                    }).join(', ')
                  : `${reward.amount} `
                )}
                {reward.type === 'experience' && `${reward.amount} `}
                {reward.type === 'item' && reward.name}
                {reward.type === 'technique' && reward.name}
              </RewardItem>
            ))}
          </Rewards>
          
          {selectedQuest.status === 'available' ? (
            <ActionButton 
              onClick={handleAcceptQuest}
              disabled={state.player.cultivation.level < selectedQuest.requiredLevel}
            >
              {state.player.cultivation.level < selectedQuest.requiredLevel 
                ? `  ${selectedQuest.requiredLevel}` 
                : ' '}
            </ActionButton>
          ) : selectedQuest.status === 'active' && (
            <ActionButton 
              onClick={handleCompleteQuest}
              disabled={!canCompleteQuest}
            >
              {canCompleteQuest ? ' ' : '  '}
            </ActionButton>
          )}
        </QuestDetails>
      )}
    </Container>
  );
}

export default QuestsTab;
