import { damageTypes } from './combat';

const { unifiedDatabase, initializeDatabaseConnection } = require('../services/database-connection-manager');
const { Sequelize } = require('sequelize');
let sequelize;

//   Sequelize  SQL-
let sequelizeInstance = null;

async function getSequelizeInstance() {
  if (!sequelize) {
    const { db } = await initializeDatabaseConnection();
    sequelize = db;
  }
  return sequelize;
}

export const enemyRanks = {
  NORMAL: 'normal',
  ELITE: 'elite',
  BOSS: 'boss'
};

/**
 *       
 * @returns {Promise<Object>}   
 */
export async function getTimeOfDaySpawnModifiers() {
  try {
    const seq = await getSequelizeInstance();
    const modifiers = await unifiedDatabase.query(
      'SELECT time_of_day, category, modifier FROM enemy_time_modifiers',
      { type: seq.QueryTypes.SELECT }
    );
    
    //    
    const timeOfDayModifiers = {};
    
    modifiers.forEach(mod => {
      if (!timeOfDayModifiers[mod.time_of_day]) {
        timeOfDayModifiers[mod.time_of_day] = {};
      }
      
      timeOfDayModifiers[mod.time_of_day][mod.category] = parseFloat(mod.modifier);
    });
    
    return timeOfDayModifiers;
  } catch (error) {
    console.error('     :', error);
    //      
    return {
      '': {
        'spirit_beast': 1.2,
        'bandit': 0.8,
        'undead': 0.5,
        'elemental': 1.1
      },
      '': {
        'spirit_beast': 1.1,
        'bandit': 1.0,
        'undead': 0.3,
        'elemental': 1.0
      },
      '': {
        'spirit_beast': 1.0,
        'bandit': 1.2,
        'undead': 0.2,
        'elemental': 0.9
      },
      '': {
        'spirit_beast': 1.0,
        'bandit': 1.2,
        'undead': 0.1,
        'elemental': 0.8
      },
      '': {
        'spirit_beast': 1.1,
        'bandit': 1.0,
        'undead': 0.7,
        'elemental': 1.0
      },
      '': {
        'spirit_beast': 0.8,
        'bandit': 0.6,
        'undead': 1.5,
        'elemental': 1.2
      }
    };
  }
}

