/**
 *            
 * @param {string} locationId - ID 
 * @param {string} timeOfDay -  
 * @param {string} weatherType -  
 * @returns {Promise<Array>}    
 */
export async function getModifiedEnemySpawns(locationId, timeOfDay, weatherType) {
  try {
    //    
    const allSpawns = await getEnemySpawns();
    const locationSpawns = allSpawns[locationId];
    
    if (!locationSpawns) return [];
    
    //  
    const allEnemies = await getEnemies();
    
    //  
    const timeModifiers = await getTimeOfDaySpawnModifiers();
    const weatherModifiers = await getWeatherSpawnModifiers();
    
    return locationSpawns.filter(spawn => {
      //    
      if (spawn.conditions) {
        //   
        if (spawn.conditions.timeOfDay && !spawn.conditions.timeOfDay.includes(timeOfDay)) {
          return false;
        }
        
        //  
        if (spawn.conditions.weather && !spawn.conditions.weather.includes(weatherType)) {
          return false;
        }
      }
      
      return true;
    }).map(spawn => {
      //     
      const modifiedSpawn = { ...spawn };
      
      //    
      const enemy = allEnemies.find(e => e.id === spawn.id);
      if (enemy && enemy.category) {
        //    
        const timeModifier = timeModifiers[timeOfDay]?.[enemy.category] || 1.0;
        
        //   
        const weatherModifier = weatherModifiers[weatherType]?.[enemy.category] || 1.0;
        
        //  
        const totalModifier = timeModifier * weatherModifier;
        
        //   
        modifiedSpawn.weight = Math.round(spawn.weight * totalModifier);
      }
      
      return modifiedSpawn;
    }).filter(spawn => spawn.weight > 0);
  } catch (error) {
    console.error('     :', error);
    
    //      
    const locationSpawns = enemySpawns[locationId];
    if (!locationSpawns) return [];
    
    return locationSpawns.filter(spawn => {
      //    
      if (spawn.conditions) {
        //   
        if (spawn.conditions.timeOfDay && !spawn.conditions.timeOfDay.includes(timeOfDay)) {
          return false;
        }
        
        //  
        if (spawn.conditions.weather && !spawn.conditions.weather.includes(weatherType)) {
          return false;
        }
      }
      
      return true;
    }).map(spawn => {
      //     
      const modifiedSpawn = { ...spawn };
      
      //    
      const enemy = enemies.find(e => e.id === spawn.id);
      if (enemy && enemy.category) {
        //    
        const timeModifier = timeOfDaySpawnModifiers[timeOfDay]?.[enemy.category] || 1.0;
        
        //   
        const weatherModifier = weatherSpawnModifiers[weatherType]?.[enemy.category] || 1.0;
        
        //  
        const totalModifier = timeModifier * weatherModifier;
        
        //   
        modifiedSpawn.weight = Math.round(spawn.weight * totalModifier);
      }
      
      return modifiedSpawn;
    }).filter(spawn => spawn.weight > 0);
  }
}

