async function initEnemyData() {
  try {
    //    
    timeOfDaySpawnModifiers = await getTimeOfDaySpawnModifiers();
    weatherSpawnModifiers = await getWeatherSpawnModifiers();
    enemies = await getEnemies();
    enemySpawns = await getEnemySpawns();
    
    console.log('      ');
  } catch (error) {
    console.error('     :', error);
  }
}

initEnemyData().catch(error => {
  console.error('     :', error);
});
