import React from 'react';
import styled from 'styled-components';

const EffectsWrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 6px;
`;

const EffectItem = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  
  &:last-child {
    border-bottom: none;
  }
`;

const EffectIcon = styled.div`
  font-size: 1rem;
  color: ${props => {
    switch (props.type) {
      case 'buff': return '#7FFF7F'; //    
      case 'debuff': return '#FF7F7F'; //    
      default: return '#FFFF7F'; //    
    }
  }};
`;

const EffectName = styled.div`
  flex: 1;
  font-size: 0.9rem;
`;

const EffectDuration = styled.div`
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.7);
`;

/**
 *         
 */
function StatusEffects({ effects = [] }) {
  //   ,  
  if (!effects || effects.length === 0) {
    return <div style={{ color: "#aaa", fontSize: "0.9rem" }}>  </div>;
  }
  
  return (
    <EffectsWrapper>
      {effects.map((effect, index) => (
        <EffectItem key={index}>
          <EffectIcon type={effect.type || 'neutral'}>
            {effect.icon || (effect.type === 'buff' ? '' : effect.type === 'debuff' ? '' : '')}
          </EffectIcon>
          <EffectName>{effect.name}</EffectName>
          {effect.duration && (
            <EffectDuration>{effect.duration} </EffectDuration>
          )}
        </EffectItem>
      ))}
    </EffectsWrapper>
  );
}

export default StatusEffects;
