/**
 * API-    
 *        
 */

//        ,    
import AuthService from './auth-adapter';
import CultivationService from './cultivation-adapter';
import InventoryService from './inventory-service';
import TechniqueService from './technique-service';
import CharacterStatsService from './character-stats-service';
import QuestService from './quest-service';
import ResourceService from './resource-service';
import { getInitializedUserModel } from '../models/user';
import CharacterProfileService from './character-profile-service';
import initialRelationships, { getInitialRelationships } from '../utils/initialRelationships';
import { hashPassword } from './web-crypto-hash';

//  URL API (  )
// const API_URL = 'https://api.cultivationgame.com/v1';

//   
const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

/**
 *     API
 */
const apiService = {
  /**
   *  
   * @param {string} username -  
   * @param {string} password - 
   * @returns {Promise<Object>} -    
   */
  async login(username, password) {
    //    
    await delay(1000);
    
    try {
      //  AuthService     
      const user = await AuthService.loginUser(username, password);
      
      //  
      console.log(' :', user.username);
      
      //   (     JWT)
      const token = Math.random().toString(36).substring(2);
      
      //        localStorage  
      localStorage.setItem('authToken', token);
      localStorage.setItem('currentUser', user.username);
      
      return {
        user: {
          id: user.id,
          username: user.username,
          email: user.email,
          cultivationLevel: user.cultivationLevel,
          experience: user.experience
        },
        token
      };
    } catch (error) {
      console.error(' :', error);
      throw error;
    }
  },
  
  /**
   *   
   * @param {string} username -  
   * @param {string} password - 
   * @returns {Promise<Object>} -    
   */
  async register(username, password) {
    //    
    await delay(1000);
    
    try {
      //  email    
      const email = `${username}@example.com`;
      
      //  AuthService      
      const user = await AuthService.registerUser(username, email, password);
      
      //   (     JWT)
      const token = Math.random().toString(36).substring(2);
      
      //        localStorage  
      localStorage.setItem('authToken', token);
      localStorage.setItem('currentUser', username);
      
      console.log(' :', username);
      
      //      
      await CharacterProfileService.updateCharacterProfile(user.id, {
        name: null,  //     
        gender: null,
        region: null,
        background: null,
        description: null,
        social: {
          sect: null,
          reputation: {},
          friends: [],
          relationships: getInitialRelationships() //   
        }
      });
      
      return {
        user: {
          id: user.id,
          username: user.username,
          email: user.email,
          cultivationLevel: user.cultivationLevel,
          experience: user.experience,
          //       
          social: {
            sect: null,
            reputation: {},
            friends: [],
            relationships: getInitialRelationships()
          }
        },
        token
      };
    } catch (error) {
      console.error(' :', error);
      throw error;
    }
  },
  
  /**
   *   
   * @returns {Promise<void>}
   */
  async logout() {
    //    
    await delay(500);
    
    //  
    localStorage.removeItem('authToken');
    localStorage.removeItem('currentUser');
    
    console.log('  ');
  },
  
  /**
   *  
   * @returns {Promise<boolean>}
   */
  async checkAuth() {
    //    
    await delay(500);
    
    //        localStorage
    const token = localStorage.getItem('authToken');
    const username = localStorage.getItem('currentUser');
    
    if (!token || !username) {
      return false;
    }
    
    //      
    try {
      const user = await AuthService.getUserProfile(username);
      return !!user;
    } catch (error) {
      console.warn('  :', error);
      //       ,  
      localStorage.removeItem('authToken');
      localStorage.removeItem('currentUser');
      return false;
    }
  },
  
  /**
   *    
   * @returns {Promise<Object>}
   */
  async getCurrentUser() {
    //    
    await delay(500);
    
    //    
    const username = localStorage.getItem('currentUser');
    
    if (!username) {
      throw new Error('  ');
    }
    
    try {
      //     
      const user = await AuthService.getUserProfile(username);
      
      //   
      const characterProfile = await CharacterProfileService.getCharacterProfile(user.id);
      
      //     
      return {
        id: user.id,
        username: user.username,
        email: user.email,
        cultivationLevel: user.cultivationLevel,
        experience: user.experience,
        //   ,   
        ...(characterProfile ? {
          name: characterProfile.name,
          gender: characterProfile.gender,
          region: characterProfile.region,
          background: characterProfile.background,
          description: characterProfile.description,
          social: characterProfile.social || {
            sect: null,
            reputation: {},
            friends: [],
            relationships: getInitialRelationships()
          }
        } : {})
      };
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  },
  
  /**
   *   
   * @param {Object} gameState -  
   * @returns {Promise<Object>}
   */
  async saveGameState(gameState) {
    try {
      //   
      await delay(300);
      
      //    
      const username = localStorage.getItem('currentUser');
      
      if (!username) {
        throw new Error('  ');
      }
      
      //     
      const user = await AuthService.getUserProfile(username);
      
      if (!user) {
        throw new Error('     ');
      }
      
      //     
      if (gameState && gameState.player) {
        //   
        if (gameState.player.name) {
          await CharacterProfileService.updateCharacterProfile(user.id, {
            name: gameState.player.name,
            gender: gameState.player.gender,
            region: gameState.player.region,
            background: gameState.player.background,
            description: gameState.player.description,
            avatar: gameState.player.avatar,
            level: gameState.player.level,
            experience: gameState.player.experience,
            currency: gameState.player.inventory.currency,
            reputation: gameState.player.social.reputation,
            relationships: gameState.player.social.relationships
          });
        }
        
        //    
        if (gameState.player.cultivation) {
          await CultivationService.updateCultivationProgress(user.id, gameState.player.cultivation);
        }
        
        //   
        if (gameState.player.stats) {
          await CharacterStatsService.updateCharacterStats(user.id, gameState.player.stats);
        }
        
        //  
        if (gameState.player.inventory && gameState.player.inventory.items) {
          //       
          for (const item of gameState.player.inventory.items) {
            await InventoryService.addInventoryItem(user.id, item);
          }
        }
        
        //   
        if (gameState.player.techniques) {
          for (const technique of gameState.player.techniques) {
            try {
              // ,    
              const learnedTechniques = await TechniqueService.getLearnedTechniques(user.id);
              const isLearned = learnedTechniques.some(t => t.id === technique.id);
              
              if (!isLearned) {
                //     ,  
                await TechniqueService.learnTechnique(user.id, technique.id);
              } else {
                //    ,   
                if (technique.level > 1) {
                  for (let i = 1; i < technique.level; i++) {
                    await TechniqueService.upgradeTechnique(user.id, technique.id);
                  }
                }
              }
            } catch (error) {
              //console.error(`    ${technique.id}:`, error);
            }
          }
        }
        
        //   
        if (gameState.player.progress && gameState.player.progress.quests) {
          //  
          for (const quest of gameState.player.progress.quests.active) {
            try {
              await QuestService.acceptQuest(user.id, quest.id);
              await QuestService.updateQuestProgress(user.id, quest.id, quest.progress || {});
            } catch (error) {
              console.error(`     ${quest.id}:`, error);
            }
          }
          
          //  
          for (const quest of gameState.player.progress.quests.completed) {
            try {
              await QuestService.acceptQuest(user.id, quest.id);
              await QuestService.completeQuest(user.id, quest.id);
            } catch (error) {
              console.error(`     ${quest.id}:`, error);
            }
          }
        }
      }
      
      //console.log('       :', username);
      
      return { success: true };
    } catch (error) {
      console.error('    :', error);
      throw error;
    }
  },
  
  /**
   *   
   * @returns {Promise<Object>}
   */
  async loadGameState() {
    try {
      //   
      await delay(300);
      
      //    
      const username = localStorage.getItem('currentUser');
      
      if (!username) {
        throw new Error('  ');
      }
      
      //     
      const user = await AuthService.getUserProfile(username);
      
      if (!user) {
        throw new Error('     ');
      }
      
      //    
      const gameState = {
        player: {
          id: user.id,
          stats: {},
          cultivation: {},
          inventory: {
            items: [],
            equipment: {},
            currency: {
              copper: 0,
              silver: 0,
              gold: 0,
              spiritStones: 0
            }
          },
          techniques: [],
          progress: {
            quests: {
              active: [],
              available: [],
              completed: []
            },
            discoveries: {},
            achievements: {}
          },
          social: {
            sect: null,
            reputation: {},
            friends: [],
            relationships: getInitialRelationships() //   
          }
        },
        world: {
          currentLocation: null,
          time: {
            day: 1,
            hour: 6,
            minute: 0,
            season: 'spring',
            year: 1
          },
          weather: 'clear',
          events: []
        },
        ui: {
          currentScreen: 'world',
          notifications: [],
          dialogs: [],
          settings: {
            soundVolume: 0.5,
            musicVolume: 0.5,
            graphicsQuality: 'medium'
          }
        },
        achievements: {
          completed: [],
          points: 0
        },
        combat: {
          inCombat: false,
          enemy: null,
          log: [],
          turn: 1
        }
      };
      
      //     
      
      //   
      const characterProfile = await CharacterProfileService.getCharacterProfile(user.id);
      
      if (characterProfile) {
        //    ,   
        gameState.player.name = characterProfile.name;
        gameState.player.gender = characterProfile.gender;
        gameState.player.region = characterProfile.region;
        gameState.player.background = characterProfile.background;
        gameState.player.description = characterProfile.description;
        gameState.player.avatar = characterProfile.avatar;
        gameState.player.level = characterProfile.level;
        gameState.player.experience = characterProfile.experience;
        gameState.player.inventory.currency = characterProfile.currency;
        gameState.player.social.reputation = characterProfile.reputation;
        
        //    relationships
        if (characterProfile.relationships) {
          //  relationships ,  
          if (!Array.isArray(characterProfile.relationships)) {
            console.log(' relationships    ');
            //   ,    
            gameState.player.social.relationships = Object.values(characterProfile.relationships);
          } else {
            //   ,  
            gameState.player.social.relationships = characterProfile.relationships;
          }
          
          //  relationships ,   
          if (Array.isArray(gameState.player.social.relationships) && 
              gameState.player.social.relationships.length === 0) {
            console.log('Relationships ,   ');
            gameState.player.social.relationships = getInitialRelationships();
          }
        } else {
          //  relationships  ,   
          console.log('Relationships  ,   ');
          gameState.player.social.relationships = getInitialRelationships();
        }
      }
      
      //    
      const cultivation = await CultivationService.getCultivationProgress(user.id);
      gameState.player.cultivation = cultivation;
      
      //   
      const stats = await CharacterStatsService.getCharacterStats(user.id);
      gameState.player.stats = stats;
      
      //   
      const secondaryStats = CharacterStatsService.calculateSecondaryStats(stats, cultivation);
      gameState.player.secondaryStats = secondaryStats;
      
      //  
      const inventoryItems = await InventoryService.getInventoryItems(user.id);
      gameState.player.inventory.items = inventoryItems;
      
      //   
      const techniques = await TechniqueService.getLearnedTechniques(user.id);
      gameState.player.techniques = techniques;
      
      //  
      const quests = await QuestService.getQuests(user.id);
      gameState.player.progress.quests = quests;
      
      console.log('       :', username);
      
      return gameState;
    } catch (error) {
      console.error('    :', error);
      throw error;
    }
  },
  
  /**
   *    localStorage   
   * @returns {Promise<Object>} -  
   */
  async migrateUsersToDatabase() {
    try {
      //    localStorage
      const users = JSON.parse(localStorage.getItem('users') || '{}');
      const migrationResults = {
        success: true,
        processed: 0,
        migrated: 0,
        skipped: 0,
        errors: []
      };
      
      //     localStorage
      for (const username in users) {
        migrationResults.processed++;
        const localUser = users[username];
        
        try {
          // ,     
          let dbUser = null;
          try {
            const UserModel = await getInitializedUserModel();
            dbUser = await UserModel.findOne({ where: { username } });
          } catch (findError) {
            console.error(`    ${username}:`, findError);
            migrationResults.errors.push({
              username,
              stage: 'search',
              error: findError.message
            });
          }
          
          if (!dbUser) {
            //    
            const email = localUser.email || `${username}@example.com`;
            
            try {
              //      WebCrypto  bcrypt
              const passwordHash = await hashPassword(localUser.password);
              
              //  
              const UserModel = await getInitializedUserModel();
              await UserModel.create({
                username,
                email,
                passwordHash,
                cultivationLevel: 1,
                experience: 0,
                createdAt: new Date(),
                updatedAt: new Date()
              });
              
              console.log(` ${username}    `);
              migrationResults.migrated++;
            } catch (createError) {
              console.error(`    ${username}  :`, createError);
              migrationResults.errors.push({
                username,
                stage: 'create',
                error: createError.message
              });
            }
          } else {
            console.log(` ${username}    `);
            migrationResults.skipped++;
          }
        } catch (error) {
          console.error(`    ${username}:`, error);
          migrationResults.errors.push({
            username,
            stage: 'process',
            error: error.message
          });
        }
      }
      
      return migrationResults;
    } catch (error) {
      console.error('   :', error);
      return {
        success: false,
        error: error.message
      };
    }
  }
};

export default apiService;
