const { Model, DataTypes } = require('../services/database');
const { unifiedDatabase } = require('../services/database-connection-manager');

//   Sequelize 
let sequelizeInstance = null;
async function getSequelize() {
  if (!sequelizeInstance) {
    const result = await unifiedDatabase.getSequelizeInstance();
    sequelizeInstance = result.db;
  }
  return sequelizeInstance;
}

class Technique extends Model {
  static associate(models) {
    //     
    Technique.hasMany(models.TechniqueEffect, {
      foreignKey: 'technique_id', //  snake_case
      as: 'effects'
    });
    Technique.hasMany(models.LearnedTechnique, {
      foreignKey: 'techniqueId', //   camelCase,    LearnedTechnique  userId  techniqueId
      as: 'learnedBy'
    });
  }
}

// :   super   
Technique.init = async function() {
  const sequelize = await getSequelize();
  
  //   init  
  return Model.init.call(this, {
    id: { //  id  SQL
      type: DataTypes.STRING(30),
      primaryKey: true,
      allowNull: false
    },
    name: { //  name  SQL
      type: DataTypes.STRING(100),
      allowNull: false
    },
    description: { //  description  SQL
      type: DataTypes.TEXT
    },
    icon: { //  icon  SQL
      type: DataTypes.STRING(10)
    },
    level: { //  level  SQL
      type: DataTypes.INTEGER,
      defaultValue: 1
    },
    damage: { //  damage  SQL
      type: DataTypes.INTEGER,
      defaultValue: 0
    },
    damageType: { //  damage_type  SQL
      type: DataTypes.STRING(20),
      field: 'damage_type'
    },
    energyCost: { //  energy_cost  SQL
      type: DataTypes.INTEGER,
      defaultValue: 0,
      field: 'energy_cost'
    },
    cooldown: { //  cooldown  SQL
      type: DataTypes.INTEGER,
      defaultValue: 0
    },
    maxLevel: { //  max_level  SQL
      type: DataTypes.INTEGER,
      defaultValue: 5,
      field: 'max_level'
    },
    type: { //  type  SQL
      type: DataTypes.STRING(20)
    },
    requiredLevel: { //  required_level  SQL
      type: DataTypes.INTEGER,
      defaultValue: 1,
      field: 'required_level'
    },
    healing: { //  healing  SQL
      type: DataTypes.INTEGER,
      defaultValue: 0
    }
    //  element 
  }, {
    sequelize,
    modelName: 'Technique',
    tableName: 'techniques', // :    lowercase
    timestamps: false       //  SQL   created_at, updated_at
  });
};

//      
(async () => {
  try {
    await Technique.init();
    console.log('Technique  ');
  } catch (error) {
    console.error('   Technique:', error);
    console.error(error.stack);
  }
})();

module.exports = Technique;
