import React, { useState } from 'react';
import styled from 'styled-components';
import { useGame } from '../../context/GameContext';

const Container = styled.div`
  display: grid;
  grid-template-columns: 1fr 300px;
  gap: 20px;
`;

const InventoryGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
  gap: 10px;
  padding: 20px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 8px;
`;

const ItemSlot = styled.div`
  width: 100%;
  aspect-ratio: 1;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid ${props => props.quality === 'common' ? '#666' :
    props.quality === 'uncommon' ? '#2196f3' :
    props.quality === 'rare' ? '#9c27b0' :
    props.quality === 'epic' ? '#ff9800' : '#d4af37'};
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  cursor: pointer;
  transition: all 0.2s ease;
  
  &:hover {
    transform: scale(1.05);
    box-shadow: 0 0 10px ${props => props.quality === 'common' ? '#666' :
      props.quality === 'uncommon' ? '#2196f3' :
      props.quality === 'rare' ? '#9c27b0' :
      props.quality === 'epic' ? '#ff9800' : '#d4af37'};
  }
`;

const ItemIcon = styled.div`
  width: 60%;
  height: 60%;
  background: ${props => props.type === 'weapon' ? '#f44336' :
    props.type === 'armor' ? '#2196f3' :
    props.type === 'accessory' ? '#9c27b0' :
    props.type === 'consumable' ? '#4caf50' :
    props.type === 'material' ? '#ff9800' :
    props.type === 'book' ? '#795548' : '#666'};
  border-radius: 4px;
`;

const ItemQuantity = styled.div`
  position: absolute;
  bottom: 2px;
  right: 2px;
  background: rgba(0, 0, 0, 0.8);
  padding: 2px 4px;
  border-radius: 2px;
  font-size: 0.8rem;
  color: #fff;
`;

const ItemDetails = styled.div`
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 8px;
  padding: 20px;
`;

const ItemName = styled.h3`
  color: ${props => props.quality === 'common' ? '#fff' :
    props.quality === 'uncommon' ? '#2196f3' :
    props.quality === 'rare' ? '#9c27b0' :
    props.quality === 'epic' ? '#ff9800' : '#d4af37'};
  margin: 0 0 10px 0;
`;

const ItemType = styled.div`
  color: #aaa;
  font-size: 0.9rem;
  margin-bottom: 15px;
`;

const ItemDescription = styled.p`
  color: #f0f0f0;
  font-size: 0.9rem;
  line-height: 1.4;
  margin-bottom: 20px;
`;

const ActionButton = styled.button`
  width: 100%;
  padding: 10px;
  background: rgba(212, 175, 55, 0.2);
  border: 1px solid #d4af37;
  border-radius: 4px;
  color: #d4af37;
  font-size: 0.9rem;
  cursor: pointer;
  margin-bottom: 10px;
  transition: all 0.2s ease;
  
  &:hover {
    background: rgba(212, 175, 55, 0.3);
  }
  
  &:disabled {
    background: #333;
    border-color: #666;
    color: #666;
    cursor: not-allowed;
  }
`;

const CurrencyInfo = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid rgba(212, 175, 55, 0.2);
`;

const CurrencyItem = styled.div`
  display: flex;
  align-items: center;
  gap: 5px;
  color: #aaa;
  
  span {
    color: #d4af37;
  }
`;

function InventoryTab() {
  const { state, actions } = useGame();
  const [selectedItem, setSelectedItem] = useState(null);
  
  const handleItemClick = (item) => {
    setSelectedItem(item);
  };
  
  const handleUseItem = () => {
    if (!selectedItem) return;
    
    console.log(' :', selectedItem);
    
    switch (selectedItem.type) {
      case 'weapon':
      case 'armor':
      case 'accessory':
        //   -  actions.equipItem  GameContextProvider
        console.log(`  ${selectedItem.name} (ID: ${selectedItem.id}, : ${selectedItem.type})`);
        
        // ,     
        //      helpers
        const { ensureItemHasCalculatedBonuses } = require('../../utils/equipmentBonusHelper');
        const itemWithBonuses = ensureItemHasCalculatedBonuses(selectedItem);
        
        //   ,    
        actions.equipItem(itemWithBonuses);
        
        actions.addNotification({
          message: `  ${selectedItem.name}`,
          type: 'success'
        });
        
        //   ,  ,   
        setSelectedItem({
          ...selectedItem,
          equipped: true
        });
        break;
        
      case 'consumable':
        //  
        if (selectedItem.id === 'qi_gathering_pill') {
          //  
          actions.updateCultivation({
            energy: Math.min(
              state.player.cultivation.energy + 50,
              state.player.cultivation.maxEnergy
            )
          });
          
          actions.addNotification({
            message: '    .   .',
            type: 'success'
          });
        }
        
        //     1
        const updatedItems = state.player.inventory.items.map(item => {
          if (item.id === selectedItem.id) {
            return {
              ...item,
              quantity: item.quantity - 1
            };
          }
          return item;
        }).filter(item => item.quantity > 0);
        
        //  
        actions.updatePlayer({
          inventory: {
            ...state.player.inventory,
            items: updatedItems
          }
        });
        
        //   ,  
        if (selectedItem.quantity <= 1) {
          setSelectedItem(null);
        } else {
          //   
          setSelectedItem({
            ...selectedItem,
            quantity: selectedItem.quantity - 1
          });
        }
        break;
        
      case 'book':
        //    
        actions.addNotification({
          message: '     .',
          type: 'success'
        });
        break;
        
      default:
        console.log(`  : ${selectedItem.type}`);
        break;
    }
  };
  
  const canUseItem = selectedItem && (
    selectedItem.type === 'consumable' ||
    selectedItem.type === 'book' ||
    selectedItem.type === 'weapon' ||
    selectedItem.type === 'armor' ||
    selectedItem.type === 'accessory'
  );
  
  return (
    <Container>
      <InventoryGrid>
        {state.player.inventory.items.map((item, index) => (
          <ItemSlot
            key={`${item.id}_${index}`}
            quality={item.quality}
            onClick={() => handleItemClick(item)}
          >
            <ItemIcon type={item.type} />
            {item.quantity > 1 && (
              <ItemQuantity>{item.quantity}</ItemQuantity>
            )}
          </ItemSlot>
        ))}
      </InventoryGrid>
      
      <ItemDetails>
        {selectedItem ? (
          <>
            <ItemName quality={selectedItem.quality}>
              {selectedItem.name}
            </ItemName>
            <ItemType>
              {selectedItem.type === 'weapon' && ''}
              {selectedItem.type === 'armor' && ''}
              {selectedItem.type === 'accessory' && ''}
              {selectedItem.type === 'consumable' && ' '}
              {selectedItem.type === 'material' && ''}
              {selectedItem.type === 'book' && ''}
            </ItemType>
            <ItemDescription>
              {selectedItem.description}
            </ItemDescription>
            
            <ActionButton
              onClick={handleUseItem}
              disabled={!canUseItem}
            >
              {selectedItem.type === 'consumable' ? '' :
               selectedItem.type === 'book' ? '' :
               selectedItem.type === 'material' ? ' ' :
               ''}
            </ActionButton>
            
            <ActionButton
              onClick={() => {
                actions.removeItem(selectedItem.id);
                setSelectedItem(null);
              }}
            >
              
            </ActionButton>
          </>
        ) : (
          <div style={{ textAlign: 'center', color: '#aaa' }}>
                
          </div>
        )}
        
        <CurrencyInfo>
          <CurrencyItem>
            : <span>{state.player.inventory.currency.copper}</span>
          </CurrencyItem>
          <CurrencyItem>
            : <span>{state.player.inventory.currency.silver}</span>
          </CurrencyItem>
          <CurrencyItem>
            : <span>{state.player.inventory.currency.gold}</span>
          </CurrencyItem>
          <CurrencyItem>
            . : <span>{state.player.inventory.currency.spiritStones}</span>
          </CurrencyItem>
        </CurrencyInfo>
      </ItemDetails>
    </Container>
  );
}

export default InventoryTab;
