const { Model, DataTypes } = require('../services/database');
const { unifiedDatabase } = require('../services/database-connection-manager');

//   Sequelize 
let sequelizeInstance = null;
async function getSequelize() {
  if (!sequelizeInstance) {
    const result = await unifiedDatabase.getSequelizeInstance();
    sequelizeInstance = result.db;
  }
  return sequelizeInstance;
}

class QuestProgress extends Model {
  static associate(models) {
    //     
    QuestProgress.belongsTo(models.User, {
      foreignKey: 'userId',
      as: 'user'
    });
    
    QuestProgress.belongsTo(models.Quest, {
      foreignKey: 'questId',
      as: 'quest'
    });
  }
}

// :   super   
QuestProgress.init = async function() {
  const sequelize = await getSequelize();
  
  //   init  
  return Model.init.call(this, {
  //   ID   
  id: {
    type: DataTypes.INTEGER,
    autoIncrement: true,
    primaryKey: true,
    allowNull: false
  },
  userId: {
    type: DataTypes.INTEGER,
    references: {
      model: 'users', //    lowercase
      key: 'id'
    },
    onUpdate: 'CASCADE',
    onDelete: 'CASCADE'
  },
  questId: {
    type: DataTypes.STRING(20), //      Quest
    references: {
      model: 'quests', //    lowercase
      key: 'id'
    },
    onUpdate: 'CASCADE',
    onDelete: 'CASCADE'
  },
  status: { //  status  
    type: DataTypes.ENUM(' ', ' ', '', ''),
    defaultValue: ' ' //  defaultValue
  },
  progress: { //  progress  
    type: DataTypes.INTEGER, //  
    defaultValue: 0 //  defaultValue
  },
  completedObjectives: { //   completedObjectives  
    type: DataTypes.JSONB,
    defaultValue: []
  },
  startedAt: { //  startedAt  
    type: DataTypes.DATE
  },
  completedAt: { //  completedAt  
    type: DataTypes.DATE
  }
  // Sequelize   createdAt  updatedAt
  }, {
    sequelize,
    modelName: 'QuestProgress',
    tableName: 'quest_progress', // :    snake_case
    timestamps: true,           //     
    underscored: true           //  snake_case  createdAt  updatedAt (created_at, updated_at)
  });
};

//      
(async () => {
  try {
    await QuestProgress.init();
    console.log('QuestProgress  ');
  } catch (error) {
    console.error('   QuestProgress:', error);
    console.error(error.stack);
  }
})();

module.exports = QuestProgress;
