const Resource = require('../models/resource');
const { resources, RESOURCE_TYPES, RARITY, getResourceById: getResourceByIdFromData } = require('../data/resources');

// ,     
const isBrowser = typeof window !== 'undefined';

/**
 *     
 */
class ResourceService {
  /**
   *       resources.js
   * @returns {Promise<void>}
   */
  static async initializeResources() {
    //     ,       resources.js
    if (isBrowser) {
      console.log(' -   ');
      return;
    }
    
    try {
      // ,       
      const count = await Resource.count();
      
      //   ,     resources.js
      if (count === 0) {
        console.log('    ...');
        
        //     
        for (const resource of resources) {
          await Resource.create(resource);
        }
        
        console.log(` ${resources.length}    .`);
      } else {
        console.log(`     ${count} .`);
      }
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }
  
  /**
   *   
   * @returns {Promise<Array>} -  
   */
  static async getAllResources() {
    try {
      //       resources.js
      if (isBrowser) {
        return resources;
      } else {
        //     
        const resourceData = await Resource.findAll();
        return resourceData;
      }
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }
  
  /**
   *    ID
   * @param {string} id - ID 
   * @returns {Promise<Object|null>} -   null,    
   */
  static async getResourceById(id) {
    try {
      //       resources.js
      if (isBrowser) {
        return getResourceByIdFromData(id);
      } else {
        //     
        const resource = await Resource.findByPk(id);
        return resource;
      }
    } catch (error) {
      console.error(`     ID ${id}:`, error);
      throw error;
    }
  }
  
  /**
   *    
   * @param {string} type -  
   * @returns {Promise<Array>} -    
   */
  static async getResourcesByType(type) {
    try {
      //       resources.js
      if (isBrowser) {
        return resources.filter(resource => resource.type === type);
      } else {
        //     
        const resourceData = await Resource.findAll({
          where: { type }
        });
        return resourceData;
      }
    } catch (error) {
      console.error(`     ${type}:`, error);
      throw error;
    }
  }
  
  /**
   *    
   * @param {string} rarity -  
   * @returns {Promise<Array>} -    
   */
  static async getResourcesByRarity(rarity) {
    try {
      //       resources.js
      if (isBrowser) {
        return resources.filter(resource => resource.rarity === rarity);
      } else {
        //     
        const resourceData = await Resource.findAll({
          where: { rarity }
        });
        return resourceData;
      }
    } catch (error) {
      console.error(`     ${rarity}:`, error);
      throw error;
    }
  }
  
  /**
   *  ,        
   * @param {string} stage -  
   * @param {number} level -  
   * @returns {Object} -   ID    
   */
  static getBreakthroughResources(stage, level) {
    //        
    const stageLower = stage.toLowerCase();
    
    //         
    switch(stageLower) {
      case ' ':
        const resources = {
          'low_grade_herb': Math.floor(level * 5 + Math.random() * 5),
          'basic_pill': Math.floor(level * 2 + Math.random() * 3)
        };
        
        if (level >= 7) {
          resources['spirit_stone'] = Math.floor((level - 6) * 2);
        }
        
        return resources;
        
      case ' ':
        const qiResources = {
          'medium_grade_herb': Math.floor(level * 4 + Math.random() * 6),
          'qi_pill': Math.floor(level * 3 + Math.random() * 3),
          'spirit_stone': Math.floor(level * 3 + Math.random() * 5)
        };
        
        if (level >= 5) {
          qiResources['spirit_mineral'] = Math.floor((level - 4) * 2);
        }
        
        return qiResources;
        
      case ' ':
        return {
          'rare_herb': Math.floor(level * 3 + Math.random() * 7),
          'high_quality_pill': Math.floor(level * 2 + Math.random() * 4),
          'spirit_mineral': Math.floor(level * 4 + Math.random() * 6),
          'spirit_stone': Math.floor(level * 10 + Math.random() * 10)
        };
        
      case ' ':
        return {
          'exotic_herb': Math.floor(level * 2 + Math.random() * 8),
          'spirit_essence': Math.floor(level * 3 + Math.random() * 5),
          'beast_soul': Math.floor(level * 1 + Math.random() * 3),
          'spirit_mineral': Math.floor(level * 8 + Math.random() * 12),
          'spirit_stone': Math.floor(level * 20 + Math.random() * 20)
        };
        
      default:
        return {
          'low_grade_herb': Math.floor(level * 5 + Math.random() * 5),
          'basic_pill': Math.floor(level * 2 + Math.random() * 3)
        };
    }
  }
  
  /**
   *   
   * @returns {Object} -    
   */
  static getResourceTypes() {
    return RESOURCE_TYPES;
  }
  
  /**
   *   
   * @returns {Object} -    
   */
  static getRarityTypes() {
    return RARITY;
  }
}

module.exports = ResourceService;
