import React, { useState } from 'react';
import styled from 'styled-components';
import { useGame } from '../../context/GameContext';
import { 
  techniques as defaultTechniques, 
  techniqueCategories, 
  elementColors 
} from '../../data/techniques';

const Container = styled.div`
  display: flex;
  gap: 20px;
  height: 100%;
`;

const TechniquesList = styled.div`
  flex: 1;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  padding: 20px;
  overflow-y: auto;
`;

const Categories = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  flex-wrap: wrap;
`;

const CategoryButton = styled.button`
  background: ${props => props.active ? 'rgba(212, 175, 55, 0.2)' : 'rgba(0, 0, 0, 0.3)'};
  color: ${props => props.active ? '#d4af37' : '#f0f0f0'};
  border: 1px solid ${props => props.active ? '#d4af37' : 'transparent'};
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
  
  &:hover {
    background: rgba(212, 175, 55, 0.1);
    border-color: #d4af37;
  }
`;

const TechniqueCard = styled.div`
  background: rgba(30, 30, 30, 0.95);
  border-radius: 6px;
  padding: 15px;
  margin-bottom: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 1px solid transparent;
  position: relative;
  
  ${props => props.element && `
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(135deg, ${props.elementColor}20 0%, transparent 100%);
      border-radius: 6px;
      pointer-events: none;
    }
  `}
  
  &:hover {
    border-color: #d4af37;
    transform: translateX(5px);
  }
  
  ${props => props.selected && `
    border-color: #d4af37;
    background: rgba(212, 175, 55, 0.1);
  `}
  
  ${props => !props.available && `
    opacity: 0.7;
    cursor: not-allowed;
    
    &:hover {
      transform: none;
    }
  `}
`;

const TechniqueHeader = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
`;

const TechniqueIcon = styled.span`
  font-size: 1.5rem;
`;

const TechniqueTitle = styled.h3`
  color: #d4af37;
  margin: 0;
  font-size: 1.1rem;
`;

const TechniqueDescription = styled.p`
  color: #aaa;
  margin: 0 0 10px 0;
  font-size: 0.9rem;
`;

const TechniqueStats = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5px;
  font-size: 0.8rem;
  color: #aaa;
`;

const StatRow = styled.div`
  display: flex;
  justify-content: space-between;
`;

const Details = styled.div`
  flex: 1;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  padding: 20px;
  display: flex;
  flex-direction: column;
`;

const DetailHeader = styled.div`
  margin-bottom: 20px;
`;

const DetailTitle = styled.h2`
  color: #d4af37;
  margin: 0 0 10px 0;
  display: flex;
  align-items: center;
  gap: 10px;
`;

const DetailDescription = styled.p`
  color: #aaa;
  line-height: 1.6;
  margin-bottom: 20px;
`;

const EffectsList = styled.div`
  margin-bottom: 20px;
`;

const EffectRow = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 8px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  margin-bottom: 8px;
  
  &:last-child {
    margin-bottom: 0;
  }
`;

const ActionButton = styled.button`
  background: ${props => props.disabled ? 'rgba(0, 0, 0, 0.3)' : 'rgba(212, 175, 55, 0.2)'};
  color: ${props => props.disabled ? '#666' : '#d4af37'};
  border: 1px solid ${props => props.disabled ? 'transparent' : '#d4af37'};
  padding: 12px 24px;
  border-radius: 4px;
  cursor: ${props => props.disabled ? 'not-allowed' : 'pointer'};
  margin-bottom: 20px;
  transition: all 0.3s ease;
  
  &:hover:not(:disabled) {
    background: rgba(212, 175, 55, 0.3);
  }
`;

//     
const hasSufficientCurrency = (playerCurrency, requiredCurrency) => {
  if (!playerCurrency || !requiredCurrency) return false;
  
  //    
  for (const [type, amount] of Object.entries(requiredCurrency)) {
    if ((playerCurrency[type] || 0) < amount) {
      return false;
    }
  }
  
  return true;
};

function SkillsTab() {
  const { state, actions } = useGame();
  const [selectedCategory, setSelectedCategory] = useState('');
  const [selectedTechnique, setSelectedTechnique] = useState(null);
  
  //      
  const allTechniques = defaultTechniques.map(technique => ({
    ...technique,
    learned: state.player.techniques.find(t => t.id === technique.id),
    available: state.player.cultivation.level >= technique.requiredLevel
  }));
  
  const filteredTechniques = allTechniques.filter(technique =>
    selectedCategory === '' || technique.type === selectedCategory
  );
  
  const handleTechniqueSelect = (technique) => {
    setSelectedTechnique(technique);
  };
  
  const handleLearnTechnique = () => {
    if (!selectedTechnique || !selectedTechnique.available || selectedTechnique.learned) return;
    
    const cost = selectedTechnique.upgradeCost(1);
    
    if (state.player.cultivation.experience < cost.experience || 
        !hasSufficientCurrency(state.player.inventory.currency, cost.currency)) {
      actions.addNotification({
        message: '    ',
        type: 'error'
      });
      return;
    }
    
    //     ,    
    actions.learnTechnique({
      ...selectedTechnique,
      cost: cost
    });
    
    //    ,        
    setSelectedTechnique({
      ...selectedTechnique,
      learned: {
        id: selectedTechnique.id,
        name: selectedTechnique.name,
        level: 1,
        experience: 0,
        learnedAt: new Date().toISOString()
      }
    });
    
    actions.addNotification({
      message: `   "${selectedTechnique.name}"!`,
      type: 'success'
    });
  };
  
  const handleUpgradeTechnique = () => {
    if (!selectedTechnique || !selectedTechnique.learned) return;
    
    const currentLevel = selectedTechnique.learned.level || 1;
    if (currentLevel >= selectedTechnique.maxLevel) {
      actions.addNotification({
        message: '    ',
        type: 'info'
      });
      return;
    }
    
    const cost = selectedTechnique.upgradeCost(currentLevel + 1);
    
    if (state.player.cultivation.experience < cost.experience || 
        !hasSufficientCurrency(state.player.inventory.currency, cost.currency)) {
      actions.addNotification({
        message: '    ',
        type: 'error'
      });
      return;
    }
    
    //       : ID    
    actions.upgradeTechnique({
      techniqueId: selectedTechnique.id,
      cost: cost
    });
    
    //    ,        
    setSelectedTechnique({
      ...selectedTechnique,
      learned: {
        ...selectedTechnique.learned,
        level: currentLevel + 1,
      }
    });
    
    actions.addNotification({
      message: ` "${selectedTechnique.name}"    ${currentLevel + 1}!`,
      type: 'success'
    });
  };
  
  return (
    <Container>
      <TechniquesList>
        <Categories>
          {techniqueCategories.map(category => (
            <CategoryButton
              key={category.id}
              active={selectedCategory === category.name}
              onClick={() => setSelectedCategory(category.name)}
            >
              {category.name.charAt(0).toUpperCase() + category.name.slice(1)}
            </CategoryButton>
          ))}
        </Categories>
        
        {filteredTechniques.map(technique => (
          <TechniqueCard
            key={technique.id}
            selected={selectedTechnique?.id === technique.id}
            available={technique.available}
            element={technique.element}
            elementColor={technique.element ? elementColors[technique.element] : null}
            onClick={() => handleTechniqueSelect(technique)}
          >
            <TechniqueHeader>
              <TechniqueIcon>{technique.icon}</TechniqueIcon>
              <TechniqueTitle>
                {technique.name}
                {technique.learned && ` (. ${technique.learned.level || 1})`}
              </TechniqueTitle>
            </TechniqueHeader>
            
            <TechniqueDescription>
              {technique.description}
            </TechniqueDescription>
            
            <TechniqueStats>
              <StatRow>
                <span>:</span>
                <span>{technique.type}</span>
              </StatRow>
              <StatRow>
                <span>:</span>
                <span>{technique.energyCost}</span>
              </StatRow>
              {technique.cooldown > 0 && (
                <StatRow>
                  <span>:</span>
                  <span>{technique.cooldown}</span>
                </StatRow>
              )}
              <StatRow>
                <span> :</span>
                <span>{technique.requiredLevel}</span>
              </StatRow>
            </TechniqueStats>
          </TechniqueCard>
        ))}
      </TechniquesList>
      
      {selectedTechnique && (
        <Details>
          <DetailHeader>
            <DetailTitle>
              <TechniqueIcon>{selectedTechnique.icon}</TechniqueIcon>
              {selectedTechnique.name}
              {selectedTechnique.learned && ` (. ${selectedTechnique.learned.level || 1})`}
            </DetailTitle>
            <DetailDescription>
              {selectedTechnique.description}
            </DetailDescription>
          </DetailHeader>
          
          {/*        */}
          {selectedTechnique.learned ? (
            <ActionButton
              onClick={handleUpgradeTechnique}
              disabled={
                selectedTechnique.learned.level >= selectedTechnique.maxLevel ||
                state.player.cultivation.experience < selectedTechnique.upgradeCost(selectedTechnique.learned.level + 1).experience ||
                !hasSufficientCurrency(
                  state.player.inventory.currency,
                  selectedTechnique.upgradeCost(selectedTechnique.learned.level + 1).currency
                )
              }
            >
              {selectedTechnique.learned.level >= selectedTechnique.maxLevel
                ? ' '
                : (() => {
                    const cost = selectedTechnique.upgradeCost(selectedTechnique.learned.level + 1);
                    const currencyText = Object.entries(cost.currency)
                      .map(([type, amount]) => {
                        const currencyName = type === 'gold' ? '' : type === 'silver' ? '' : '';
                        return `${amount} ${currencyName}`;
                      })
                      .join(', ');
                    return ` (${cost.experience} , ${currencyText})`;
                  })()
              }
            </ActionButton>
          ) : (
            <ActionButton
              onClick={handleLearnTechnique}
              disabled={
                !selectedTechnique.available ||
                state.player.cultivation.experience < selectedTechnique.upgradeCost(1).experience ||
                !hasSufficientCurrency(
                  state.player.inventory.currency,
                  selectedTechnique.upgradeCost(1).currency
                )
              }
            >
              {!selectedTechnique.available
                ? ` ${selectedTechnique.requiredLevel} `
                : (() => {
                    const cost = selectedTechnique.upgradeCost(1);
                    const currencyText = Object.entries(cost.currency)
                      .map(([type, amount]) => {
                        const currencyName = type === 'gold' ? '' : type === 'silver' ? '' : '';
                        return `${amount} ${currencyName}`;
                      })
                      .join(', ');
                    return ` (${cost.experience} , ${currencyText})`;
                  })()
              }
            </ActionButton>
          )}
          
          <EffectsList>
            <h3>:</h3>
            {selectedTechnique.effects && selectedTechnique.effects.length > 0 ? (
              selectedTechnique.effects.map((effect, index) => (
                <EffectRow key={index}>
                  <span>{effect.name}:</span>
                  <span>
                    {effect.duration ? `${effect.duration} .` : ''}
                    {effect.damage ? ` ${effect.damage} ` : ''}
                    {effect.healing ? ` ${effect.healing} ` : ''}
                    {effect.stats ? Object.entries(effect.stats).map(([stat, value]) => 
                      ` ${stat}: ${value > 0 ? '+' : ''}${value}`).join(', ') : ''}
                  </span>
                </EffectRow>
              ))
            ) : (
              <EffectRow>
                <span> </span>
                <span>-</span>
              </EffectRow>
            )}
          </EffectsList>
        </Details>
      )}
    </Container>
  );
}

export default SkillsTab;
