//      
let equipmentItems = [];

/**
 *         
 * @param {Object} item -   
 * @param {Array} effects -  
 * @param {Array} requirements -  
 * @param {Array} specialEffects -   
 * @returns {Object} -     
 */
function convertEquipmentItemFromDB(item, effects = [], requirements = [], specialEffects = []) {
  const result = {
    id: item.id,
    name: item.name,
    description: item.description || '',
    type: item.type,
    subType: item.sub_type,
    rarity: item.rarity,
    level: item.level || 1,
    value: item.value || 0,
    icon: item.icon
  };
  
  //  
  if (effects.length > 0) {
    result.effects = {};
    
    effects.forEach(effect => {
      const statName = effect.stat_name;
      const value = effect.value;
      const isPercentage = effect.is_percentage;
      
      result.effects[statName] = {
        value: value,
        isPercentage: isPercentage
      };
    });
  }
  
  //  
  if (requirements.length > 0) {
    result.requirements = {};
    
    requirements.forEach(req => {
      const reqType = req.requirement_type;
      const value = req.value;
      
      result.requirements[reqType] = value;
    });
  }
  
  //   
  if (specialEffects.length > 0) {
    result.specialEffects = [];
    
    specialEffects.forEach(effect => {
      result.specialEffects.push({
        name: effect.name,
        description: effect.description,
        triggerCondition: effect.trigger_condition,
        effects: JSON.parse(effect.effects || '{}')
      });
    });
  }
  
  //    ( )
  if (item.calculated_bonuses) {
    try {
      result.calculatedBonuses = JSON.parse(item.calculated_bonuses);
    } catch (e) {
      console.error('    :', e);
    }
  }
  
  return result;
}

/**
 *        
 * @returns {Promise<Array>} -   
 */
async function loadEquipmentItems() {
  try {
    //   Sequelize
    const seq = await getSequelizeInstance();
    
    //      
    const itemsData = await unifiedDatabase.query(
      'SELECT * FROM equipment_items',
      { type: seq.QueryTypes.SELECT }
    );
    
    if (itemsData.length === 0) {
      return [];
    }
    
    //  ID 
    const itemIds = itemsData.map(item => item.id);
    
    //    
    const effectsQuery = `
      SELECT * FROM equipment_item_effects 
      WHERE item_id IN (${itemIds.map(id => `'${id}'`).join(',')})
    `;
    const effectsData = await unifiedDatabase.query(
      effectsQuery,
      { type: seq.QueryTypes.SELECT }
    );
    
    //    
    const requirementsQuery = `
      SELECT * FROM equipment_item_requirements 
      WHERE item_id IN (${itemIds.map(id => `'${id}'`).join(',')})
    `;
    const requirementsData = await unifiedDatabase.query(
      requirementsQuery,
      { type: seq.QueryTypes.SELECT }
    );
    
    //     
    const specialEffectsQuery = `
      SELECT * FROM equipment_item_special_effects 
      WHERE item_id IN (${itemIds.map(id => `'${id}'`).join(',')})
    `;
    const specialEffectsData = await unifiedDatabase.query(
      specialEffectsQuery,
      { type: seq.QueryTypes.SELECT }
    );
    
    //  ,      ID 
    const effectsByItemId = {};
    effectsData.forEach(effect => {
      if (!effectsByItemId[effect.item_id]) {
        effectsByItemId[effect.item_id] = [];
      }
      effectsByItemId[effect.item_id].push(effect);
    });
    
    const requirementsByItemId = {};
    requirementsData.forEach(req => {
      if (!requirementsByItemId[req.item_id]) {
        requirementsByItemId[req.item_id] = [];
      }
      requirementsByItemId[req.item_id].push(req);
    });
    
    const specialEffectsByItemId = {};
    specialEffectsData.forEach(effect => {
      if (!specialEffectsByItemId[effect.item_id]) {
        specialEffectsByItemId[effect.item_id] = [];
      }
      specialEffectsByItemId[effect.item_id].push(effect);
    });
    
    //     
    const formattedItems = itemsData.map(item => 
      convertEquipmentItemFromDB(
        item,
        effectsByItemId[item.id] || [],
        requirementsByItemId[item.id] || [],
        specialEffectsByItemId[item.id] || []
      )
    );
    
    //     
    equipmentItems = formattedItems;
    
    return formattedItems;
  } catch (error) {
    console.error('       :', error);
    //      
    return equipmentItems;
  }
}

/**
 *      ID
 * @param {string} id - ID 
 * @returns {Promise<Object|null>} -   null,   
 */
async function getEquipmentItemByIdAsync(id) {
  try {
    //   Sequelize
    const seq = await getSequelizeInstance();
    
    //     
    const itemData = await unifiedDatabase.query(
      'SELECT * FROM equipment_items WHERE id = :id',
      { 
        replacements: { id },
        type: seq.QueryTypes.SELECT 
      }
    );
    
    if (itemData.length === 0) {
      return null;
    }
    
    //  ,      
    const [effectsData, requirementsData, specialEffectsData] = await Promise.all([
      unifiedDatabase.query(
        'SELECT * FROM equipment_item_effects WHERE item_id = :id',
        { 
          replacements: { id },
          type: seq.QueryTypes.SELECT 
        }
      ),
      unifiedDatabase.query(
        'SELECT * FROM equipment_item_requirements WHERE item_id = :id',
        { 
          replacements: { id },
          type: seq.QueryTypes.SELECT 
        }
      ),
      unifiedDatabase.query(
        'SELECT * FROM equipment_item_special_effects WHERE item_id = :id',
        { 
          replacements: { id },
          type: seq.QueryTypes.SELECT 
        }
      )
    ]);
    
    //    
    return convertEquipmentItemFromDB(
      itemData[0], 
      effectsData, 
      requirementsData, 
      specialEffectsData
    );
  } catch (error) {
    console.error(`      ID ${id}:`, error);
    //      
    return getEquipmentItemById(id);
  }
}

/**
 *        ID (  )
 * @param {string} id - ID 
 * @returns {Object|null} -   null,   
 */
function getEquipmentItemById(id) {
  return equipmentItems.find(item => item.id === id) || null;
}

