/**
 * API-    
 *  HTTP         
 */

//  URL API
const API_URL = '/api';

class AuthServiceAPI {
  /**
   *   
   * @param {string} username -  
   * @param {string} email - Email 
   * @param {string} password -  
   * @returns {Promise<Object>} -   
   */
  static async registerUser(username, email, password) {
    try {
      const response = await fetch(`${API_URL}/auth/register`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          username,
          email,
          password,
        }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || '  ');
      }

      const user = await response.json();
      return user;
    } catch (error) {
      console.error(' :', error);
      throw error;
    }
  }

  /**
   *  
   * @param {string} usernameOrEmail -    email
   * @param {string} password -  
   * @returns {Promise<Object>} -   
   */
  static async loginUser(usernameOrEmail, password) {
    try {
      const response = await fetch(`${API_URL}/auth/login`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          usernameOrEmail,
          password,
        }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || '  ');
      }

      const user = await response.json();
      return user;
    } catch (error) {
      console.error(' :', error);
      throw error;
    }
  }

  /**
   *   
   * @param {number} userId - ID 
   * @param {number} experience -  
   * @param {number} cultivationLevel -  
   * @returns {Promise<Object>} -   
   */
  static async updateUserProgress(userId, experience, cultivationLevel) {
    try {
      const response = await fetch(`${API_URL}/users/${userId}/progress`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('authToken')}`,
        },
        body: JSON.stringify({
          experience,
          cultivationLevel,
        }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || '   ');
      }

      const user = await response.json();
      return user;
    } catch (error) {
      console.error('  :', error);
      throw error;
    }
  }

  /**
   *   
   * @param {string|number} userIdOrUsername - ID    
   * @returns {Promise<Object>} -  
   */
  static async getUserProfile(userIdOrUsername) {
    try {
      // ,  ID   
      const isId = typeof userIdOrUsername === 'number' || !isNaN(parseInt(userIdOrUsername));
      const endpoint = isId 
        ? `${API_URL}/users/${userIdOrUsername}`
        : `${API_URL}/users/by-username/${userIdOrUsername}`;
      
      const response = await fetch(endpoint, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('authToken')}`,
        },
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || '   ');
      }

      const user = await response.json();
      return user;
    } catch (error) {
      console.error('  :', error);
      throw error;
    }
  }
}

export default AuthServiceAPI;