const { Model, DataTypes } = require('../services/database');
const { unifiedDatabase } = require('../services/database-connection-manager');

//   Sequelize 
let sequelizeInstance = null;
async function getSequelize() {
  if (!sequelizeInstance) {
    const result = await unifiedDatabase.getSequelizeInstance();
    sequelizeInstance = result.db;
  }
  return sequelizeInstance;
}

class Quest extends Model {
  static associate(models) {
    //    
    Quest.hasMany(models.QuestObjective, {
      foreignKey: 'quest_id',
      as: 'objectives'
    });
    //     
    Quest.hasMany(models.QuestReward, {
      foreignKey: 'quest_id',
      as: 'rewards'
    });
    //     (   QuestCategory)
    // Quest.belongsTo(models.QuestCategory, { foreignKey: 'category', as: 'questCategory' });
  }
}

// :   super   
Quest.init = async function() {
  const sequelize = await getSequelize();
  
  //   init  
  return Model.init.call(this, {
    id: { //  id  SQL
      type: DataTypes.STRING(20),
      primaryKey: true
    },
    title: { //  title  SQL
      type: DataTypes.STRING(100),
      allowNull: false
    },
    category: { //  category  SQL
      type: DataTypes.STRING(50),
      // references: { model: 'quest_categories', key: 'id' } // ,    QuestCategory
    },
    difficulty: { //  difficulty  SQL
      type: DataTypes.STRING(20)
    },
    description: { //  description  SQL
      type: DataTypes.TEXT
    },
    status: { //  status  SQL
      type: DataTypes.STRING(20)
    },
    requiredLevel: { //  required_level  SQL
      type: DataTypes.INTEGER,
      defaultValue: 0,
      field: 'required_level'
    },
    repeatable: { //  repeatable  SQL
      type: DataTypes.BOOLEAN,
      defaultValue: false
    }
    //  rewards ,      
  }, {
    sequelize,
    modelName: 'Quest',
    tableName: 'quests', // :    lowercase
    timestamps: false   //  SQL   created_at, updated_at
  });
};

//      
(async () => {
  try {
    await Quest.init();
    console.log('Quest  ');
  } catch (error) {
    console.error('   Quest:', error);
    console.error(error.stack);
  }
})();

module.exports = Quest;
