const { Model, DataTypes } = require('../services/database');
const { unifiedDatabase } = require('../services/database-connection-manager');

//   Sequelize 
let sequelizeInstance = null;
async function getSequelize() {
  if (!sequelizeInstance) {
    const result = await unifiedDatabase.getSequelizeInstance();
    sequelizeInstance = result.db;
  }
  return sequelizeInstance;
}

class CultivationProgress extends Model {
  static associate(models) {
    //     
    CultivationProgress.belongsTo(models.User, {
      foreignKey: 'userId',
      as: 'user'
    });
  }
}

// :   super   
CultivationProgress.init = async function() {
  const sequelize = await getSequelize();

  //   init  
  return Model.init.call(this, {
  userId: {
    type: DataTypes.INTEGER,
    references: {
      model: 'Users',
      key: 'id'
    },
    onUpdate: 'CASCADE',
    onDelete: 'CASCADE',
    unique: true
  },
  stage: {
    type: DataTypes.ENUM(' ', ' ', ' ', ' '),
    defaultValue: ' '
  },
  level: {
    type: DataTypes.INTEGER,
    defaultValue: 1
  },
  experience: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  experienceToNextLevel: {
    type: DataTypes.INTEGER,
    defaultValue: 100
  },
  breakthroughProgress: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  dailyCultivationLimit: {
    type: DataTypes.INTEGER,
    defaultValue: 1000
  },
  dailyCultivationUsed: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  lastCultivationReset: {
    type: DataTypes.DATE,
    defaultValue: DataTypes.NOW
  },
  energy: {
    type: DataTypes.INTEGER,
    defaultValue: 100
  },
  maxEnergy: {
    type: DataTypes.INTEGER,
    defaultValue: 100
  },
  //      
  tribulationCompleted: {
    type: DataTypes.BOOLEAN,
    defaultValue: false
  },
  insightPoints: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  bottleneckProgress: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  requiredBottleneckProgress: {
    type: DataTypes.INTEGER,
    defaultValue: 100
  },
  lastInsightTime: {
    type: DataTypes.DATE,
    defaultValue: DataTypes.NOW
  },
  cultivationEfficiency: {
    type: DataTypes.FLOAT,
    defaultValue: 1.0
  },
  requiredResources: {
    type: DataTypes.JSON,
    defaultValue: {}
  }
  }, {
    sequelize,
    modelName: 'CultivationProgress',
    tableName: 'CultivationProgress'
  });
};

//      
(async () => {
  try {
    await CultivationProgress.init();
    console.log('CultivationProgress  ');
  } catch (error) {
    console.error('   CultivationProgress:', error);
    console.error(error.stack);
  }
})();

module.exports = CultivationProgress;
