import React from 'react';
import styles from './ActivityList.module.css';

/**
 *       
 * @param {Object[]} activities -  
 * @param {Object} selectedActivity -  
 * @param {Function} onSelectActivity -   
 */
const ActivityList = ({ activities = [], selectedActivity, onSelectActivity }) => {
  // Ensure activities is always an array
  const safeActivities = Array.isArray(activities) ? activities : [];
  //     
  const getActivityTypeIcon = (type) => {
    switch (type) {
      case 'raid':
        return ''; // 
      case 'hunt':
        return ''; // 
      case 'expedition':
        return ''; // 
      case 'tournament':
        return ''; // 
      case 'caravan':
        return ''; // 
      case 'tribulation':
        return ''; //  
      case 'craft':
        return ''; //  
      default:
        return ''; //  
    }
  };
  
  //     
  const getActivityTypeName = (type) => {
    switch (type) {
      case 'raid':
        return '';
      case 'hunt':
        return '';
      case 'expedition':
        return '';
      case 'tournament':
        return '';
      case 'caravan':
        return '';
      case 'tribulation':
        return ' ';
      case 'craft':
        return ' ';
      default:
        return '';
    }
  };
  
  //     
  const getDifficultyText = (difficulty) => {
    switch (difficulty) {
      case 'easy':
        return '';
      case 'medium':
        return '';
      case 'hard':
        return '';
      case 'extreme':
        return '';
      case 'legendary':
        return '';
      default:
        return ' ';
    }
  };
  
  //     
  const getDifficultyClass = (difficulty) => {
    switch (difficulty) {
      case 'easy':
        return styles.difficultyEasy;
      case 'medium':
        return styles.difficultyMedium;
      case 'hard':
        return styles.difficultyHard;
      case 'extreme':
        return styles.difficultyExtreme;
      case 'legendary':
        return styles.difficultyLegendary;
      default:
        return '';
    }
  };
  
  return (
    <div className={styles.activityList}>
      <h3 className={styles.title}> </h3>
      
      {safeActivities.length === 0 ? (
        <div className={styles.emptyMessage}>
               .
        </div>
      ) : (
        <ul className={styles.list}>
          {safeActivities.map(activity => (
            <li 
              key={activity.id} 
              className={`${styles.activityItem} ${selectedActivity?.id === activity.id ? styles.selected : ''}`}
              onClick={() => onSelectActivity(activity)}
            >
              <div className={styles.header}>
                <div className={styles.nameContainer}>
                  <span className={styles.typeIcon}>
                    {getActivityTypeIcon(activity.type)}
                  </span>
                  <h4 className={styles.name}>{activity.name}</h4>
                </div>
                <span className={`${styles.difficulty} ${getDifficultyClass(activity.difficulty)}`}>
                  {getDifficultyText(activity.difficulty)}
                </span>
              </div>
              
              <div className={styles.typeName}>
                {getActivityTypeName(activity.type)}
              </div>
              
              <div className={styles.infoRow}>
                <span className={styles.label}>:</span>
                <span className={styles.value}>
                  {activity.minParticipants || 1}-{activity.maxParticipants || 5}
                </span>
              </div>
              
              <div className={styles.infoRow}>
                <span className={styles.label}>:</span>
                <span className={styles.value}>
                  {activity.minCultivationLevel || 1}+ (. {activity.recommendedCultivationLevel || activity.minCultivationLevel || 1}+)
                </span>
              </div>
              
              <div className={styles.infoRow}>
                <span className={styles.label}>:</span>
                <span className={styles.value}>
                  {activity.duration || 30} .
                </span>
              </div>
              
              {activity.location && (
                <div className={styles.location}>
                  <span className={styles.locationIcon}></span>
                  <span className={styles.locationName}>{activity.location}</span>
                </div>
              )}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default ActivityList;
