import React, { useReducer, useEffect } from 'react';
import styled from 'styled-components';
import { useGame } from '../../context/GameContext';
import useTimeWeather from '../../hooks/useTimeWeather';

//       
const WidgetContainer = styled.div`
  background-color: rgba(20, 20, 20, 0.8);
  border: 1px solid #444;
  border-radius: 6px;
  padding: 10px 15px;
  color: #fff;
  font-family: 'Arial', sans-serif;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
`;

const TimeSection = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
`;

const TimeIcon = styled.div`
  font-size: 1.2rem;
  color: ${props => props.isDayTime ? '#FFD700' : '#8A97CD'};
`;

const TimeText = styled.div`
  font-size: 1.1rem;
  font-weight: bold;
  color: #eee;
`;

const WeatherSection = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
`;

const WeatherIcon = styled.div`
  font-size: 1.2rem;
  color: ${props => {
    switch (props.type) {
      case 'clear': return '#FFD700'; // 
      case 'cloudy': return '#B0C4DE'; // -
      case 'rain': return '#4682B4'; // 
      case 'thunderstorm': return '#483D8B'; // -
      case 'fog': return '#D3D3D3'; // -
      case 'snow': return '#F0F8FF'; //    
      default: return '#FFD700';
    }
  }};
`;

const WeatherText = styled.div`
  font-size: 1rem;
`;

const SeasonIndicator = styled.div`
  height: 8px;
  width: 100%;
  background: ${props => {
    switch (props.season) {
      case 'spring': return 'linear-gradient(to right, #C5E8B7, #ABE098, #83D475)'; // 
      case 'summer': return 'linear-gradient(to right, #FFD700, #FFA500, #FF8C00)'; // 
      case 'autumn': return 'linear-gradient(to right, #D2691E, #CD853F, #B8860B)'; // 
      case 'winter': return 'linear-gradient(to right, #B0E0E6, #87CEEB, #1E90FF)'; // 
      default: return 'linear-gradient(to right, #C5E8B7, #ABE098, #83D475)';
    }
  }};
  border-radius: 4px;
  margin-top: 8px;
`;

//      
const weatherNames = {
  clear: '',
  cloudy: '',
  rain: '',
  thunderstorm: '',
  fog: '',
  snow: ''
};

//      
const daytimeNames = {
  dawn: '',
  morning: '',
  noon: '',
  afternoon: '',
  evening: '',
  night: '',
  deepNight: ' '
};

//    
const daytimeIcons = {
  dawn: '',
  morning: '',
  noon: '',
  afternoon: '',
  evening: '',
  night: '',
  deepNight: ''
};

//   
const weatherIcons = {
  clear: '',
  cloudy: '',
  rain: '',
  thunderstorm: '',
  fog: '',
  snow: ''
};

//   
const seasonNames = {
  spring: '',
  summer: '',
  autumn: '',
  winter: ''
};

/**
 *     ,   
 */
function WorldTimeWidget() {
  //   useTimeWeather    
  const timeWeather = useTimeWeather();
  
  //  useReducer    
  const [_, forceUpdate] = useReducer(x => x + 1, 0);
  
  //     
  const { 
    hour, 
    minute, 
    formattedTime,
    isDayTime,
    daytimePeriod,
    currentWeather, 
    dayCount, 
    season,
    forceUpdateCounter
  } = timeWeather;
  
  //     ,  null
  if (hour === undefined && minute === undefined) return null;
  
  //        
  useEffect(() => {
    forceUpdate();
    console.log(' WorldTimeWidget:  ', {
      forceUpdateCounter, 
      time: formattedTime,
      day: dayCount
    });
  }, [forceUpdateCounter, formattedTime, dayCount]);
  
  return (
    <WidgetContainer>
      <TimeSection>
        <TimeIcon isDayTime={isDayTime}>
          {daytimeIcons[daytimePeriod]}
        </TimeIcon>
        <TimeText>
          {formattedTime} ({daytimeNames[daytimePeriod]})
        </TimeText>
      </TimeSection>
      
      <WeatherSection>
        <WeatherIcon type={currentWeather}>
          {weatherIcons[currentWeather]}
        </WeatherIcon>
        <WeatherText>
          {weatherNames[currentWeather]}
        </WeatherText>
      </WeatherSection>
      
      <div>
        <div>{seasonNames[season]} ( {dayCount})</div>
        <SeasonIndicator season={season} />
      </div>
    </WidgetContainer>
  );
}

export default WorldTimeWidget;
