import { Model, DataTypes } from '../services/database';
import { unifiedDatabase } from '../services/database-connection-manager';

class User extends Model {
  static associate(models) {
    //     
    User.hasOne(models.CultivationProgress, {
      foreignKey: 'userId',
      as: 'cultivationProgress'
    });
  }
}

//        
let initializedUserModel = null;
let initializationPromise = null;

/**
 *      User
 * ,      
 * @returns {Promise<Model>} ,    
 */
export async function getInitializedUserModel() {
  //    ,  
  if (initializedUserModel) {
    return initializedUserModel;
  }
  
  //    ,   
  if (initializationPromise) {
    return initializationPromise;
  }
  
  //  
  initializationPromise = initializeUserModel();
  return initializationPromise;
}

/**
 *     User
 * @private
 * @returns {Promise<Model>} ,    
 */
async function initializeUserModel() {
  try {
    //   Sequelize
    const result = await unifiedDatabase.getSequelizeInstance();
    const sequelize = result.db;
    
    //  
    Model.init.call(User, {
      //   ID   SQL
      id: {
        type: DataTypes.INTEGER,
        autoIncrement: true,
        primaryKey: true
      },
      username: {
        type: DataTypes.STRING,
        allowNull: false,
        unique: true
      },
      email: {
        type: DataTypes.STRING,
        allowNull: false,
        unique: true
      },
      passwordHash: {
        type: DataTypes.STRING,
        allowNull: false,
        field: 'password_hash' //    password_hash   
      },
      cultivationLevel: { //  cultivation_level  SQL
        type: DataTypes.INTEGER,
        defaultValue: 1
      },
      experience: {
        type: DataTypes.INTEGER,
        defaultValue: 0
      },
      lastLogin: { //  last_login  SQL
        type: DataTypes.DATE
      }
      // Sequelize   createdAt  updatedAt
    }, {
      sequelize,
      modelName: 'User',
      tableName: 'users', // :    lowercase
      timestamps: true,   //     
      underscored: true   //  snake_case  createdAt  updatedAt (created_at, updated_at)
    });
    
    console.log('User  ');
    initializedUserModel = User;
    return User;
  } catch (error) {
    console.error('   User:', error);
    console.error(error.stack);
    throw error;
  }
}

//     
getInitializedUserModel().catch(error => {
  console.error('     User:', error);
});

//     User  
//       getInitializedUserModel()
export default User;
