const { Model, DataTypes } = require('../services/database');
const { unifiedDatabase } = require('../services/database-connection-manager');

//   Sequelize 
let sequelizeInstance = null;
async function getSequelize() {
  if (!sequelizeInstance) {
    const result = await unifiedDatabase.getSequelizeInstance();
    sequelizeInstance = result.db;
  }
  return sequelizeInstance;
}

class LearnedTechnique extends Model {
  static associate(models) {
    //     
    LearnedTechnique.belongsTo(models.User, {
      foreignKey: 'userId',
      as: 'user'
    });
    
    LearnedTechnique.belongsTo(models.Technique, {
      foreignKey: 'techniqueId',
      as: 'technique'
    });
  }
}

// :   super   
LearnedTechnique.init = async function() {
  const sequelize = await getSequelize();
  
  //   init  
  return Model.init.call(this, {
  //   ID   
  id: {
    type: DataTypes.INTEGER,
    autoIncrement: true,
    primaryKey: true,
    allowNull: false
  },
  userId: {
    type: DataTypes.INTEGER,
    references: {
      model: 'users', //    lowercase
      key: 'id'
    },
    onUpdate: 'CASCADE',
    onDelete: 'CASCADE'
  },
  techniqueId: {
    type: DataTypes.INTEGER,
    references: {
      model: 'techniques', //    lowercase
      key: 'id'
    },
    onUpdate: 'CASCADE',
    onDelete: 'CASCADE'
  },
  //  level ,  masteryLevel
  masteryLevel: { //  masteryLevel  
    type: DataTypes.INTEGER,
    defaultValue: 1 //  defaultValue
  },
  experience: { //  experience  
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  isEquipped: { //   isEquipped  
    type: DataTypes.BOOLEAN,
    defaultValue: false
  },
  learnedAt: { //   learnedAt  
    type: DataTypes.DATE,
    defaultValue: DataTypes.NOW
  },
  lastUsed: { //  lastUsed  
    type: DataTypes.DATE
  }
  // Sequelize   createdAt  updatedAt
  }, {
    sequelize,
    modelName: 'LearnedTechnique',
    tableName: 'learned_techniques', // :    snake_case
    timestamps: true,               //     
    underscored: true               //  snake_case  createdAt  updatedAt (created_at, updated_at)
  });
};

//      
(async () => {
  try {
    await LearnedTechnique.init();
    console.log('LearnedTechnique  ');
  } catch (error) {
    console.error('   LearnedTechnique:', error);
    console.error(error.stack);
  }
})();

module.exports = LearnedTechnique;
