import { Model, DataTypes } from '../services/database';
import { unifiedDatabase } from '../services/database-connection-manager';

//   Sequelize 
let sequelizeInstance = null;
async function getSequelize() {
  if (!sequelizeInstance) {
    const result = await unifiedDatabase.getSequelizeInstance();
    sequelizeInstance = result.db;
  }
  return sequelizeInstance;
}

class CharacterProfile extends Model {
  static associate(models) {
    //     
    CharacterProfile.belongsTo(models.User, {
      foreignKey: 'userId',
      as: 'user'
    });
  }
}

// :   super   
CharacterProfile.init = async function() {
  const sequelize = await getSequelize();
  
  //   init  
  return Model.init.call(this, {
  userId: {
    type: DataTypes.INTEGER,
    references: {
      model: 'Users',
      key: 'id'
    },
    onUpdate: 'CASCADE',
    onDelete: 'CASCADE',
    unique: true
  },
  name: {
    type: DataTypes.STRING,
    allowNull: false
  },
  gender: {
    type: DataTypes.ENUM('male', 'female'),
    defaultValue: 'male'
  },
  region: {
    type: DataTypes.STRING,
    defaultValue: 'central'
  },
  background: {
    type: DataTypes.STRING,
    defaultValue: 'commoner'
  },
  description: {
    type: DataTypes.TEXT
  },
  avatar: {
    type: DataTypes.STRING
  },
  level: {
    type: DataTypes.INTEGER,
    defaultValue: 1
  },
  experience: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  gold: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  silver: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  copper: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  spiritStones: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  reputation: {
    type: DataTypes.JSON,
    defaultValue: {}
  },
  relationships: {
    type: DataTypes.JSON,
    defaultValue: {}
  }
  }, {
    sequelize,
    modelName: 'CharacterProfile',
    tableName: 'CharacterProfiles'
    });
};

//      
(async () => {
  try {
    await CharacterProfile.init();
    console.log('CharacterProfile  ');
  } catch (error) {
    console.error('   CharacterProfile:', error);
    console.error(error.stack);
  }
})();

export default CharacterProfile;
