export const combatStats = {
  calculateDamage: (attacker, defender, base, type, penetration = 0) => {
    const defense = type === damageTypes.PHYSICAL 
      ? defender.physicalDefense 
      : defender.spiritualDefense;
    
    const reduction = Math.max(0, defense * (1 - penetration));
    const damage = Math.max(1, base * (1 - reduction / (reduction + 100)));
    
    return Math.floor(damage);
  },
  
  calculateHitChance: (attacker, defender) => {
    const accuracy = attacker.accuracy || 100;
    const evasion = defender.evasion || 0;
    
    return Math.min(95, Math.max(20, accuracy - evasion));
  },
  
  calculateCritical: (attacker, defender) => {
    const critChance = (attacker.criticalChance || 5) - (defender.criticalResistance || 0);
    const isCritical = Math.random() * 100 < Math.max(0, Math.min(critChance, 50));
    
    return {
      isCritical,
      multiplier: isCritical ? 2 + (attacker.criticalDamage || 0) : 1
    };
  }
};

