export const combatRewards = {
  calculateExperience: (playerLevel, enemyLevel, enemyRank) => {
    const basExp = 100;
    const levelDiff = enemyLevel - playerLevel;
    const multiplier = Math.max(0.1, 1 + levelDiff * 0.1);
    const rankBonus = {
      normal: 1,
      elite: 2,
      boss: 5
    }[enemyRank] || 1;
    
    return Math.floor(basExp * multiplier * rankBonus);
  },
  
  calculateLoot: (enemy, luck = 0) => {
    const baseLoot = enemy.loot || [];
    const lootChance = Math.min(100, (enemy.lootChance || 50) + luck);
    
    return baseLoot.filter(() => Math.random() * 100 < lootChance);
  }
};
