import React, { useState } from 'react';
import { useGameContext } from '../../context/GameContext';
import Button from '../ui/Button';
import styles from './CreateGroupForm.module.css';

/**
 *      
 * @param {Function} onSubmit -   
 * @param {Function} onCancel -   
 * @param {boolean} loading -  
 */
const CreateGroupForm = ({ onSubmit, onCancel, loading }) => {
  const { state } = useGameContext();
  const { cultivation } = state.player;
  
  //    
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    maxMembers: 5,
    minCultivationLevel: 1,
    isPrivate: false,
    requiresApproval: true,
    leaderSpecialization: 'damage' //   
  });
  
  //   
  const specializations = [
    { id: 'damage', name: '', description: '    ' },
    { id: 'tank', name: '', description: '      ' },
    { id: 'support', name: '', description: '      ' },
    { id: 'control', name: '', description: '       ' },
    { id: 'healer', name: '', description: '    ' }
  ];
  
  //    
  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };
  
  //   
  const handleSubmit = (e) => {
    e.preventDefault();
    
    //    
    if (!formData.name.trim()) {
      alert(',   ');
      return;
    }
    
    //   
    onSubmit(formData);
  };
  
  return (
    <div className={styles.formContainer}>
      <h3 className={styles.title}>  </h3>
      
      <form className={styles.form} onSubmit={handleSubmit}>
        <div className={styles.formGroup}>
          <label htmlFor="name" className={styles.label}> </label>
          <input
            type="text"
            id="name"
            name="name"
            className={styles.input}
            value={formData.name}
            onChange={handleChange}
            placeholder="  "
            required
            maxLength={30}
            disabled={loading}
          />
          <div className={styles.fieldHint}> 2  30 </div>
        </div>
        
        <div className={styles.formGroup}>
          <label htmlFor="description" className={styles.label}></label>
          <textarea
            id="description"
            name="description"
            className={styles.textarea}
            value={formData.description}
            onChange={handleChange}
            placeholder="   "
            rows={3}
            maxLength={200}
            disabled={loading}
          />
          <div className={styles.fieldHint}> 200 </div>
        </div>
        
        <div className={styles.formGroup}>
          <label htmlFor="maxMembers" className={styles.label}>  </label>
          <select
            id="maxMembers"
            name="maxMembers"
            className={styles.select}
            value={formData.maxMembers}
            onChange={handleChange}
            disabled={loading}
          >
            <option value={2}>2 </option>
            <option value={3}>3 </option>
            <option value={4}>4 </option>
            <option value={5}>5 </option>
            {cultivation && cultivation.level >= 4 && (
              <>
                <option value={6}>6 </option>
                <option value={7}>7 </option>
              </>
            )}
            {cultivation && cultivation.level >= 7 && (
              <>
                <option value={8}>8 </option>
                <option value={9}>9 </option>
                <option value={10}>10 </option>
              </>
            )}
          </select>
        </div>
        
        <div className={styles.formGroup}>
          <label htmlFor="minCultivationLevel" className={styles.label}>  </label>
          <select
            id="minCultivationLevel"
            name="minCultivationLevel"
            className={styles.select}
            value={formData.minCultivationLevel}
            onChange={handleChange}
            disabled={loading}
          >
            <option value={1}> 1 ( )</option>
            <option value={2}> 2 ( )</option>
            <option value={3}> 3 ( )</option>
            <option value={4}> 4 ( )</option>
            <option value={5}> 5 ( )</option>
            <option value={6}> 6 ( )</option>
            <option value={7}> 7 ( )</option>
            <option value={8}> 8 ( )</option>
            <option value={9}> 9 ( )</option>
          </select>
        </div>
        
        <div className={styles.formGroup}>
          <label htmlFor="leaderSpecialization" className={styles.label}>   </label>
          <select
            id="leaderSpecialization"
            name="leaderSpecialization"
            className={styles.select}
            value={formData.leaderSpecialization}
            onChange={handleChange}
            disabled={loading}
          >
            {specializations.map(spec => (
              <option key={spec.id} value={spec.id}>
                {spec.name} - {spec.description}
              </option>
            ))}
          </select>
        </div>
        
        <div className={styles.checkboxGroup}>
          <label className={styles.checkboxLabel}>
            <input
              type="checkbox"
              name="isPrivate"
              checked={formData.isPrivate}
              onChange={handleChange}
              disabled={loading}
            />
            <span className={styles.checkboxText}>  (  )</span>
          </label>
        </div>
        
        <div className={styles.checkboxGroup}>
          <label className={styles.checkboxLabel}>
            <input
              type="checkbox"
              name="requiresApproval"
              checked={formData.requiresApproval}
              onChange={handleChange}
              disabled={loading}
            />
            <span className={styles.checkboxText}>   </span>
          </label>
        </div>
        
        <div className={styles.actions}>
          <Button 
            type="button" 
            onClick={onCancel} 
            className={styles.cancelButton}
            disabled={loading}
          >
            
          </Button>
          <Button 
            type="submit" 
            className={styles.submitButton}
            disabled={loading}
          >
            {loading ? '...' : ' '}
          </Button>
        </div>
      </form>
    </div>
  );
};

export default CreateGroupForm;
