const { Model, DataTypes } = require('../services/database');
const { unifiedDatabase } = require('../services/database-connection-manager');

//   Sequelize 
let sequelizeInstance = null;
async function getSequelize() {
  if (!sequelizeInstance) {
    const result = await unifiedDatabase.getSequelizeInstance();
    sequelizeInstance = result.db;
  }
  return sequelizeInstance;
}

class CharacterStats extends Model {
  static associate(models) {
    //     
    CharacterStats.belongsTo(models.User, {
      foreignKey: 'userId',
      as: 'user'
    });
  }
}

// :   super   
CharacterStats.init = async function() {
  const sequelize = await getSequelize();
  
  //   init  
  return Model.init.call(this, {
  userId: {
    type: DataTypes.INTEGER,
    references: {
      model: 'Users',
      key: 'id'
    },
    onUpdate: 'CASCADE',
    onDelete: 'CASCADE',
    unique: true
  },
  strength: {
    type: DataTypes.INTEGER,
    defaultValue: 10
  },
  intellect: {
    type: DataTypes.INTEGER,
    defaultValue: 10
  },
  spirit: {
    type: DataTypes.INTEGER,
    defaultValue: 10
  },
  agility: {
    type: DataTypes.INTEGER,
    defaultValue: 10
  },
  health: {
    type: DataTypes.INTEGER,
    defaultValue: 10
  },
  physicalDefense: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  spiritualDefense: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  attackSpeed: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  criticalChance: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  movementSpeed: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  },
  luck: {
    type: DataTypes.INTEGER,
    defaultValue: 0
  }
  }, {
    sequelize,
    modelName: 'CharacterStats',
    tableName: 'CharacterStats'
  });
};

//      
(async () => {
  try {
    await CharacterStats.init();
    console.log('CharacterStats  ');
  } catch (error) {
    console.error('   CharacterStats:', error);
    console.error(error.stack);
  }
})();

module.exports = CharacterStats;
