const QuestProgress = require('../models/quest-progress');
const Quest = require('../models/quest');
const User = require('../models/user');

/**
 *     
 */
class QuestService {
  /**
   *    
   * @param {number} userId - ID 
   * @returns {Promise<Object>} -    
   */
  static async getQuests(userId) {
    try {
      //    
      const questProgress = await QuestProgress.findAll({
        where: { userId },
        include: [
          {
            model: Quest,
            as: 'quest'
          }
        ]
      });
      
      //    
      const quests = {
        available: [],
        active: [],
        completed: []
      };
      
      questProgress.forEach(progress => {
        const quest = {
          id: progress.quest.id,
          title: progress.quest.title,
          description: progress.quest.description,
          type: progress.quest.type,
          level: progress.quest.level,
          rewards: progress.quest.rewards,
          objectives: progress.quest.objectives,
          status: progress.status,
          progress: progress.progress,
          startedAt: progress.startedAt,
          completedAt: progress.completedAt
        };
        
        if (progress.status === 'available') {
          quests.available.push(quest);
        } else if (progress.status === 'active') {
          quests.active.push(quest);
        } else if (progress.status === 'completed') {
          quests.completed.push(quest);
        }
      });
      
      return quests;
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }
  
  /**
   *  
   * @param {number} userId - ID 
   * @param {number} questId - ID 
   * @returns {Promise<Object>} -  
   */
  static async acceptQuest(userId, questId) {
    try {
      // ,   
      const quest = await Quest.findByPk(questId);
      
      if (!quest) {
        throw new Error('  ');
      }
      
      // ,     
      const existingProgress = await QuestProgress.findOne({
        where: {
          userId,
          questId
        }
      });
      
      if (existingProgress && existingProgress.status !== 'available') {
        throw new Error('    ');
      }
      
      //       
      let questProgress;
      
      if (existingProgress) {
        existingProgress.status = 'active';
        existingProgress.startedAt = new Date();
        await existingProgress.save();
        questProgress = existingProgress;
      } else {
        questProgress = await QuestProgress.create({
          userId,
          questId,
          status: 'active',
          progress: {},
          startedAt: new Date()
        });
      }
      
      return {
        id: quest.id,
        title: quest.title,
        description: quest.description,
        type: quest.type,
        level: quest.level,
        rewards: quest.rewards,
        objectives: quest.objectives,
        status: questProgress.status,
        progress: questProgress.progress,
        startedAt: questProgress.startedAt
      };
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }
  
  /**
   *   
   * @param {number} userId - ID 
   * @param {number} questId - ID 
   * @param {Object} progress -  
   * @returns {Promise<Object>} -  
   */
  static async updateQuestProgress(userId, questId, progress) {
    try {
      //     
      const questProgress = await QuestProgress.findOne({
        where: {
          userId,
          questId
        },
        include: [
          {
            model: Quest,
            as: 'quest'
          }
        ]
      });
      
      if (!questProgress) {
        throw new Error('     ');
      }
      
      if (questProgress.status !== 'active') {
        throw new Error('  ');
      }
      
      //   
      questProgress.progress = {
        ...questProgress.progress,
        ...progress
      };
      
      await questProgress.save();
      
      return {
        id: questProgress.quest.id,
        title: questProgress.quest.title,
        description: questProgress.quest.description,
        type: questProgress.quest.type,
        level: questProgress.quest.level,
        rewards: questProgress.quest.rewards,
        objectives: questProgress.quest.objectives,
        status: questProgress.status,
        progress: questProgress.progress,
        startedAt: questProgress.startedAt
      };
    } catch (error) {
      console.error('    :', error);
      throw error;
    }
  }
  
  /**
   *  
   * @param {number} userId - ID 
   * @param {number} questId - ID 
   * @returns {Promise<Object>} -  
   */
  static async completeQuest(userId, questId) {
    try {
      //     
      const questProgress = await QuestProgress.findOne({
        where: {
          userId,
          questId
        },
        include: [
          {
            model: Quest,
            as: 'quest'
          }
        ]
      });
      
      if (!questProgress) {
        throw new Error('     ');
      }
      
      if (questProgress.status !== 'active') {
        throw new Error('  ');
      }
      
      //   
      questProgress.status = 'completed';
      questProgress.completedAt = new Date();
      await questProgress.save();
      
      return {
        id: questProgress.quest.id,
        title: questProgress.quest.title,
        description: questProgress.quest.description,
        type: questProgress.quest.type,
        level: questProgress.quest.level,
        rewards: questProgress.quest.rewards,
        objectives: questProgress.quest.objectives,
        status: questProgress.status,
        progress: questProgress.progress,
        startedAt: questProgress.startedAt,
        completedAt: questProgress.completedAt
      };
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }
}

module.exports = QuestService;
