import React from 'react';
import styled from 'styled-components';
import { useGame } from '../../context/GameContext';

const Container = styled.div`
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 20px;
`;

const CharacterPanel = styled.div`
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 8px;
  padding: 20px;
`;

const Avatar = styled.div`
  width: 200px;
  height: 200px;
  margin: 0 auto 20px;
  background: #2c2c2c;
  border: 2px solid #d4af37;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  color: #d4af37;
`;

const CharacterInfo = styled.div`
  text-align: center;
  margin-bottom: 20px;
`;

const CharacterName = styled.h2`
  color: #d4af37;
  margin: 0 0 5px;
`;

const CharacterTitle = styled.div`
  color: #aaa;
  font-size: 0.9rem;
  margin-bottom: 10px;
`;

const CultivationRealm = styled.div`
  color: #f0f0f0;
  font-size: 1.1rem;
  margin-bottom: 20px;
`;

const StatsPanel = styled.div`
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 8px;
  padding: 20px;
`;

const StatGroup = styled.div`
  margin-bottom: 20px;
  
  &:last-child {
    margin-bottom: 0;
  }
`;

const StatGroupTitle = styled.h3`
  color: #d4af37;
  margin: 0 0 10px;
  font-size: 1.1rem;
  border-bottom: 1px solid rgba(212, 175, 55, 0.2);
  padding-bottom: 5px;
`;

const StatGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
`;

const StatItem = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
`;

const StatControls = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
`;

const StatButton = styled.button`
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(212, 175, 55, 0.2);
  color: #d4af37;
  border: 1px solid #d4af37;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
  
  &:hover:not(:disabled) {
    background: rgba(212, 175, 55, 0.4);
  }
  
  &:disabled {
    opacity: 0.3;
    cursor: not-allowed;
  }
`;

const UnassignedPoints = styled.div`
  margin-top: 10px;
  padding: 10px;
  background: rgba(212, 175, 55, 0.1);
  border-radius: 4px;
  text-align: center;
  color: #d4af37;
  font-weight: bold;
`;

const StatLabel = styled.span`
  color: #aaa;
`;

const StatValue = styled.span`
  color: #f0f0f0;
`;

const BackgroundInfo = styled.div`
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid rgba(212, 175, 55, 0.2);
`;

const BackgroundTitle = styled.h3`
  color: #d4af37;
  margin: 0 0 10px;
  font-size: 1.1rem;
`;

const BackgroundText = styled.p`
  color: #f0f0f0;
  font-size: 0.9rem;
  line-height: 1.4;
  margin: 0;
`;

function CharacterTab() {
  const { state, actions } = useGame();
  const player = state?.player || {};
  const cultivation = player?.cultivation || {};
  const stats = player?.stats || {};
  const derivedStats = player?.secondaryStats || {};
  const unassignedPoints = stats.unassignedPoints || 0;
  
  //      
  const getCultivationStageName = (level) => {
    level = level || 0;
    if (level < 10) return '';
    if (level < 20) return '';
    if (level < 30) return '';
    if (level < 40) return '';
    if (level < 50) return '';
    return '';
  };
  
  //     
  const formatStat = (value) => {
    value = value || 0;
    if (value >= 1000) {
      return `${(value / 1000).toFixed(1)}k`;
    }
    return value.toString();
  };
  
  //    
  const increaseStat = (statName) => {
    if (unassignedPoints <= 0) return;
    
    const newStats = {
      ...stats,
      [statName]: (stats[statName] || 0) + 1,
      unassignedPoints: unassignedPoints - 1
    };
    
    actions.updatePlayerStats(newStats);
  };
  
  //       
  console.log('DEBUG - CharacterTab -  :', state.player);
  console.log('DEBUG - CharacterTab -  :', player.equipmentBonuses);
  console.log('DEBUG - CharacterTab - :', stats);
  console.log('DEBUG - CharacterTab -  :', derivedStats);
  
  return (
    <Container>
      <CharacterPanel>
        <Avatar>
          {(player.name || '?')[0].toUpperCase()}
        </Avatar>
        
        <CharacterInfo>
          <CharacterName>{player.name || ''}</CharacterName>
          <CharacterTitle>{player.title || ' '}</CharacterTitle>
          <CultivationRealm>
            {getCultivationStageName(cultivation.level)} ({cultivation.level || 0} )
          </CultivationRealm>
        </CharacterInfo>
        
        <BackgroundInfo>
          <BackgroundTitle></BackgroundTitle>
          <BackgroundText>
            {player.background || '     ...'}
          </BackgroundText>
        </BackgroundInfo>
      </CharacterPanel>
      
      <StatsPanel>
        <StatGroup>
          <StatGroupTitle> </StatGroupTitle>
          {unassignedPoints > 0 && (
            <UnassignedPoints>
               : {unassignedPoints}
            </UnassignedPoints>
          )}
          <StatGrid>
            <StatItem>
              <StatLabel></StatLabel>
              <StatControls>
                <StatValue>
                  {formatStat(stats.strength)}
                  {/*       */}
                  {player.equipmentBonuses?.stats?.strength > 0 && (
                    <span style={{ color: '#4caf50', marginLeft: '5px' }}>
                      +{player.equipmentBonuses.stats.strength}
                    </span>
                  )}
                </StatValue>
                {unassignedPoints > 0 && (
                  <StatButton onClick={() => increaseStat('strength')}>+</StatButton>
                )}
              </StatControls>
            </StatItem>
            <StatItem>
              <StatLabel></StatLabel>
              <StatControls>
                <StatValue>
                  {formatStat(stats.agility)}
                  {/*  agility  dexterity   */}
                  {player.equipmentBonuses?.stats?.dexterity > 0 && (
                    <span style={{ color: '#4caf50', marginLeft: '5px' }}>
                      +{player.equipmentBonuses.stats.dexterity}
                    </span>
                  )}
                </StatValue>
                {unassignedPoints > 0 && (
                  <StatButton onClick={() => increaseStat('agility')}>+</StatButton>
                )}
              </StatControls>
            </StatItem>
            <StatItem>
              <StatLabel></StatLabel>
              <StatControls>
                <StatValue>
                  {formatStat(stats.health)}
                  {/*  health  vitality   */}
                  {player.equipmentBonuses?.stats?.vitality > 0 && (
                    <span style={{ color: '#4caf50', marginLeft: '5px' }}>
                      +{player.equipmentBonuses.stats.vitality}
                    </span>
                  )}
                </StatValue>
                {unassignedPoints > 0 && (
                  <StatButton onClick={() => increaseStat('health')}>+</StatButton>
                )}
              </StatControls>
            </StatItem>
            <StatItem>
              <StatLabel></StatLabel>
              <StatControls>
                <StatValue>
                  {formatStat(stats.intellect)}
                  {/*  intellect  intelligence   */}
                  {player.equipmentBonuses?.stats?.intelligence > 0 && (
                    <span style={{ color: '#4caf50', marginLeft: '5px' }}>
                      +{player.equipmentBonuses.stats.intelligence}
                    </span>
                  )}
                </StatValue>
                {unassignedPoints > 0 && (
                  <StatButton onClick={() => increaseStat('intellect')}>+</StatButton>
                )}
              </StatControls>
            </StatItem>
            <StatItem>
              <StatLabel></StatLabel>
              <StatControls>
                <StatValue>
                  {formatStat(stats.spirit)}
                  {/*  spirit  perception   */}
                  {player.equipmentBonuses?.stats?.perception > 0 && (
                    <span style={{ color: '#4caf50', marginLeft: '5px' }}>
                      +{player.equipmentBonuses.stats.perception}
                    </span>
                  )}
                </StatValue>
                {unassignedPoints > 0 && (
                  <StatButton onClick={() => increaseStat('spirit')}>+</StatButton>
                )}
              </StatControls>
            </StatItem>
            {/*    ,   */}
            {player.equipmentBonuses?.stats?.luck > 0 && (
              <StatItem>
                <StatLabel></StatLabel>
                <StatControls>
                  <StatValue>
                    {0}
                    <span style={{ color: '#4caf50', marginLeft: '5px' }}>
                      +{player.equipmentBonuses.stats.luck}
                    </span>
                  </StatValue>
                </StatControls>
              </StatItem>
            )}
          </StatGrid>
        </StatGroup>
        
        <StatGroup>
          <StatGroupTitle> </StatGroupTitle>
          <StatGrid>
            <StatItem>
              <StatLabel>. </StatLabel>
              <StatValue>
                {formatStat(stats.strength)}
                {player.equipmentBonuses?.combat?.physicalDamage > 0 && (
                  <span style={{ color: '#4caf50', marginLeft: '5px' }}>
                    +{player.equipmentBonuses.combat.physicalDamage}
                  </span>
                )}
              </StatValue>
            </StatItem>
            <StatItem>
              <StatLabel>. </StatLabel>
              <StatValue>
                {formatStat(derivedStats.magicalAttack)}
                {player.equipmentBonuses?.combat?.magicDamage > 0 && (
                  <span style={{ color: '#4caf50', marginLeft: '5px' }}>
                    +{player.equipmentBonuses.combat.magicDamage}
                  </span>
                )}
              </StatValue>
            </StatItem>
            <StatItem>
              <StatLabel>. </StatLabel>
              <StatValue>
                {formatStat(derivedStats.physicalDefense)}
                {player.equipmentBonuses?.combat?.physicalDefense > 0 && (
                  <span style={{ color: '#4caf50', marginLeft: '5px' }}>
                    +{player.equipmentBonuses.combat.physicalDefense}
                  </span>
                )}
              </StatValue>
            </StatItem>
            <StatItem>
              <StatLabel>. </StatLabel>
              <StatValue>
                {formatStat(derivedStats.magicalDefense)}
                {player.equipmentBonuses?.combat?.magicDefense > 0 && (
                  <span style={{ color: '#4caf50', marginLeft: '5px' }}>
                    +{player.equipmentBonuses.combat.magicDefense}
                  </span>
                )}
              </StatValue>
            </StatItem>
            <StatItem>
              <StatLabel></StatLabel>
              <StatValue>
                {formatStat(derivedStats.evasion)}
                {player.equipmentBonuses?.combat?.dodgeChance > 0 && (
                  <span style={{ color: '#4caf50', marginLeft: '5px' }}>
                    +{player.equipmentBonuses.combat.dodgeChance}%
                  </span>
                )}
              </StatValue>
            </StatItem>
            <StatItem>
              <StatLabel></StatLabel>
              <StatValue>{formatStat(derivedStats.accuracy)}</StatValue>
            </StatItem>
          </StatGrid>
        </StatGroup>
        
        <StatGroup>
          <StatGroupTitle></StatGroupTitle>
          <StatGrid>
            <StatItem>
              <StatLabel> </StatLabel>
              <StatValue>{cultivation.energy || 0}/{cultivation.maxEnergy || 100}</StatValue>
            </StatItem>
            <StatItem>
              <StatLabel></StatLabel>
              <StatValue>{cultivation.experience || 0}/{cultivation.experienceRequired || 100}</StatValue>
            </StatItem>
            <StatItem>
              <StatLabel> </StatLabel>
              <StatValue>{cultivation.daoUnderstanding || 0}%</StatValue>
            </StatItem>
            <StatItem>
              <StatLabel></StatLabel>
              <StatValue>{cultivation.breakthroughs || 0}</StatValue>
            </StatItem>
          </StatGrid>
        </StatGroup>
      </StatsPanel>
    </Container>
  );
}

export default CharacterTab;
