/**
 * API-     
 *  HTTP         
 */

//  URL API
const API_URL = '/api';

/**
 *         API
 */
class CultivationServiceAPI {
  /**
   *     
   * @param {number} userId - ID 
   * @returns {Promise<Object>} -   
   */
  static async getCultivationProgress(userId) {
    try {
      const response = await fetch(`${API_URL}/cultivation/${userId}`, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('authToken')}`,
        },
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || '     ');
      }

      const cultivation = await response.json();
      return cultivation;
    } catch (error) {
      console.error('     :', error);
      throw error;
    }
  }

  /**
   *     
   * @param {number} userId - ID 
   * @param {Object} data -    
   * @returns {Promise<Object>} -    
   */
  static async updateCultivationProgress(userId, data) {
    try {
      const response = await fetch(`${API_URL}/cultivation/${userId}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('authToken')}`,
        },
        body: JSON.stringify(data),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || '     ');
      }

      const updatedCultivation = await response.json();
      return updatedCultivation;
    } catch (error) {
      console.error('     :', error);
      throw error;
    }
  }

  /**
   *      
   * @param {number} userId - ID 
   * @returns {Promise<Object>} -  
   */
  static async checkBreakthroughPossibility(userId) {
    try {
      const response = await fetch(`${API_URL}/cultivation/${userId}/breakthrough-check`, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('authToken')}`,
        },
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || '    ');
      }

      const result = await response.json();
      return result;
    } catch (error) {
      console.error('    :', error);
      throw error;
    }
  }

  /**
   *  
   * @param {number} userId - ID 
   * @param {Object} tribulationResult -  
   * @returns {Promise<Object>} -   
   */
  static async completeTribulation(userId, tribulationResult) {
    try {
      const response = await fetch(`${API_URL}/cultivation/${userId}/complete-tribulation`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('authToken')}`,
        },
        body: JSON.stringify(tribulationResult),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || '   ');
      }

      const result = await response.json();
      return result;
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }

  /**
   *   " "
   * @param {number} userId - ID 
   * @param {number} amount -    
   * @returns {Promise<Object>} -    
   */
  static async increaseBottleneckProgress(userId, amount) {
    try {
      const response = await fetch(`${API_URL}/cultivation/${userId}/bottleneck-progress`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('authToken')}`,
        },
        body: JSON.stringify({ amount }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || '    " "');
      }

      const result = await response.json();
      return result;
    } catch (error) {
      console.error('    " ":', error);
      throw error;
    }
  }

  /**
   *  
   * @param {number} userId - ID 
   * @returns {Promise<Object>} -  
   */
  static async gainInsight(userId) {
    try {
      const response = await fetch(`${API_URL}/cultivation/${userId}/gain-insight`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('authToken')}`,
        },
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || '   ');
      }

      const result = await response.json();
      return result;
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }
}

export default CultivationServiceAPI;