/**
 *     AuthService     
 *             
 */
import { isServerEnvironment } from '../sequelize-config';

//  API-   
import AuthServiceAPI from './auth-api'; //  API ( )

//    
function isTrueServerEnvironment() {
  return (
    isServerEnvironment &&
    typeof process !== 'undefined' &&
    process.versions &&
    process.versions.node
  );
}

//       
async function getAuthService() {
  if (isTrueServerEnvironment()) {
    try {
      //    
      //    webpack      
      const serverModule = await import('./auth');
      return serverModule.default || serverModule;
    } catch (error) {
      console.error('    AuthService:', error);
      console.warn(' API  -  ');
      return AuthServiceAPI;
    }
  }
  //     API 
  return AuthServiceAPI;
}

//   ,      
const AuthService = new Proxy({}, {
  get: function(target, prop) {
    return async function(...args) {
      const service = await getAuthService();
      if (typeof service[prop] === 'function') {
        return service[prop](...args);
      } else {
        throw new Error(` ${prop}    AuthService`);
      }
    };
  }
});

export default AuthService;