import React, { useState, useEffect } from 'react';
import { useGame } from '../../context/GameContext';
import styled from 'styled-components';

//  
const TabContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px;
  color: #f0f0f0;
`;

const TabHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
`;

const TabTitle = styled.h2`
  font-size: 24px;
  margin: 0;
  color: #ffd700;
`;

const TabContent = styled.div`
  display: flex;
  flex: 1;
  gap: 20px;
`;

const LeftPanel = styled.div`
  flex: 1;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  padding: 15px;
  display: flex;
  flex-direction: column;
`;

const RightPanel = styled.div`
  flex: 1;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  padding: 15px;
  display: flex;
  flex-direction: column;
`;

const TabMenu = styled.div`
  display: flex;
  margin-bottom: 15px;
  border-bottom: 1px solid #444;
`;

const TabButton = styled.button`
  background: ${props => props.active ? 'rgba(255, 215, 0, 0.2)' : 'transparent'};
  color: ${props => props.active ? '#ffd700' : '#ccc'};
  border: none;
  padding: 10px 15px;
  font-size: 16px;
  cursor: pointer;
  border-bottom: ${props => props.active ? '2px solid #ffd700' : 'none'};
  transition: all 0.3s;
  
  &:hover {
    background: rgba(255, 215, 0, 0.1);
    color: #ffd700;
  }
`;

const RecipeList = styled.div`
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const RecipeCard = styled.div`
  display: flex;
  background: ${props => props.selected ? 'rgba(255, 215, 0, 0.1)' : 'rgba(30, 30, 30, 0.7)'};
  border: 1px solid ${props => props.selected ? '#ffd700' : '#444'};
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
  transition: all 0.2s;
  
  &:hover {
    background: rgba(255, 215, 0, 0.05);
    border-color: #ffd700;
  }
`;

const RecipeIcon = styled.div`
  width: 40px;
  height: 40px;
  background: #333;
  border-radius: 5px;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
`;

const RecipeInfo = styled.div`
  flex: 1;
`;

const RecipeName = styled.div`
  font-weight: bold;
  color: ${props => {
    switch(props.rarity) {
      case 'common': return '#aaa';
      case 'uncommon': return '#1eff00';
      case 'rare': return '#0070dd';
      case 'epic': return '#a335ee';
      case 'legendary': return '#ff8000';
      default: return '#aaa';
    }
  }};
`;

const RecipeDescription = styled.div`
  font-size: 14px;
  color: #aaa;
  margin-top: 5px;
`;

const RecipeDetails = styled.div`
  padding: 15px;
  background: rgba(30, 30, 30, 0.7);
  border-radius: 5px;
  margin-top: 15px;
`;

const DetailTitle = styled.h3`
  margin: 0 0 10px 0;
  color: #ffd700;
`;

const DetailRow = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
`;

const DetailLabel = styled.div`
  color: #aaa;
`;

const DetailValue = styled.div`
  color: #fff;
`;

const IngredientsList = styled.div`
  margin-top: 15px;
`;

const IngredientItem = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  padding: 8px;
  border-radius: 4px;
  background: ${props => props.available ? 'rgba(0, 128, 0, 0.1)' : 'rgba(128, 0, 0, 0.1)'};
`;

const IngredientName = styled.div`
  flex: 1;
  color: ${props => props.available ? '#5f5' : '#f55'};
`;

const IngredientQuantity = styled.div`
  color: ${props => props.available ? '#5f5' : '#f55'};
`;

const ActionButton = styled.button`
  background: ${props => props.primary ? 'rgba(255, 215, 0, 0.2)' : 'rgba(80, 80, 80, 0.3)'};
  color: ${props => props.primary ? '#ffd700' : '#ccc'};
  border: 1px solid ${props => props.primary ? '#ffd700' : '#444'};
  border-radius: 5px;
  padding: 10px 15px;
  margin-top: 15px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s;
  
  &:hover {
    background: ${props => props.primary ? 'rgba(255, 215, 0, 0.3)' : 'rgba(80, 80, 80, 0.5)'};
  }
  
  &:disabled {
    background: rgba(50, 50, 50, 0.3);
    color: #666;
    border-color: #333;
    cursor: not-allowed;
  }
`;

const SearchBar = styled.input`
  background: rgba(30, 30, 30, 0.7);
  color: #fff;
  border: 1px solid #444;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 15px;
  font-size: 16px;
  
  &::placeholder {
    color: #777;
  }
`;

const FilterContainer = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
`;

const FilterSelect = styled.select`
  background: rgba(30, 30, 30, 0.7);
  color: #fff;
  border: 1px solid #444;
  border-radius: 5px;
  padding: 8px;
  font-size: 14px;
`;

const NoRecipesMessage = styled.div`
  text-align: center;
  padding: 20px;
  color: #777;
  font-style: italic;
`;

const ResultMessage = styled.div`
  margin-top: 20px;
  padding: 15px;
  border-radius: 5px;
  background: ${props => props.success ? 'rgba(0, 128, 0, 0.2)' : 'rgba(128, 0, 0, 0.2)'};
  color: ${props => props.success ? '#5f5' : '#f55'};
  text-align: center;
`;

const CraftingProgress = styled.div`
  margin-top: 20px;
  height: 20px;
  background: rgba(30, 30, 30, 0.7);
  border-radius: 10px;
  overflow: hidden;
`;

const ProgressBar = styled.div`
  height: 100%;
  width: ${props => props.progress}%;
  background: linear-gradient(to right, #ffd700, #ffaa00);
  border-radius: 10px;
  transition: width 0.1s linear;
`;

/**
 *   ""
 */
const AlchemyTab = () => {
  const { state, actions } = useGame();
  const { player, cultivation } = state;
  
  //  
  const [activeTab, setActiveTab] = useState('pills'); // pills, talismans, weapons, armor, accessories
  const [recipes, setRecipes] = useState([]);
  const [selectedRecipe, setSelectedRecipe] = useState(null);
  const [searchQuery, setSearchQuery] = useState('');
  const [rarityFilter, setRarityFilter] = useState('all');
  const [craftingStatus, setCraftingStatus] = useState(null); // null, 'crafting', 'success', 'failed'
  const [craftingProgress, setCraftingProgress] = useState(0);
  const [craftingResult, setCraftingResult] = useState(null);
  
  //      
  const getRecipeType = () => {
    switch (activeTab) {
      case 'pills': return 'pill';
      case 'talismans': return 'talisman';
      case 'weapons': return 'weapon';
      case 'armor': return 'armor';
      case 'accessories': return 'accessory';
      default: return 'pill';
    }
  };
  
  //     
  useEffect(() => {
    const loadRecipes = async () => {
      try {
        //     API   
        // : const response = await api.get(`/alchemy/recipes/${getRecipeType()}`);
        
        //        
        const currentType = getRecipeType();
        
        //    -        
        let mockRecipes = [];
        
        // 
        if (currentType === 'pill') {
          mockRecipes = [
            {
              id: 1,
              name: '  ',
              description: ' ,     .',
              type: 'pill',
              rarity: 'common',
              requiredLevel: 1,
              requiredStage: ' ',
              successRate: 95,
              ingredients: [
                { id: 'low_grade_herb', name: '  ', quantity: 3 },
                { id: 'medium_grade_herb', name: '  ', quantity: 1 }
              ]
            },
            {
              id: 2,
              name: '  ',
              description: '  ,     .',
              type: 'pill',
              rarity: 'uncommon',
              requiredLevel: 3,
              requiredStage: ' ',
              successRate: 85,
              ingredients: [
                { id: 'low_grade_herb', name: '  ', quantity: 2 },
                { id: 'medium_grade_herb', name: '  ', quantity: 2 },
                { id: 'spirit_iron', name: ' ', quantity: 1 }
              ]
            },
            {
              id: 3,
              name: '  ',
              description: '   ,     .',
              type: 'pill',
              rarity: 'uncommon',
              requiredLevel: 5,
              requiredStage: ' ',
              successRate: 80,
              ingredients: [
                { id: 'medium_grade_herb', name: '  ', quantity: 3 },
                { id: 'water_essence', name: ' ', quantity: 1 },
                { id: 'spirit_stone', name: ' ', quantity: 2 }
              ]
            }
          ];
        } 
        // 
        else if (currentType === 'talisman') {
          mockRecipes = [
            {
              id: 4,
              name: ' ',
              description: '  ,   .',
              type: 'talisman',
              rarity: 'common',
              requiredLevel: 2,
              requiredStage: ' ',
              successRate: 90,
              ingredients: [
                { id: 'spirit_paper', name: ' ', quantity: 2 },
                { id: 'spirit_ink', name: ' ', quantity: 1 }
              ]
            },
            {
              id: 5,
              name: '  ',
              description: ' ,      .',
              type: 'talisman',
              rarity: 'uncommon',
              requiredLevel: 4,
              requiredStage: ' ',
              successRate: 80,
              ingredients: [
                { id: 'spirit_paper', name: ' ', quantity: 3 },
                { id: 'spirit_ink', name: ' ', quantity: 2 },
                { id: 'fire_essence', name: ' ', quantity: 2 }
              ]
            }
          ];
        } 
        // 
        else if (currentType === 'weapon') {
          mockRecipes = [
            {
              id: 6,
              name: ' ',
              description: ',         ,    .',
              type: 'weapon',
              rarity: 'uncommon',
              requiredLevel: 5,
              requiredStage: ' ',
              successRate: 70,
              ingredients: [
                { id: 'spirit_iron', name: ' ', quantity: 5 },
                { id: 'spirit_stone', name: ' ', quantity: 3 },
                { id: 'spirit_essence', name: ' ', quantity: 1 }
              ]
            },
            {
              id: 7,
              name: '  ',
              description: ',     ,    .',
              type: 'weapon',
              rarity: 'rare',
              requiredLevel: 3,
              requiredStage: ' ',
              successRate: 60,
              ingredients: [
                { id: 'spirit_iron', name: ' ', quantity: 8 },
                { id: 'spirit_crystal', name: ' ', quantity: 3 },
                { id: 'fire_essence', name: ' ', quantity: 2 },
                { id: 'water_essence', name: ' ', quantity: 2 }
              ]
            }
          ];
        } 
        // 
        else if (currentType === 'armor') {
          mockRecipes = [
            {
              id: 8,
              name: '   ',
              description: ',   ,        .',
              type: 'armor',
              rarity: 'uncommon',
              requiredLevel: 4,
              requiredStage: ' ',
              successRate: 75,
              ingredients: [
                { id: 'spirit_silk', name: ' ', quantity: 6 },
                { id: 'spirit_thread', name: ' ', quantity: 4 },
                { id: 'spirit_essence', name: ' ', quantity: 2 }
              ]
            }
          ];
        } 
        // 
        else if (currentType === 'accessory') {
          mockRecipes = [
            {
              id: 9,
              name: '  ',
              description: ',         .',
              type: 'accessory',
              rarity: 'uncommon',
              requiredLevel: 3,
              requiredStage: ' ',
              successRate: 80,
              ingredients: [
                { id: 'spirit_iron', name: ' ', quantity: 3 },
                { id: 'spirit_stone', name: ' ', quantity: 2 },
                { id: 'spirit_essence', name: ' ', quantity: 1 }
              ]
            },
            {
              id: 10,
              name: '  ',
              description: ',           .',
              type: 'accessory',
              rarity: 'rare',
              requiredLevel: 4,
              requiredStage: ' ',
              successRate: 70,
              ingredients: [
                { id: 'spirit_stone', name: ' ', quantity: 5 },
                { id: 'spirit_crystal', name: ' ', quantity: 2 },
                { id: 'fire_essence', name: ' ', quantity: 1 },
                { id: 'water_essence', name: ' ', quantity: 1 }
              ]
            }
          ];
        }
        
        setRecipes(mockRecipes);
        setSelectedRecipe(null);
      } catch (error) {
        console.error('    :', error);
      }
    };
    
    loadRecipes();
  }, [activeTab]);
  
  //  
  const filteredRecipes = React.useMemo(() => {
    if (!recipes || recipes.length === 0) {
      return [];
    }
    
    return recipes.filter(recipe => {
      //    
      const matchesSearch = searchQuery === '' || 
        recipe.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        recipe.description.toLowerCase().includes(searchQuery.toLowerCase());
      
      //   
      const matchesRarity = rarityFilter === 'all' || recipe.rarity === rarityFilter;
      
      //    (  )
      const playerStage = cultivation?.stage || ' ';
      const playerLevel = cultivation?.level || 1;
      
      const stageOrder = [' ', ' ', ' ', ' '];
      const playerStageIndex = stageOrder.indexOf(playerStage);
      const recipeStageIndex = stageOrder.indexOf(recipe.requiredStage);
      
      let isAvailable = true;
      
      //     ,  
      if (playerStageIndex < recipeStageIndex) {
        isAvailable = false;
      }
      
      //    ,  
      if (playerStageIndex === recipeStageIndex && playerLevel < recipe.requiredLevel) {
        isAvailable = false;
      }
      
      return matchesSearch && matchesRarity;
    });
  }, [recipes, searchQuery, rarityFilter, cultivation]);
  
  //     
  const checkIngredients = (recipe) => {
    if (!recipe || !recipe.ingredients || !player.inventory.items) {
      return false;
    }
    
    //       
    for (const ingredient of recipe.ingredients) {
      const inventoryItem = player.inventory.items.find(item => 
        item.itemId === ingredient.id && item.quantity >= ingredient.quantity
      );
      
      if (!inventoryItem) {
        return false;
      }
    }
    
    return true;
  };
  
  //     
  const getIngredientQuantityInInventory = (ingredientId) => {
    if (!player.inventory.items) {
      return 0;
    }
    
    const inventoryItem = player.inventory.items.find(item => item.itemId === ingredientId);
    return inventoryItem ? inventoryItem.quantity : 0;
  };
  
  //     
  const handleCraft = async () => {
    if (!selectedRecipe) return;
    
    //   
    setCraftingStatus('crafting');
    setCraftingProgress(0);
    
    //   
    const craftingTime = 3000; // 3 
    const interval = 50; // 50    
    const steps = craftingTime / interval;
    let currentStep = 0;
    
    const craftingInterval = setInterval(() => {
      currentStep++;
      setCraftingProgress(Math.min(100, (currentStep / steps) * 100));
      
      if (currentStep >= steps) {
        clearInterval(craftingInterval);
        completeCrafting();
      }
    }, interval);
  };
  
  //   
  const completeCrafting = () => {
    //   
    const roll = Math.random() * 100;
    const isSuccess = roll <= selectedRecipe.successRate;
    
    if (isSuccess) {
      setCraftingStatus('success');
      setCraftingResult({
        name: selectedRecipe.name,
        description: selectedRecipe.description,
        rarity: selectedRecipe.rarity
      });
      
      //         
      //      
    } else {
      setCraftingStatus('failed');
      setCraftingResult(null);
      
      //          
    }
  };
  
  //   
  const renderRecipeIcon = (type) => {
    switch (type) {
      case 'pill': return '';
      case 'talisman': return '';
      case 'weapon': return '';
      case 'armor': return '';
      case 'accessory': return '';
      default: return '?';
    }
  };
  
  //     
  const renderCraftingResult = () => {
    if (!craftingStatus || craftingStatus === 'crafting') {
      return null;
    }
    
    if (craftingStatus === 'success') {
      return (
        <ResultMessage success>
          <h3> !</h3>
          <p>   {craftingResult.name}</p>
        </ResultMessage>
      );
    } else {
      return (
        <ResultMessage success={false}>
          <h3>  !</h3>
          <p>    {selectedRecipe.name}.   .</p>
        </ResultMessage>
      );
    }
  };
  
  return (
    <TabContainer>
      <TabHeader>
        <TabTitle>   </TabTitle>
      </TabHeader>
      
      <TabMenu>
        <TabButton 
          active={activeTab === 'pills'} 
          onClick={() => setActiveTab('pills')}
        >
          
        </TabButton>
        <TabButton 
          active={activeTab === 'talismans'} 
          onClick={() => setActiveTab('talismans')}
        >
          
        </TabButton>
        <TabButton 
          active={activeTab === 'weapons'} 
          onClick={() => setActiveTab('weapons')}
        >
          
        </TabButton>
        <TabButton 
          active={activeTab === 'armor'} 
          onClick={() => setActiveTab('armor')}
        >
          
        </TabButton>
        <TabButton 
          active={activeTab === 'accessories'} 
          onClick={() => setActiveTab('accessories')}
        >
          
        </TabButton>
      </TabMenu>
      
      <TabContent>
        <LeftPanel>
          <SearchBar 
            placeholder=" ..." 
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
          
          <FilterContainer>
            <FilterSelect 
              value={rarityFilter}
              onChange={(e) => setRarityFilter(e.target.value)}
            >
              <option value="all"> </option>
              <option value="common"></option>
              <option value="uncommon"></option>
              <option value="rare"></option>
              <option value="epic"></option>
              <option value="legendary"></option>
            </FilterSelect>
          </FilterContainer>
          
          <RecipeList>
            {filteredRecipes.length > 0 ? (
              filteredRecipes.map((recipe) => (
                <RecipeCard 
                  key={recipe.id} 
                  selected={selectedRecipe && selectedRecipe.id === recipe.id}
                  onClick={() => setSelectedRecipe(recipe)}
                >
                  <RecipeIcon>{renderRecipeIcon(recipe.type)}</RecipeIcon>
                  <RecipeInfo>
                    <RecipeName rarity={recipe.rarity}>{recipe.name}</RecipeName>
                    <RecipeDescription>{recipe.description}</RecipeDescription>
                  </RecipeInfo>
                </RecipeCard>
              ))
            ) : (
              <NoRecipesMessage>     </NoRecipesMessage>
            )}
          </RecipeList>
        </LeftPanel>
        
        <RightPanel>
          {selectedRecipe ? (
            <>
              <DetailTitle>{selectedRecipe.name}</DetailTitle>
              <RecipeDescription>{selectedRecipe.description}</RecipeDescription>
              
              <RecipeDetails>
                <DetailRow>
                  <DetailLabel>:</DetailLabel>
                  <DetailValue>
                    {(() => {
                      switch(selectedRecipe.type) {
                        case 'pill': return '';
                        case 'talisman': return '';
                        case 'weapon': return '';
                        case 'armor': return '';
                        case 'accessory': return '';
                        default: return selectedRecipe.type;
                      }
                    })()}
                  </DetailValue>
                </DetailRow>
                
                <DetailRow>
                  <DetailLabel>:</DetailLabel>
                  <DetailValue style={{
                    color: (() => {
                      switch(selectedRecipe.rarity) {
                        case 'common': return '#aaa';
                        case 'uncommon': return '#1eff00';
                        case 'rare': return '#0070dd';
                        case 'epic': return '#a335ee';
                        case 'legendary': return '#ff8000';
                        default: return '#aaa';
                      }
                    })()
                  }}>
                    {(() => {
                      switch(selectedRecipe.rarity) {
                        case 'common': return '';
                        case 'uncommon': return '';
                        case 'rare': return '';
                        case 'epic': return '';
                        case 'legendary': return '';
                        default: return selectedRecipe.rarity;
                      }
                    })()}
                  </DetailValue>
                </DetailRow>
                
                <DetailRow>
                  <DetailLabel> :</DetailLabel>
                  <DetailValue>{selectedRecipe.requiredStage}</DetailValue>
                </DetailRow>
                
                <DetailRow>
                  <DetailLabel> :</DetailLabel>
                  <DetailValue>{selectedRecipe.requiredLevel}</DetailValue>
                </DetailRow>
                
                <DetailRow>
                  <DetailLabel> :</DetailLabel>
                  <DetailValue>{selectedRecipe.successRate}%</DetailValue>
                </DetailRow>
              </RecipeDetails>
              
              <IngredientsList>
                <DetailTitle>:</DetailTitle>
                {selectedRecipe.ingredients.map((ingredient) => {
                  const inventoryQuantity = getIngredientQuantityInInventory(ingredient.id);
                  const hasEnough = inventoryQuantity >= ingredient.quantity;
                  
                  return (
                    <IngredientItem 
                      key={ingredient.id} 
                      available={hasEnough}
                    >
                      <IngredientName available={hasEnough}>{ingredient.name}</IngredientName>
                      <IngredientQuantity available={hasEnough}>
                        {inventoryQuantity}/{ingredient.quantity}
                      </IngredientQuantity>
                    </IngredientItem>
                  );
                })}
              </IngredientsList>
              
              {craftingStatus === 'crafting' && (
                <CraftingProgress>
                  <ProgressBar progress={craftingProgress} />
                </CraftingProgress>
              )}
              
              {renderCraftingResult()}
              
              <ActionButton 
                primary 
                onClick={handleCraft}
                disabled={!checkIngredients(selectedRecipe) || craftingStatus === 'crafting'}
              >
                {craftingStatus === 'crafting' ? '...' : ''}
              </ActionButton>
            </>
          ) : (
            <NoRecipesMessage>    </NoRecipesMessage>
          )}
        </RightPanel>
      </TabContent>
    </TabContainer>
  );
};

export default AlchemyTab;
