/**
 *      
 *       equipment-items.js
 */

import { getEquipmentItemById } from '../data/equipment-items';

/**
 *      
 * @param {Object} item -  
 * @returns {Object} -     
 */
function ensureItemHasCalculatedBonuses(item) {
  if (!item) return item;
  
  //       ,    
  if (item.calculatedBonuses) {
    return item;
  }
  
  //   itemId  (    )
  const itemId = item.itemId || item.id;
  
  if (!itemId) {
    console.warn('   :', item);
    return item;
  }
  
  try {
    //     equipment-items.js
    const baseItem = getEquipmentItemById(parseInt(itemId));
    
    if (baseItem && baseItem.calculatedBonuses) {
      //      
      return {
        ...item,
        calculatedBonuses: baseItem.calculatedBonuses
      };
    }
  } catch (error) {
    console.error(`       ID=${itemId}:`, error);
  }
  
  return item;
}

/**
 *        
 * @param {Object} equippedItems -    
 * @returns {Object} -    ,      
 */
function ensureEquipmentHasCalculatedBonuses(equippedItems) {
  if (!equippedItems) return {};
  
  //    
  const result = { ...equippedItems };
  
  //       
  Object.keys(result).forEach(slot => {
    if (result[slot]) {
      result[slot] = ensureItemHasCalculatedBonuses(result[slot]);
    }
  });
  
  return result;
}

/**
 *        
 * @param {Array} items -  
 * @returns {Array} -      
 */
function ensureItemsHaveCalculatedBonuses(items) {
  if (!items || !Array.isArray(items)) return items;
  
  return items.map(item => ensureItemHasCalculatedBonuses(item));
}

export {
  ensureItemHasCalculatedBonuses,
  ensureEquipmentHasCalculatedBonuses,
  ensureItemsHaveCalculatedBonuses
};
