import React, { useState, useEffect, useContext } from 'react';
import styled from 'styled-components';
import TimeDebugPanel from './TimeDebugPanel';
import { GameContext } from '../../context/GameContext';
import '../../utils/directConsoleCommands'; //   

//      
const DebugButtonContainer = styled.div`
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 9999;
`;

//     
const DebugButton = styled.button`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #333;
  color: #ddd;
  border: 1px solid #555;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0,0,0,0.3);
  transition: all 0.2s;
  opacity: 0.7;
  
  &:hover {
    opacity: 1;
    transform: scale(1.05);
  }
`;

/**
 *        
 * @param {Object} props -  
 * @param {boolean} props.enabled -   
 */
function DebugTools({ enabled = false }) {
  //      
  const [showTools, setShowTools] = useState(false);
  //      
  const [keyActivated, setKeyActivated] = useState(false);
  
  //     
  const gameContext = useContext(GameContext);
  
  //    
  const toggleTools = () => {
    setShowTools(prev => !prev);
    
    //      
    if (!showTools) {
      console.info('   :');
      console.info(' getGameTime() -     ');
      console.info(' showTimeHelp() -      ');
      console.info(' add1000Currency() -  1000   ');
      console.info(' 1000() -     ');
    }
    
    //        
    if (typeof window !== 'undefined' && gameContext) {
      window.__GAME_CONTEXT__ = gameContext;
      window.__GAME_STATE__ = gameContext.state;
      window.__GAME_DISPATCH__ = gameContext.actions.dispatch;
    }
  };
  
  //    1000   
  const addCurrency = () => {
    if (!gameContext || !gameContext.actions) {
      console.error('      ');
      return;
    }
    
    try {
      //    
      const currentCurrency = gameContext.state.player.inventory.currency || {};
      
      //  1000   
      const updatedCurrency = {
        copper: (currentCurrency.copper || 0) + 1000,
        silver: (currentCurrency.silver || 0) + 1000,
        gold: (currentCurrency.gold || 0) + 1000,
        spiritStones: (currentCurrency.spiritStones || 0) + 1000
      };
      
      //     
      gameContext.actions.updateCurrency(updatedCurrency);
      
      console.log('  1000   !', updatedCurrency);
    } catch (error) {
      console.error('   :', error);
    }
  };
  
  //      
  useEffect(() => {
    if (!enabled) return;
    
    const handleKeyDown = (e) => {
      //  Ctrl+Shift+D   
      if (e.ctrlKey && e.shiftKey && e.key === 'D') {
        e.preventDefault();
        setKeyActivated(true);
        toggleTools();
      }
    };
    
    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [enabled, toggleTools]);
  
  //    ,   
  if (!enabled && !keyActivated) return null;
  
  return (
    <>
      {/*     */}
      <DebugButtonContainer>
        <DebugButton onClick={toggleTools} title="  (Ctrl+Shift+D)">
          
        </DebugButton>
      </DebugButtonContainer>
      
      {/*   (   ) */}
      {showTools && (
        <div style={{ position: 'fixed', top: '10px', right: '10px', zIndex: 9998, maxWidth: '350px' }}>
          <TimeDebugPanel onClose={() => setShowTools(false)} />
          
          {/*     */}
          <div style={{ 
            marginTop: '10px', 
            padding: '10px', 
            backgroundColor: '#333', 
            borderRadius: '5px',
            boxShadow: '0 2px 5px rgba(0,0,0,0.3)',
            color: '#fff' 
          }}>
            <h3 style={{ margin: '0 0 8px 0', fontSize: '14px' }}></h3>
            <button 
              onClick={addCurrency}
              style={{
                padding: '5px 10px',
                backgroundColor: '#d4af37',
                color: '#000',
                border: 'none',
                borderRadius: '3px',
                cursor: 'pointer',
                fontWeight: 'bold',
                fontSize: '12px'
              }}
            >
               1000  
            </button>
          </div>
        </div>
      )}
    </>
  );
}

export default DebugTools;
