const InventoryItem = require('../models/inventory-item');
const User = require('../models/user');

// ,     
const isBrowser = typeof window !== 'undefined';

//        
let browserInventoryData = {};

/**
 *     
 */
class InventoryService {
  /**
   *     
   * @param {number} userId - ID 
   * @returns {Promise<Array>} -   
   */
  static async getInventoryItems(userId) {
    try {
      if (isBrowser) {
        //      
        if (!browserInventoryData[userId]) {
          browserInventoryData[userId] = [];
        }
        
        //    
        return browserInventoryData[userId].map(item => ({
          id: item.itemId,
          name: item.name,
          type: item.type,
          rarity: item.rarity,
          quantity: item.quantity,
          equipped: item.equipped,
          ...item.properties
        }));
      } else {
        //     
        //     
        const items = await InventoryItem.findAll({
          where: { userId }
        });
        
        //    
        return items.map(item => ({
          id: item.itemId,
          name: item.name,
          type: item.type,
          rarity: item.rarity,
          quantity: item.quantity,
          equipped: item.equipped,
          ...item.properties
        }));
      }
    } catch (error) {
      console.error('    :', error);
      throw error;
    }
  }
  
  /**
   *    
   * @param {number} userId - ID 
   * @param {Object} item -   
   * @returns {Promise<Object>} -  
   */
  static async addInventoryItem(userId, item) {
    try {
      if (isBrowser) {
        //      
        if (!browserInventoryData[userId]) {
          browserInventoryData[userId] = [];
        }
        
        // ,       
        const existingItemIndex = browserInventoryData[userId].findIndex(
          existingItem => existingItem.itemId === item.id
        );
        
        if (existingItemIndex >= 0 && item.stackable !== false) {
          //       ,  
          browserInventoryData[userId][existingItemIndex].quantity += item.quantity || 1;
          
          const existingItem = browserInventoryData[userId][existingItemIndex];
          
          return {
            id: existingItem.itemId,
            name: existingItem.name,
            type: existingItem.type,
            rarity: existingItem.rarity,
            quantity: existingItem.quantity,
            equipped: existingItem.equipped,
            ...existingItem.properties
          };
        } else {
          //       ,  
          const newItem = {
            userId,
            itemId: item.id,
            name: item.name,
            type: item.type || 'misc',
            rarity: item.rarity || 'common',
            quantity: item.quantity || 1,
            equipped: item.equipped || false,
            properties: {
              description: item.description,
              effects: item.effects,
              requirements: item.requirements,
              value: item.value,
              ...item
            }
          };
          
          browserInventoryData[userId].push(newItem);
          
          return {
            id: newItem.itemId,
            name: newItem.name,
            type: newItem.type,
            rarity: newItem.rarity,
            quantity: newItem.quantity,
            equipped: newItem.equipped,
            ...newItem.properties
          };
        }
      } else {
        //     
        // ,       
        const existingItem = await InventoryItem.findOne({
          where: { 
            userId,
            itemId: item.id
          }
        });
        
        if (existingItem && item.stackable !== false) {
          //       ,  
          existingItem.quantity += item.quantity || 1;
          await existingItem.save();
          
          return {
            id: existingItem.itemId,
            name: existingItem.name,
            type: existingItem.type,
            rarity: existingItem.rarity,
            quantity: existingItem.quantity,
            equipped: existingItem.equipped,
            ...existingItem.properties
          };
        } else {
          //       ,  
          const newItem = await InventoryItem.create({
            userId,
            itemId: item.id,
            name: item.name,
            type: item.type || 'misc',
            rarity: item.rarity || 'common',
            quantity: item.quantity || 1,
            equipped: item.equipped || false,
            properties: {
              description: item.description,
              effects: item.effects,
              requirements: item.requirements,
              value: item.value,
              ...item
            }
          });
          
          return {
            id: newItem.itemId,
            name: newItem.name,
            type: newItem.type,
            rarity: newItem.rarity,
            quantity: newItem.quantity,
            equipped: newItem.equipped,
            ...newItem.properties
          };
        }
      }
    } catch (error) {
      console.error('     :', error);
      throw error;
    }
  }
  
  /**
   *    
   * @param {number} userId - ID 
   * @param {string} itemId - ID 
   * @param {number} quantity -     (  1)
   * @returns {Promise<boolean>} -  
   */
  static async removeInventoryItem(userId, itemId, quantity = 1) {
    try {
      if (isBrowser) {
        //      
        if (!browserInventoryData[userId]) {
          browserInventoryData[userId] = [];
        }
        
        //    
        const itemIndex = browserInventoryData[userId].findIndex(
          item => item.itemId === itemId
        );
        
        if (itemIndex === -1) {
          throw new Error('    ');
        }
        
        const item = browserInventoryData[userId][itemIndex];
        
        if (item.quantity > quantity) {
          //   ,   ,  
          item.quantity -= quantity;
        } else {
          //     ,  
          browserInventoryData[userId].splice(itemIndex, 1);
        }
        
        return true;
      } else {
        //     
        //    
        const item = await InventoryItem.findOne({
          where: { 
            userId,
            itemId
          }
        });
        
        if (!item) {
          throw new Error('    ');
        }
        
        if (item.quantity > quantity) {
          //   ,   ,  
          item.quantity -= quantity;
          await item.save();
        } else {
          //     ,  
          await item.destroy();
        }
        
        return true;
      }
    } catch (error) {
      console.error('     :', error);
      throw error;
    }
  }
  
  /**
   * / 
   * @param {number} userId - ID 
   * @param {string} itemId - ID 
   * @param {boolean} equipped -  
   * @returns {Promise<Object>} -  
   */
  static async toggleEquipItem(userId, itemId, equipped) {
    try {
      if (isBrowser) {
        //      
        if (!browserInventoryData[userId]) {
          browserInventoryData[userId] = [];
        }
        
        //    
        const itemIndex = browserInventoryData[userId].findIndex(
          item => item.itemId === itemId
        );
        
        if (itemIndex === -1) {
          throw new Error('    ');
        }
        
        //   
        browserInventoryData[userId][itemIndex].equipped = equipped;
        
        const item = browserInventoryData[userId][itemIndex];
        
        return {
          id: item.itemId,
          name: item.name,
          type: item.type,
          rarity: item.rarity,
          quantity: item.quantity,
          equipped: item.equipped,
          ...item.properties
        };
      } else {
        //     
        //    
        const item = await InventoryItem.findOne({
          where: { 
            userId,
            itemId
          }
        });
        
        if (!item) {
          throw new Error('    ');
        }
        
        //   
        item.equipped = equipped;
        await item.save();
        
        return {
          id: item.itemId,
          name: item.name,
          type: item.type,
          rarity: item.rarity,
          quantity: item.quantity,
          equipped: item.equipped,
          ...item.properties
        };
      }
    } catch (error) {
      console.error('  / :', error);
      throw error;
    }
  }
}

module.exports = InventoryService;
