/**
 *  Sequelize     
 *
 * :
 * 1.   Sequelize, DataTypes  Model   
 * 2.    (  )
 * 3.       (  )
 */

/**
 *         
 */
const isServerEnvironment = (
  typeof window === 'undefined' &&
  typeof process !== 'undefined' &&
  process.versions &&
  process.versions.node
);
export { isServerEnvironment };

// ------------------------------------------------------------------------
// :       
//         
// ------------------------------------------------------------------------

//       
const BrowserSequelize = function() {
  console.error(
    ':   Sequelize   .\n' +
    ' client-api.js      .\n' +
    '       .'
  );
  return {};
};

const BrowserDataTypes = {
  STRING: 'STRING',
  TEXT: 'TEXT',
  INTEGER: 'INTEGER',
  FLOAT: 'FLOAT',
  BOOLEAN: 'BOOLEAN',
  DATE: 'DATE',
  JSON: 'JSON',
  JSONB: 'JSONB',
  //     
  UUID: 'UUID',
  ENUM: (...args) => 'ENUM',
  ARRAY: (type) => `ARRAY(${type})`,
  DECIMAL: 'DECIMAL',
  VIRTUAL: 'VIRTUAL',
  //  
  ABSTRACT: function() { return 'ABSTRACT'; }
};

class BrowserModel {
  static init() {
    console.error(
      ':   Model.init  .\n' +
      ' client-api.js      .\n'
    );
    return this;
  }
  
  static findOne() {
    console.error(':        (findOne)');
    return Promise.reject(new Error('  '));
  }
  
  static findAll() {
    console.error(':        (findAll)');
    return Promise.reject(new Error('  '));
  }
  
  static findByPk() {
    console.error(':        (findByPk)');
    return Promise.reject(new Error('  '));
  }
  
  static create() {
    console.error(':        (create)');
    return Promise.reject(new Error('  '));
  }
  
  static update() {
    console.error(':        (update)');
    return Promise.reject(new Error('  '));
  }
  
  static destroy() {
    console.error(':        (destroy)');
    return Promise.reject(new Error('  '));
  }
}

//     
let Sequelize = BrowserSequelize;
let DataTypes = BrowserDataTypes;
let Model = BrowserModel;

//       
//       
if (isServerEnvironment) {
  //   try-catch,     
  try {
    //     sequelize
    //       ,   webpack
    //       
    const dynamicImport = Function('return import("sequelize")')();

    //   
    dynamicImport
      .then((sequelizePackage) => {
        Sequelize = sequelizePackage.Sequelize;
        DataTypes = sequelizePackage.DataTypes;
        Model = sequelizePackage.Model;
        console.log('Sequelize     ');
      })
      .catch((error) => {
        console.error('    Sequelize:', error);
        //      
      });
  } catch (error) {
    console.error('    Sequelize:', error);
    //     
  }
}

export { Sequelize, DataTypes, Model };
