import { useEffect, useRef, useState, useCallback } from 'react';
import { useGame } from '../../context/GameContext';
import { updateGameTime, INITIALIZE_WEATHER } from '../../context/actions/weather-actions';
import { connectDebugTools } from '../../utils/debugTools';
import WeatherService from '../../services/weather-service';
import { restockMerchantItems } from '../../data/merchants';
import ACTION_TYPES from '../../context/actions/actionTypes';

//      ()
const lastManualUpdateStamp = { time: 0 };

//     
const ENABLE_LOGS = false;

/**
 *     
 *     15 ,    
 *  middleware
 * @param {Object} props -  
 * @param {boolean} props.debug -     
 */
function GameTimeUpdater({ debug = false }) {
  const { actions } = useGame();
  //  useRef         
  const intervalRef = useRef(null);
  //  useRef     dispatch
  const dispatchRef = useRef(actions.dispatch);
  
  //    dispatch  
  useEffect(() => {
    dispatchRef.current = actions.dispatch;
  }, [actions.dispatch]);

  //  useRef  useState     
  //    
  const lastUpdateRef = useRef(new Date());
  //      (15   60 )
  const updateInterval = 15000;

  //   ,     
  const canPerformAutoUpdate = useCallback(() => {
    const timeSinceLastManual = Date.now() - lastManualUpdateStamp.time;
    if (timeSinceLastManual < 2000) {
      return false;
    }
    return true;
  }, []);

  //      useCallback,      
  const updateTime = useCallback(() => {
    // ,     
    if (!canPerformAutoUpdate()) {
      return;
    }
    
    const now = new Date();
    //   ,     ( )
    const elapsedMinutes = (now - lastUpdateRef.current) / 60000;
    
    //        
    //   false - ,    
    dispatchRef.current(updateGameTime(elapsedMinutes, false));
    
    //      ref (  )
    lastUpdateRef.current = now;
    
    //    
  }, [canPerformAutoUpdate]); //  ,   lastUpdateRef -  ref

  //            
  useEffect(() => {
    //         
    const initializeGameState = () => {
      //    
      dispatchRef.current({ type: 'SYNC_SEASON_DAY' });
      console.log(' GameTimeUpdater:       ');
      
      //         
      dispatchRef.current({ type: INITIALIZE_WEATHER });
      console.log(' GameTimeUpdater:      ');
      
      //      DOM-
      window.dispatchEvent(new CustomEvent('weather_changed', { 
        detail: { auto: true, initialization: true, withForecast: true } 
      }));
    };
    
    //     ,  ,    
    const timer = setTimeout(() => {
      initializeGameState();
    }, 500);
    
    //    
    return () => clearTimeout(timer);
  }, []);

  //      
  useEffect(() => {
    //      
    updateTime();
    
    //    
    intervalRef.current = setInterval(updateTime, updateInterval);
    
    //     
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
    };
  }, [updateTime]); //  updateTime  

  //            15 
  useEffect(() => {
    const syncAndCheckInterval = setInterval(() => {
      if (canPerformAutoUpdate()) {
        //   
        dispatchRef.current({ type: 'SYNC_SEASON_DAY' });
        
        // ,       
        if (actions.state?.weather?.nextWeatherChange === 0) {
          console.log(' GameTimeUpdater:   nextWeatherChange,   ');
          dispatchRef.current({ type: 'FORCE_WEATHER_CHANGE' });
          
          //       DOM-
          window.dispatchEvent(new CustomEvent('weather_changed', { detail: { auto: true } }));
        }
        
        if (ENABLE_LOGS) {
          console.log(' GameTimeUpdater:        ');
        }
      }
    }, 15000); //  15 
    
    return () => clearInterval(syncAndCheckInterval);
  }, [canPerformAutoUpdate, actions.state]);
  
  //          
  useEffect(() => {
    //      
    const currentGameTime = actions.state?.world?.time?.gameTime;
    
    //    ,   
    if (currentGameTime === undefined) return;
    
    //     
    const restockItems = () => {
      if (canPerformAutoUpdate()) {
        //    
        const restockOccurred = restockMerchantItems(currentGameTime);
        
        //    ,   
        if (restockOccurred) {
          if (ENABLE_LOGS) {
            console.log(' GameTimeUpdater:    ');
          }
          
          //   
          dispatchRef.current({ 
            type: ACTION_TYPES.UPDATE_MARKET_STATE, 
            payload: { 
              //     ,    
              merchantsUpdated: true,
              timestamp: Date.now()
            } 
          });
        }
      }
    };
    
    //    
    restockItems();
    
    //       (  5   )
    const merchantRestockInterval = setInterval(restockItems, 5 * 60 * 1000);
    
    //     
    return () => clearInterval(merchantRestockInterval);
  }, [actions.state?.world?.time?.gameTime, canPerformAutoUpdate]);

  //      
  useEffect(() => {
    //     
    const handleManualTimeUpdate = (event) => {
      // ,       
      if (event.detail?.type === 'manual_time_update') {
        lastManualUpdateStamp.time = Date.now();
      }
    };
    
    //        
    window.addEventListener('manual_time_update', handleManualTimeUpdate);
    
    //    
    return () => {
      window.removeEventListener('manual_time_update', handleManualTimeUpdate);
    };
  }, [debug]);

  //       (   )
  useEffect(() => {
    const handleVisibilityChange = () => {
      if (document.visibilityState === 'visible') {
        const now = new Date();
        const elapsedMinutes = (now - lastUpdateRef.current) / 60000;
        
        //    0.25  (15 ),   
        if (elapsedMinutes > 0.25 && canPerformAutoUpdate()) {
          updateTime();
        }
      }
    };
    
    //     
    document.addEventListener('visibilitychange', handleVisibilityChange);
    
    //    
    return () => {
      document.removeEventListener('visibilitychange', handleVisibilityChange);
    };
  }, [updateTime, canPerformAutoUpdate]);

  //    ,   
  useEffect(() => {
    if (debug) {
      //       ,  
      connectDebugTools({ context: { state: actions.state } });
    }
  }, [debug, actions]);
  
  //       
  useEffect(() => {
    if (debug && actions.state) {
      connectDebugTools({ context: { state: actions.state } });
    }
  }, [actions.state, debug]);

  //     UI
  return null;
}

export default GameTimeUpdater;
