import React, { useState, useEffect } from 'react';
import { useGame } from '../../context/GameContext';
import { 
  petTypeNames, 
  petTypeDescriptions, 
  petTypeBaseStats,
  petTypeAbilities,
  evolutionStages,
  calculateExpForLevel,
  calculateStatBonus,
  calculateCombatBonus
} from '../../data/spirit-pets';
import './SpiritPetsTab.css';

//   
const PetCard = ({ pet, isActive, onActivate, onFeed, onTrain, onViewDetails }) => {
  const getPetTypeColor = (type) => {
    const typeColors = {
      'fire': '#e74c3c',
      'water': '#3498db',
      'earth': '#8e44ad',
      'wind': '#2ecc71',
      'lightning': '#f1c40f',
      'wood': '#27ae60',
      'metal': '#95a5a6',
      'darkness': '#34495e',
      'light': '#f5f5f5'
    };
    return typeColors[type] || '#bdc3c7';
  };

  //      
  const expForCurrentLevel = calculateExpForLevel(pet.level);
  const expPercentage = (pet.experience / expForCurrentLevel) * 100;
  
  //     
  const combatBonus = calculateCombatBonus(pet);

  return (
    <div className={`pet-card ${isActive ? 'pet-card-active' : ''}`} style={{ borderColor: getPetTypeColor(pet.type) }}>
      <div className="pet-card-header" style={{ backgroundColor: getPetTypeColor(pet.type) }}>
        <h3 className="pet-name">{pet.name}</h3>
        <div className="pet-type">{petTypeNames[pet.type]}</div>
        {isActive && <div className="pet-active-badge"></div>}
      </div>
      
      <div className="pet-card-body">
        <div className="pet-info-row">
          <div className="pet-level"> {pet.level}</div>
          <div className="pet-evolution">: {evolutionStages[pet.evolutionStage].name}</div>
        </div>
        
        <div className="pet-exp-bar-container">
          <div className="pet-exp-bar" style={{ width: `${expPercentage}%` }}></div>
          <div className="pet-exp-text">{pet.experience} / {expForCurrentLevel} </div>
        </div>
        
        <div className="pet-stats-container">
          <div className="pet-stat">
            <span className="stat-name">:</span>
            <span className="stat-value">{pet.strength}</span>
          </div>
          <div className="pet-stat">
            <span className="stat-name">:</span>
            <span className="stat-value">{pet.intelligence}</span>
          </div>
          <div className="pet-stat">
            <span className="stat-name">:</span>
            <span className="stat-value">{pet.agility}</span>
          </div>
          <div className="pet-stat">
            <span className="stat-name">:</span>
            <span className="stat-value">{pet.vitality}</span>
          </div>
          <div className="pet-stat">
            <span className="stat-name">:</span>
            <span className="stat-value">{pet.spirit}</span>
          </div>
        </div>
        
        <div className="pet-indicators">
          <div className="pet-indicator">
            <span className="indicator-name">:</span>
            <div className="indicator-bar-container">
              <div className="indicator-bar" style={{ width: `${pet.hunger}%`, backgroundColor: pet.hunger < 30 ? '#e74c3c' : '#2ecc71' }}></div>
            </div>
            <span className="indicator-value">{pet.hunger}%</span>
          </div>
          <div className="pet-indicator">
            <span className="indicator-name">:</span>
            <div className="indicator-bar-container">
              <div className="indicator-bar" style={{ width: `${pet.loyalty}%`, backgroundColor: pet.loyalty < 50 ? '#e74c3c' : '#2ecc71' }}></div>
            </div>
            <span className="indicator-value">{pet.loyalty}%</span>
            {pet.loyalty <= 50 && (
              <div className="loyalty-warning" style={{ 
                color: pet.loyalty <= 25 ? '#e74c3c' : '#e67e22', 
                fontSize: '0.8rem', 
                fontWeight: 'bold',
                marginTop: '5px',
                textAlign: 'center',
                padding: '3px',
                border: `1px solid ${pet.loyalty <= 25 ? '#e74c3c' : '#e67e22'}`,
                borderRadius: '4px',
                background: pet.loyalty <= 25 ? 'rgba(231, 76, 60, 0.1)' : 'rgba(230, 126, 34, 0.1)'
              }}>
                {pet.loyalty <= 25 ? '     !' : '       !'}
              </div>
            )}
          </div>
        </div>
        
        <div className="pet-card-buttons">
          {!isActive && (
            <button className="pet-action-button activate-button" onClick={() => onActivate(pet.id)}>
               
            </button>
          )}
          <button className="pet-action-button feed-button" onClick={() => onFeed(pet.id)}>
            
          </button>
          <button className="pet-action-button train-button" onClick={() => onTrain(pet.id)}>
            
          </button>
          <button className="pet-action-button details-button" onClick={() => onViewDetails(pet)}>
            
          </button>
        </div>
      </div>
    </div>
  );
};

//         
const PetFeedingModal = ({ pet, onClose, onSelect, availableFood }) => {
  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <div className="modal-header">
          <h3>  {pet.name}</h3>
          <button className="modal-close" onClick={onClose}></button>
        </div>
        <div className="modal-body">
          <p>   :</p>
          
          {availableFood.length > 0 ? (
            <div className="food-items-list">
              {availableFood.map(food => (
                <div 
                  key={food.id} 
                  className="food-item"
                  onClick={() => onSelect(food.id)}
                  style={{
                    padding: '10px',
                    margin: '5px 0',
                    border: '1px solid #444',
                    borderRadius: '5px',
                    display: 'flex',
                    flexDirection: 'column',
                    cursor: 'pointer',
                    transition: 'background 0.2s',
                    background: 'rgba(30, 30, 30, 0.7)'
                  }}
                  onMouseOver={(e) => {
                    e.currentTarget.style.background = 'rgba(80, 80, 80, 0.7)';
                  }}
                  onMouseOut={(e) => {
                    e.currentTarget.style.background = 'rgba(30, 30, 30, 0.7)';
                  }}
                >
                  <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                    <span style={{ fontWeight: 'bold', color: (() => {
                      switch(food.rarity) {
                        case 'common': return '#aaa';
                        case 'uncommon': return '#1eff00';
                        case 'rare': return '#0070dd';
                        case 'epic': return '#a335ee';
                        case 'legendary': return '#ff8000';
                        default: return '#aaa';
                      }
                    })() }}>{food.name}</span>
                    <span>x{food.quantity}</span>
                  </div>
                  <div style={{ fontSize: '0.9em', color: '#aaa', marginTop: '3px' }}>{food.description}</div>
                  <div style={{ display: 'flex', marginTop: '5px' }}>
                  <div style={{ 
                      flex: 1, 
                      color: '#2ecc71', 
                      fontSize: '0.9em' 
                    }}>
                      : +{food.nutritionValue}%
                    </div>
                    <div style={{ 
                      flex: 1, 
                      color: '#e67e22', 
                      fontSize: '0.9em' 
                    }}>
                      : +{food.loyaltyBonus}%
                    </div>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <p className="no-food-message" style={{ color: '#e74c3c' }}>
                   .         .
            </p>
          )}
        </div>
      </div>
    </div>
  );
};

//      
const PetTrainingModal = ({ pet, onClose, onTrain }) => {
  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <div className="modal-header">
          <h3>  {pet.name}</h3>
          <button className="modal-close" onClick={onClose}></button>
        </div>
        <div className="modal-body">
          <p>   :</p>
          <p className="training-info">     1,     15%.</p>
          
          <div className="training-buttons">
            <button 
              className="training-button" 
              onClick={() => onTrain(pet.id, 'strength')}
              disabled={pet.hunger < 30}
            >
               ({pet.strength})
            </button>
            <button 
              className="training-button" 
              onClick={() => onTrain(pet.id, 'intelligence')}
              disabled={pet.hunger < 30}
            >
               ({pet.intelligence})
            </button>
            <button 
              className="training-button" 
              onClick={() => onTrain(pet.id, 'agility')}
              disabled={pet.hunger < 30}
            >
               ({pet.agility})
            </button>
            <button 
              className="training-button" 
              onClick={() => onTrain(pet.id, 'vitality')}
              disabled={pet.hunger < 30}
            >
               ({pet.vitality})
            </button>
            <button 
              className="training-button" 
              onClick={() => onTrain(pet.id, 'spirit')}
              disabled={pet.hunger < 30}
            >
               ({pet.spirit})
            </button>
          </div>
          
          {pet.hunger < 30 && (
            <p className="training-warning">
                  .   !
            </p>
          )}
        </div>
      </div>
    </div>
  );
};

//      
const PetDetailsModal = ({ pet, onClose }) => {
  //   
  const petAbilities = pet.abilities.map(abilityId => {
    const ability = petTypeAbilities[pet.type]?.find(a => a.id === abilityId);
    return ability || { id: abilityId, name: ' ', description: ' ' };
  });
  
  //     
  const combatBonus = calculateCombatBonus(pet);
  
  //      
  const formatDate = (date) => {
    if (!date) return '';
    const d = new Date(date);
    return `${d.toLocaleDateString()} ${d.toLocaleTimeString()}`;
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content pet-details-modal">
        <div className="modal-header">
          <h3>{pet.name} - {petTypeNames[pet.type]}</h3>
          <button className="modal-close" onClick={onClose}></button>
        </div>
        <div className="modal-body">
          <div className="pet-details-section">
            <h4> </h4>
            <p className="pet-description">{petTypeDescriptions[pet.type]}</p>
            <div className="pet-details-row">
              <div className="pet-detail">
                <span className="detail-label">:</span>
                <span className="detail-value">{pet.level}</span>
              </div>
              <div className="pet-detail">
                <span className="detail-label">:</span>
                <span className="detail-value">{pet.experience} / {calculateExpForLevel(pet.level)}</span>
              </div>
              <div className="pet-detail">
                <span className="detail-label"> :</span>
                <span className="detail-value">{evolutionStages[pet.evolutionStage].name}</span>
              </div>
            </div>
            <div className="pet-evolution-description">
              {evolutionStages[pet.evolutionStage].description}
            </div>
          </div>
          
          <div className="pet-details-section">
            <h4></h4>
            <div className="pet-details-stats">
              <div className="pet-detail-stat">
                <span className="detail-stat-name">:</span>
                <span className="detail-stat-value">{pet.strength}</span>
              </div>
              <div className="pet-detail-stat">
                <span className="detail-stat-name">:</span>
                <span className="detail-stat-value">{pet.intelligence}</span>
              </div>
              <div className="pet-detail-stat">
                <span className="detail-stat-name">:</span>
                <span className="detail-stat-value">{pet.agility}</span>
              </div>
              <div className="pet-detail-stat">
                <span className="detail-stat-name">:</span>
                <span className="detail-stat-value">{pet.vitality}</span>
              </div>
              <div className="pet-detail-stat">
                <span className="detail-stat-name">:</span>
                <span className="detail-stat-value">{pet.spirit}</span>
              </div>
            </div>
          </div>
          
          <div className="pet-details-section">
            <h4> </h4>
            <div className="pet-details-combat">
              <div className="pet-combat-bonus">
                <span className="combat-bonus-name">:</span>
                <span className="combat-bonus-value">+{combatBonus.attack}</span>
              </div>
              <div className="pet-combat-bonus">
                <span className="combat-bonus-name">:</span>
                <span className="combat-bonus-value">+{combatBonus.defense}</span>
              </div>
              <div className="pet-combat-bonus">
                <span className="combat-bonus-name">:</span>
                <span className="combat-bonus-value">+{combatBonus.speed}</span>
              </div>
              <div className="pet-combat-bonus">
                <span className="combat-bonus-name"> .:</span>
                <span className="combat-bonus-value">+{(combatBonus.critChance * 100).toFixed(1)}%</span>
              </div>
              <div className="pet-combat-bonus">
                <span className="combat-bonus-name"> :</span>
                <span className="combat-bonus-value">+{combatBonus.healthBonus}</span>
              </div>
              <div className="pet-combat-bonus">
                <span className="combat-bonus-name"> :</span>
                <span className="combat-bonus-value">+{combatBonus.energyBonus}</span>
              </div>
            </div>
          </div>
          
          <div className="pet-details-section">
            <h4></h4>
            <div className="pet-abilities-list">
              {petAbilities.map(ability => (
                <div key={ability.id} className="pet-ability">
                  <div className="ability-header">
                    <span className="ability-name">{ability.name}</span>
                    <span className="ability-type">{ability.type === 'attack' ? '' : ability.type === 'buff' ? '' : ''}</span>
                  </div>
                  <div className="ability-description">{ability.description}</div>
                  {ability.cooldown && (
                    <div className="ability-cooldown">: {ability.cooldown} </div>
                  )}
                </div>
              ))}
            </div>
            {petAbilities.length === 0 && (
              <p className="no-abilities">    </p>
            )}
          </div>
          
          <div className="pet-details-section">
            <h4>  </h4>
            <div className="pet-details-care">
              <div className="pet-care-info">
                <span className="care-info-name">:</span>
                <div className="care-info-bar-container">
                  <div 
                    className="care-info-bar" 
                    style={{ 
                      width: `${pet.hunger}%`, 
                      backgroundColor: pet.hunger < 30 ? '#e74c3c' : '#2ecc71' 
                    }}
                  ></div>
                </div>
                <span className="care-info-value">{pet.hunger}%</span>
              </div>
              <div className="pet-care-info">
                <span className="care-info-name">:</span>
                <div className="care-info-bar-container">
                  <div 
                    className="care-info-bar" 
                    style={{ 
                      width: `${pet.loyalty}%`, 
                      backgroundColor: pet.loyalty < 50 ? '#e74c3c' : '#2ecc71' 
                    }}
                  ></div>
                </div>
                <span className="care-info-value">{pet.loyalty}%</span>
                {pet.loyalty <= 25 && (
                  <div style={{
                    color: '#e74c3c',
                    fontSize: '0.85rem',
                    fontWeight: 'bold',
                    marginTop: '5px',
                    textAlign: 'center',
                    padding: '5px',
                    border: '1px solid #e74c3c',
                    borderRadius: '4px',
                    background: 'rgba(231, 76, 60, 0.1)'
                  }}>
                     !         !
                  </div>
                )}
              </div>
              <div className="pet-loyalty-mechanics" style={{
                marginTop: '10px',
                padding: '10px',
                backgroundColor: 'rgba(50, 50, 50, 0.5)',
                borderRadius: '5px',
                fontSize: '0.9em'
              }}>
                <h5 style={{ margin: '0 0 5px 0', fontSize: '1em' }}> :</h5>
                <ul style={{ margin: '0', paddingLeft: '20px' }}>
                  <li>     ,   </li>
                  <li> : <span style={{ fontWeight: 'bold' }}>5  (100 / )</span></li>
                  <li>  ,    </li>
                  <li>    : <span style={{ fontWeight: 'bold', color: '#e74c3c' }}>{Math.round(5 * (100 / Math.max(pet.hunger, 1)))}%</span></li>
                  <li>   25%,     </li>
                </ul>
              </div>
              
              <div className="pet-care-timestamps" style={{ marginTop: '10px' }}>
                <div className="pet-timestamp">
                  <span className="timestamp-label"> :</span>
                  <span className="timestamp-value">{formatDate(pet.lastFed)}</span>
                </div>
                <div className="pet-timestamp">
                  <span className="timestamp-label"> :</span>
                  <span className="timestamp-value">{formatDate(pet.lastTrained)}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

//    - (      )
const createDemoPet = (userId) => {
  return {
    id: `pet_${Date.now()}`,
    userId: userId || 'demo_user',
    name: '',
    type: 'wind',
    level: 1,
    experience: 0,
    loyalty: 80,
    hunger: 70,
    strength: 7,
    intelligence: 5,
    agility: 6,
    vitality: 4,
    spirit: 6,
    abilities: ['wind_breath'],
    evolutionStage: 1,
    isActive: true,
    lastFed: new Date(),
    lastTrained: new Date(),
    createdAt: new Date(),
    updatedAt: new Date()
  };
};

//  
const SpiritPetsTab = () => {
  const { state, actions } = useGame();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  
  //    
  const [selectedPet, setSelectedPet] = useState(null);
  const [showTrainingModal, setShowTrainingModal] = useState(false);
  const [showDetailsModal, setShowDetailsModal] = useState(false);
  const [showFeedingModal, setShowFeedingModal] = useState(false);
  
  //       
  const spiritPets = state.player?.spiritPets || { pets: [], activePetId: null };
  const pets = Array.isArray(spiritPets.pets) ? spiritPets.pets : [];
  
  //   
  const activePet = pets.find(pet => pet.id === spiritPets.activePetId);
  
  //  ,     
  useEffect(() => {
    const userId = state.player?.id || 'demo_user';
    
    //     ,  -
    if (!state.player?.spiritPets || !Array.isArray(state.player?.spiritPets.pets) || state.player.spiritPets.pets.length === 0) {
      console.log(' -  ');
      const demoPet = createDemoPet(userId);
      actions.addSpiritPet(demoPet);
    }
  }, [state.player?.id, state.player?.spiritPets, actions]);
  
  //   
  const handleActivatePet = (petId) => {
    setIsLoading(true);
    try {
      actions.setActiveSpiritPet(petId);
      setIsLoading(false);
    } catch (err) {
      console.error('   :', err);
      setError('   . ,  .');
      setIsLoading(false);
    }
  };
  
  //        
  const getPetFoodFromInventory = () => {
    //   
    const inventory = state.player?.inventory?.items || [];
    
    //    'pet_food'
    const petFood = inventory.filter(item => item.type === 'pet_food');
    
    return petFood;
  };
  
  //     
  const handleOpenFeedingModal = (petId) => {
    const pet = pets.find(p => p.id === petId);
    if (pet) {
      setSelectedPet(pet);
      setShowFeedingModal(true);
    }
  };
  
  //   
  const handleFeedPet = (petId, foodItemId) => {
    setIsLoading(true);
    try {
      //    
      if (foodItemId) {
        //      ID  
        actions.feedSpiritPet({
          petId,
          foodItemId
        });
      } else {
        //   -   
        console.log('     ');
        const defaultFood = {
          id: 'default_food',
          name: ' ',
          nutritionValue: 20,
          loyaltyBonus: 5
        };
        
        actions.feedSpiritPet({
          petId,
          foodItem: defaultFood
        });
      }
      
      //    
      setShowFeedingModal(false);
      setIsLoading(false);
    } catch (err) {
      console.error('   :', err);
      setError('   . ,  .');
      setIsLoading(false);
    }
  };
  
  //   
  const handleTrainPet = (petId, statToTrain) => {
    setIsLoading(true);
    try {
      //  statToTrain   trainingType
      actions.trainSpiritPet({
        petId,
        trainingType: statToTrain  //  statToTrain   : strength, intelligence, agility, etc.
      });
      
      //    
      setShowTrainingModal(false);
      setIsLoading(false);
    } catch (err) {
      console.error('   :', err);
      setError('   . , ,    .');
      setIsLoading(false);
    }
  };
  
  //     
  const handleOpenTraining = (petId) => {
    const pet = pets.find(p => p.id === petId);
    if (pet) {
      setSelectedPet(pet);
      setShowTrainingModal(true);
    }
  };
  
  //      
  const handleViewDetails = (pet) => {
    setSelectedPet(pet);
    setShowDetailsModal(true);
  };
  
  //    
  const handleTamePet = () => {
    setIsLoading(true);
    try {
      //     
      const location = state.player?.location?.name || ' ';
      
      //    
      const userId = state.player?.id || 'demo_user';
      const petTypes = ['fire', 'water', 'earth', 'wind', 'lightning'];
      const randomType = petTypes[Math.floor(Math.random() * petTypes.length)];
      
      const names = [
        '', '', '', '', '', '', '', 
        '', '', '', '', '', '', ''
      ];
      const randomName = names[Math.floor(Math.random() * names.length)];
      
      //      
      const baseStats = petTypeBaseStats[randomType] || {
        strength: 5,
        intelligence: 5,
        agility: 5,
        vitality: 5,
        spirit: 5
      };
      
      const newPet = {
        id: `pet_${Date.now()}`,
        userId: userId,
        name: randomName,
        type: randomType,
        level: 1,
        experience: 0,
        loyalty: 50,
        hunger: 70,
        strength: baseStats.strength,
        intelligence: baseStats.intelligence,
        agility: baseStats.agility,
        vitality: baseStats.vitality,
        spirit: baseStats.spirit,
        abilities: [`${randomType}_breath`],
        evolutionStage: 1,
        isActive: false,
        lastFed: new Date(),
        lastTrained: new Date(),
        createdAt: new Date(),
        updatedAt: new Date()
      };
      
      //     
      actions.addSpiritPet(newPet);
      
      setIsLoading(false);
    } catch (err) {
      console.error('   :', err);
      setError('   . ,  .');
      setIsLoading(false);
    }
  };
  
  //   
  if (isLoading && pets.length === 0) {
    return (
      <div className="pets-loading-container">
        <div className="pets-loading-spinner"></div>
        <div> ...</div>
      </div>
    );
  }
  
  //  
  if (error && pets.length === 0) {
    return (
      <div className="pets-error-container">
        <div className="pets-error-message">{error}</div>
        <button className="pets-refresh-button" onClick={() => window.location.reload()}>
           
        </button>
      </div>
    );
  }

  return (
    <div className="spirit-pets-container">
      <div className="pets-header">
        <h2> </h2>
        <button className="tame-pet-button" onClick={handleTamePet}>
            
        </button>
      </div>
      
      {error && <div className="pets-error-message">{error}</div>}
      
      {pets.length === 0 ? (
        <div className="no-pets-message">
          <p>    .   "  ",  .</p>
        </div>
      ) : (
        <div className="pets-grid">
          {pets.map(pet => (
            <PetCard
              key={pet.id}
              pet={pet}
              isActive={activePet && activePet.id === pet.id}
              onActivate={handleActivatePet}
              onFeed={handleOpenFeedingModal}
              onTrain={handleOpenTraining}
              onViewDetails={handleViewDetails}
            />
          ))}
        </div>
      )}
      
      {/*    */}
      {showFeedingModal && selectedPet && (
        <PetFeedingModal
          pet={selectedPet}
          onClose={() => setShowFeedingModal(false)}
          onSelect={(foodItemId) => handleFeedPet(selectedPet.id, foodItemId)}
          availableFood={getPetFoodFromInventory()}
        />
      )}
      
      {/*    */}
      {showTrainingModal && selectedPet && (
        <PetTrainingModal
          pet={selectedPet}
          onClose={() => setShowTrainingModal(false)}
          onTrain={handleTrainPet}
        />
      )}
      
      {/*     */}
      {showDetailsModal && selectedPet && (
        <PetDetailsModal
          pet={selectedPet}
          onClose={() => setShowDetailsModal(false)}
        />
      )}
    </div>
  );
};

export default SpiritPetsTab;
