//       
const { unifiedDatabase, initializeDatabaseConnection } = require('../services/database-connection-manager');
const { Sequelize } = require('sequelize');
let sequelize;

//     Sequelize
let sequelizeInstance = null;

//      Sequelize
async function getSequelizeInstance() {
  if (!sequelize) {
    const { db } = await initializeDatabaseConnection();
    sequelize = db;
  }
  return sequelize;
}
//        
function getObjectivesFromDB(quest) {
  try {
    // ,  steps   JSON   rewards    
    const stepsData = JSON.parse(quest.steps || '[]');
    return stepsData.map(step => ({
      id: `q${quest.id}_obj${step.id}`,
      text: step.description,
      completed: step.completed || false
    }));
  } catch (e) {
    console.error('    :', e);
    return [];
  }
}

//       
function getRewardsFromDB(quest) {
  try {
    const rewardsData = JSON.parse(quest.rewards || '{}');
    const rewards = [];
    
    //  ,   
    if (rewardsData.experience) {
      rewards.push({
        type: 'experience',
        amount: rewardsData.experience,
        icon: ''
      });
    }
    
    //  
    if (rewardsData.items && Array.isArray(rewardsData.items)) {
      rewardsData.items.forEach(item => {
        rewards.push({
          type: 'item',
          name: item.name,
          icon: getIconForItem(item.type)
        });
      });
    }
    
    //  
    if (rewardsData.gold) {
      const gold = Math.floor(rewardsData.gold);
      const silver = Math.round((rewardsData.gold - gold) * 10);
      
      rewards.push({
        type: 'currency',
        amount: { gold, silver },
        icon: ''
      });
    }
    
    //  ,  
    if (rewardsData.technique) {
      rewards.push({
        type: 'technique',
        name: rewardsData.technique.name,
        icon: getTechniqueIcon(rewardsData.technique.id)
      });
    }
    
    return rewards;
  } catch (e) {
    console.error('    :', e);
    return [];
  }
}

//     
function getIconForItem(type) {
  const icons = {
    'weapon': '',
    'armor': '',
    'pill': '',
    'resource': '',
    'artifact': '',
    'accessory': ''
  };
  return icons[type] || '';
}

function getTechniqueIcon(techniqueId) {
  const icons = {
    4: '', //   
    8: '', //   
    12: '', //   
    default: ''
  };
  return icons[techniqueId] || icons.default;
}

//       
async function getQuests() {
  try {
    //   Sequelize
    const seq = await getSequelizeInstance();
    
    //      
    const questsFromDB = await unifiedDatabase.query(
      'SELECT * FROM quests',
      { type: seq.QueryTypes.SELECT }
    );
    
    //       ,   
    return questsFromDB.map(quest => ({
      id: quest.id,
      title: quest.title,
      category: quest.type || '',
      difficulty: quest.difficulty || '',
      description: quest.description,
      objectives: getObjectivesFromDB(quest),
      rewards: getRewardsFromDB(quest),
      status: mapStatus(quest.status),
      requiredLevel: quest.required_cultivation_level || 0
    }));
  } catch (error) {
    console.error('     :', error);
    return []; //      
  }
}

//         
function mapStatus(dbStatus) {
  const statusMap = {
    '': 'available',
    ' ': 'active',
    '': 'completed',
    '': 'failed'
  };
  return statusMap[dbStatus] || 'available';
}

//      
let quests = [];

//  
async function initQuestData() {
  quests = await getQuests();
  return quests;
}

//     
initQuestData().catch(error => {
  console.error('    :', error);
});

const questCategories = [
  { id: 'all', name: '' },
  { id: 'main', name: '' },
  { id: 'side', name: '' },
  { id: 'sect', name: '' },
  { id: 'daily', name: '' }
];

//    
module.exports = {
  quests,
  initQuestData,
  getQuests,
  questCategories
};
