import React, { useState, useEffect } from 'react';
import { useGame } from '../../context/GameContext';
import styled from 'styled-components';
import ACTION_TYPES from '../../context/actions/actionTypes';
import { getAllMerchants } from '../../data/merchants';
import { getEquipmentItemById } from '../../data/equipment-items';
import BonusService from '../../services/bonus-service';
import { ensureItemHasCalculatedBonuses } from '../../utils/equipmentBonusHelper';
import { calculateMerchantDiscount, applyLoyaltyDiscount } from '../../utils/sectRelationshipSyncer';

//  
const TabContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px;
  color: #f0f0f0;
`;

const TabHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
`;

const TabTitle = styled.h2`
  font-size: 24px;
  margin: 0;
  color: #ffd700;
`;

const TabContent = styled.div`
  display: flex;
  flex: 1;
  gap: 20px;
`;

const LeftPanel = styled.div`
  flex: 1;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  padding: 15px;
  display: flex;
  flex-direction: column;
`;

const RightPanel = styled.div`
  flex: 1;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  padding: 15px;
  display: flex;
  flex-direction: column;
`;

const TabMenu = styled.div`
  display: flex;
  margin-bottom: 15px;
  border-bottom: 1px solid #444;
`;

const TabButton = styled.button`
  background: ${props => props.active ? 'rgba(255, 215, 0, 0.2)' : 'transparent'};
  color: ${props => props.active ? '#ffd700' : '#ccc'};
  border: none;
  padding: 10px 15px;
  font-size: 16px;
  cursor: pointer;
  border-bottom: ${props => props.active ? '2px solid #ffd700' : 'none'};
  transition: all 0.3s;
  
  &:hover {
    background: rgba(255, 215, 0, 0.1);
    color: #ffd700;
  }
`;

const ItemList = styled.div`
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const ItemCard = styled.div`
  display: flex;
  background: ${props => props.selected ? 'rgba(255, 215, 0, 0.1)' : 'rgba(30, 30, 30, 0.7)'};
  border: 1px solid ${props => props.selected ? '#ffd700' : '#444'};
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
  transition: all 0.2s;
  
  &:hover {
    background: rgba(255, 215, 0, 0.05);
    border-color: #ffd700;
  }
`;

const ItemIcon = styled.div`
  width: 40px;
  height: 40px;
  background: #333;
  border-radius: 5px;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
`;

const ItemInfo = styled.div`
  flex: 1;
`;

const ItemName = styled.div`
  font-weight: bold;
  color: ${props => {
    switch(props.rarity) {
      case 'common': return '#aaa';
      case 'uncommon': return '#1eff00';
      case 'rare': return '#0070dd';
      case 'epic': return '#a335ee';
      case 'legendary': return '#ff8000';
      default: return '#aaa';
    }
  }};
`;

const ItemPrice = styled.div`
  color: ${props => {
    switch(props.currencyType) {
      case 'copper': return '#b87333';
      case 'silver': return '#c0c0c0';
      case 'gold': return '#ffd700';
      case 'spiritStones': return '#7cb9e8';
      default: return '#ffd700';
    }
  }};
`;

const ItemDescription = styled.div`
  font-size: 14px;
  color: #aaa;
  margin-top: 5px;
`;

const ItemDetails = styled.div`
  padding: 15px;
  background: rgba(30, 30, 30, 0.7);
  border-radius: 5px;
  margin-top: 15px;
`;

const DetailTitle = styled.h3`
  margin: 0 0 10px 0;
  color: #ffd700;
`;

const DetailRow = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
`;

const DetailLabel = styled.div`
  color: #aaa;
`;

const DetailValue = styled.div`
  color: #fff;
`;

const ActionButton = styled.button`
  background: ${props => props.primary ? 'rgba(255, 215, 0, 0.2)' : 'rgba(80, 80, 80, 0.3)'};
  color: ${props => props.primary ? '#ffd700' : '#ccc'};
  border: 1px solid ${props => props.primary ? '#ffd700' : '#444'};
  border-radius: 5px;
  padding: 10px 15px;
  margin-top: 15px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s;
  
  &:hover {
    background: ${props => props.primary ? 'rgba(255, 215, 0, 0.3)' : 'rgba(80, 80, 80, 0.5)'};
  }
  
  &:disabled {
    background: rgba(50, 50, 50, 0.3);
    color: #666;
    border-color: #333;
    cursor: not-allowed;
  }
`;

const QuantityControl = styled.div`
  display: flex;
  align-items: center;
  margin-top: 15px;
`;

const QuantityButton = styled.button`
  background: rgba(80, 80, 80, 0.3);
  color: #ccc;
  border: 1px solid #444;
  border-radius: 5px;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  cursor: pointer;
  
  &:hover {
    background: rgba(80, 80, 80, 0.5);
  }
  
  &:disabled {
    background: rgba(50, 50, 50, 0.3);
    color: #666;
    border-color: #333;
    cursor: not-allowed;
  }
`;

const QuantityInput = styled.input`
  background: rgba(30, 30, 30, 0.7);
  color: #fff;
  border: 1px solid #444;
  border-radius: 5px;
  width: 60px;
  height: 30px;
  margin: 0 10px;
  text-align: center;
  font-size: 16px;
`;

const SearchBar = styled.input`
  background: rgba(30, 30, 30, 0.7);
  color: #fff;
  border: 1px solid #444;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 15px;
  font-size: 16px;
  
  &::placeholder {
    color: #777;
  }
`;

const FilterContainer = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
`;

const FilterSelect = styled.select`
  background: rgba(30, 30, 30, 0.7);
  color: #fff;
  border: 1px solid #444;
  border-radius: 5px;
  padding: 8px;
  font-size: 14px;
`;

const NoItemsMessage = styled.div`
  text-align: center;
  padding: 20px;
  color: #777;
  font-style: italic;
`;

const CurrencyDisplay = styled.div`
  display: flex;
  align-items: center;
  gap: 15px;
  margin-left: auto;
`;

const CurrencyItem = styled.div`
  display: flex;
  align-items: center;
`;

const CurrencyIcon = styled.div`
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 5px;
  background: ${props => {
    switch(props.type) {
      case 'copper': return '#b87333';
      case 'silver': return '#c0c0c0';
      case 'gold': return '#ffd700';
      case 'spiritStone': return '#7cb9e8';
      default: return '#ccc';
    }
  }};
`;

const CurrencyValue = styled.span`
  color: #fff;
`;

/**
 *   ""
 */
const MarketTab = () => {
  const { state, actions } = useGame();
  const { player, market, world } = state;
  
  //  
  const [activeTab, setActiveTab] = useState('market'); // market, sell, merchants
  const [selectedItem, setSelectedItem] = useState(null);
  const [quantity, setQuantity] = useState(1);
  const [searchQuery, setSearchQuery] = useState('');
  const [itemTypeFilter, setItemTypeFilter] = useState('all');
  const [rarityFilter, setRarityFilter] = useState('all');
  const [selectedMerchant, setSelectedMerchant] = useState(null);
  const [activeSellerFilter, setActiveSellerFilter] = useState(null); //   ID  ()
  
  //      
  useEffect(() => {
    //    
    const merchants = getAllMerchants();
    
    //           
    const marketItems = [];
    
    merchants.forEach(merchant => {
      //        
      const relationshipLevel = getRelationshipLevel(merchant.id);
      
      if (merchant.items && merchant.items.length > 0) {
        merchant.items.forEach(item => {
          // ,     
          if (item.itemType === 'currency') {
            console.log(`MarketTab:    "currency": ${item.name || ' '}   ${merchant.name}`);
            return; //    "currency"
          }
          
          //     
          const basePrice = item.basePrice || 100;
          const discount = calculateMerchantDiscount(relationshipLevel);
          const { finalPrice } = applyLoyaltyDiscount(basePrice, relationshipLevel);
          
          //    
          const marketItem = {
            id: marketItems.length + 1,
            itemId: item.itemId,
            itemType: item.itemType,
            name: item.name || ` ${item.itemId}`,
            description: item.description || `  ${item.itemId}`,
            rarity: item.rarity || 'common',
            quantity: item.quantity < 0 ? 10 : item.quantity, //   , ,     ( 10)
            price: finalPrice,
            originalPrice: basePrice, //      
            discount: discount, //   
            sellerId: merchant.id,
            sellerName: merchant.name
          };
          
          //     
          if (item.itemType === 'pet_food') {
            //     
            if (item.nutritionValue !== undefined) {
              marketItem.nutritionValue = item.nutritionValue;
            }
            if (item.loyaltyBonus !== undefined) {
              marketItem.loyaltyBonus = item.loyaltyBonus;
            }
          }
          
          marketItems.push(marketItem);
        });
      }
    });
    
    //   UPDATE_MARKET_STATE    
    actions.dispatch({ 
      type: ACTION_TYPES.UPDATE_MARKET_STATE, 
      payload: { marketItems }
    });
  }, []); //    actions  market,      
  
  //      
  useEffect(() => {
    setSelectedItem(null);
  }, [itemTypeFilter, rarityFilter, activeSellerFilter, searchQuery]);
  
  //  
  const filteredItems = React.useMemo(() => {
    if (!market || !market.marketItems) {
      return [];
    }
    
    const filtered = market.marketItems.filter(item => {
      //    -     0
      if (item.quantity <= 0) {
        return false;
      }
      
      //    
      const matchesSearch = searchQuery === '' || 
        (item.name && item.name.toLowerCase().includes(searchQuery.toLowerCase())) ||
        (item.description && item.description.toLowerCase().includes(searchQuery.toLowerCase()));
      
      //    
      const matchesType = itemTypeFilter === 'all' || (item.itemType && item.itemType === itemTypeFilter);
      
      //   
      const matchesRarity = rarityFilter === 'all' || (item.rarity && item.rarity === rarityFilter);
      
      //    () -       
      const itemSellerId = Number(item.sellerId);
      const filterSellerId = Number(activeSellerFilter);
      const matchesSeller = activeSellerFilter === null || 
                           (item.sellerId !== undefined && itemSellerId === filterSellerId);
      
      return matchesSearch && matchesType && matchesRarity && matchesSeller;
    });
    
    return filtered;
  }, [market?.marketItems, searchQuery, itemTypeFilter, rarityFilter, activeSellerFilter]);
  
  //     
  const getRelationshipLevel = (merchantId) => {
    //      
    let playerRelationships = state?.player?.social?.relationships || [];
    
    // ,  playerRelationships -  
    if (!Array.isArray(playerRelationships)) {
      console.warn('   ,  :', playerRelationships);
      playerRelationships = typeof playerRelationships === 'object' && playerRelationships !== null
        ? Object.values(playerRelationships)
        : [];
    }
    
    //    
    const merchantData = getAllMerchants().find(m => m.id === merchantId);
    if (!merchantData) {
      return 0;
    }
    
    //        
    const socialRelation = playerRelationships.find(rel => rel.name === merchantData.name);
    if (socialRelation && socialRelation.level !== undefined) {
      return socialRelation.level;
    }
    
    //     
    if (merchantData.sect && state?.sect?.sect) {
      const sectName = state.sect.sect.name;
      if (merchantData.sect === sectName) {
        //  ,     
        if (state.sect.sect.members && Array.isArray(state.sect.sect.members)) {
          const sectMember = state.sect.sect.members.find(m => m.name === merchantData.name);
          if (sectMember && sectMember.loyalty !== undefined) {
            return sectMember.loyalty;
          }
        }
        
        //       ,    
        const sectRelation = playerRelationships.find(rel => rel.name === sectName);
        if (sectRelation && sectRelation.level !== undefined) {
          return sectRelation.level;
        }
      }
    }
    
    return 0;
  };
  
  //       
  const getCurrencyTypeByRarity = (rarity) => {
    switch(rarity) {
      case 'common': return 'copper';
      case 'uncommon': return 'silver';
      case 'rare': return 'gold';
      case 'epic': return 'gold+spiritStones';
      case 'legendary': return 'spiritStones';
      default: return 'gold';
    }
  };
  
  //        
  const formatPrice = (price, rarity) => {
    const currencyType = getCurrencyTypeByRarity(rarity);
    
    switch(currencyType) {
      case 'copper':
        return `${price} `;
      case 'silver':
        return `${price} `;
      case 'gold':
        return `${price} `;
      case 'gold+spiritStones':
        return `${Math.floor(price * 0.7)}  + ${Math.ceil(price * 0.3 / 100)} . `;
      case 'spiritStones':
        return `${Math.ceil(price / 100)} . `;
      default:
        return `${price} `;
    }
  };
  
  //     
  const hasSufficientCurrency = (price, rarity) => {
    const currencyType = getCurrencyTypeByRarity(rarity);
    const totalPrice = price * quantity;
    
    switch(currencyType) {
      case 'copper':
        return (player.inventory.currency.copper || 0) >= totalPrice;
      case 'silver':
        return (player.inventory.currency.silver || 0) >= totalPrice;
      case 'gold':
        return (player.inventory.currency.gold || 0) >= totalPrice;
      case 'gold+spiritStones': {
        const goldCost = Math.floor(totalPrice * 0.7);
        const spiritStonesCost = Math.ceil(totalPrice * 0.3 / 100);
        return (player.inventory.currency.gold || 0) >= goldCost && 
               (player.inventory.currency.spiritStones || 0) >= spiritStonesCost;
      }
      case 'spiritStones':
        return (player.inventory.currency.spiritStones || 0) >= Math.ceil(totalPrice / 100);
      default:
        return (player.inventory.currency.gold || 0) >= totalPrice;
    }
  };
  
  //   
  const handleBuyItem = () => {
    if (!selectedItem) return;
    
    //       
    const currencyType = getCurrencyTypeByRarity(selectedItem.rarity);
    const totalPrice = selectedItem.price * quantity;
    
    //     
    if (!hasSufficientCurrency(selectedItem.price, selectedItem.rarity)) {
      alert(` ${
        currencyType === 'copper' ? '' : 
        currencyType === 'silver' ? '' : 
        currencyType === 'gold' ? '' : 
        currencyType === 'gold+spiritStones' ? '   ' : 
        ' '
      }  `);
      return;
    }
    
    // ,     
    if (selectedItem.quantity < quantity) {
      alert(`   . : ${selectedItem.quantity}`);
      return;
    }
    
    //       
    console.log('  :', player.inventory.currency);
    
    //        
    //        (, .. )
    const currencyUpdate = {};
    
    //       
    switch(currencyType) {
      case 'copper':
        currencyUpdate.copper = -totalPrice; //     
        break;
      case 'silver':
        currencyUpdate.silver = -totalPrice; //     
        break;
      case 'gold':
        currencyUpdate.gold = -totalPrice; //     
        break;
      case 'gold+spiritStones': {
        const goldCost = Math.floor(totalPrice * 0.7);
        const spiritStonesCost = Math.ceil(totalPrice * 0.3 / 100);
        currencyUpdate.gold = -goldCost; //    
        currencyUpdate.spiritStones = -spiritStonesCost; //    
        break;
      }
      case 'spiritStones':
        currencyUpdate.spiritStones = -Math.ceil(totalPrice / 100); //    
        break;
    }
    
    //    
    alert(` ${quantity} .  "${selectedItem.name}"  ${formatPrice(totalPrice, selectedItem.rarity)}`);
    
    //      
    const itemToAdd = {
      id: selectedItem.itemId,
      type: selectedItem.itemType,
      name: selectedItem.name,
      description: selectedItem.description,
      rarity: selectedItem.rarity,
      price: selectedItem.price,
      quantity: quantity //    
    };
    
    //    ,   
    if (selectedItem.itemType === 'pet_food') {
      //   ,       
      if (selectedItem.nutritionValue !== undefined) {
        itemToAdd.nutritionValue = selectedItem.nutritionValue;
      }
      if (selectedItem.loyaltyBonus !== undefined) {
        itemToAdd.loyaltyBonus = selectedItem.loyaltyBonus;
      }
      //     
      if (selectedItem.statBonus) {
        itemToAdd.statBonus = selectedItem.statBonus;
      }
      if (selectedItem.preferredTypes) {
        itemToAdd.preferredTypes = selectedItem.preferredTypes;
      }
      
      console.log('      :', itemToAdd);
    }
    
    //    
    actions.addItem(itemToAdd);
    
    //      (true = /)
    actions.updateCurrency(currencyUpdate, true);
    
    //       
    console.log('   ( ):', currencyUpdate);
    
    //     
    //  ,   
    const merchants = getAllMerchants();
    const merchant = merchants.find(m => m.id === selectedItem.sellerId);
    
    if (merchant && merchant.items) {
      //      
      const merchantItem = merchant.items.find(item => 
        item.itemId === selectedItem.itemId && 
        item.itemType === selectedItem.itemType
      );
      
      if (merchantItem) {
        //     
        merchantItem.quantity -= quantity;
        
        //   
        if (merchantItem.quantity < 0) {
          merchantItem.quantity = 0;
        }
        
        //       
        if (merchantItem.lastRestockTime !== undefined) {
          merchantItem.lastRestockTime = Date.now() / 3600000; //     
        }
        
        console.log(`    "${merchant.name}": ${merchantItem.quantity}`);
        
        //       ,     UI
        const updatedMerchants = getAllMerchants();
        const updatedMarketItems = [];
        
        updatedMerchants.forEach(merchant => {
          if (merchant.items && merchant.items.length > 0) {
            //       
            const relationshipLevel = getRelationshipLevel(merchant.id);
            console.log(`   :    ${merchant.name}: ${relationshipLevel}`);
            
            merchant.items.forEach(item => {
              //    "currency"
              if (item.itemType === 'currency') return;
              
              //     
              const basePrice = item.basePrice || 100;
              const discount = calculateMerchantDiscount(relationshipLevel);
              const { finalPrice } = applyLoyaltyDiscount(basePrice, relationshipLevel);
              
              //    
              const marketItem = {
                id: updatedMarketItems.length + 1,
                itemId: item.itemId,
                itemType: item.itemType,
                name: item.name || ` ${item.itemId}`,
                description: item.description || `  ${item.itemId}`,
                rarity: item.rarity || 'common',
                quantity: item.quantity < 0 ? 10 : item.quantity,
                price: finalPrice,
                originalPrice: basePrice, //      
                discount: discount, //   
                sellerId: merchant.id,
                sellerName: merchant.name
              };
              
              //     
              if (item.itemType === 'pet_food') {
                //     
                if (item.nutritionValue !== undefined) {
                  marketItem.nutritionValue = item.nutritionValue;
                }
                if (item.loyaltyBonus !== undefined) {
                  marketItem.loyaltyBonus = item.loyaltyBonus;
                }
              }
              
              updatedMarketItems.push(marketItem);
            });
          }
        });
        
        //    
        actions.dispatch({
          type: ACTION_TYPES.UPDATE_MARKET_STATE,
          payload: {
            marketItems: updatedMarketItems,
            merchantsUpdated: true,
            timestamp: Date.now()
          }
        });
      }
    }
    
    setSelectedItem(null);
    setQuantity(1);
  };
  
  //   
  const handleSellItem = () => {
    if (!selectedItem) return;
    
    //    
    if (!selectedItem.price) {
      alert("   ");
      return;
    }
    
    //     
    if (selectedItem.quantity < quantity) {
      alert("     ");
      return;
    }
    
    //        
    const currencyType = getCurrencyTypeByRarity(selectedItem.rarity || 'common');
    
    //   ,    (70%   )
    const sellPrice = Math.floor(selectedItem.price * 0.7) * quantity;
    
    //        
    const currencyToAdd = {};
    
    switch(currencyType) {
      case 'copper':
        currencyToAdd.copper = sellPrice;
        break;
      case 'silver':
        currencyToAdd.silver = sellPrice;
        break;
      case 'gold':
        currencyToAdd.gold = sellPrice;
        break;
      case 'gold+spiritStones': {
        const goldAmount = Math.floor(sellPrice * 0.7);
        const spiritStonesAmount = Math.ceil(sellPrice * 0.3 / 100);
        currencyToAdd.gold = goldAmount;
        currencyToAdd.spiritStones = spiritStonesAmount;
        break;
      }
      case 'spiritStones':
        currencyToAdd.spiritStones = Math.ceil(sellPrice / 100);
        break;
      default:
        currencyToAdd.gold = sellPrice;
    }
    
    //    
    alert(` ${quantity} .  "${selectedItem.name}"  ${formatPrice(sellPrice, selectedItem.rarity)}`);
    
    //    
    actions.removeItem(selectedItem.id, quantity);
    
    //  
    actions.updateCurrency(currencyToAdd, true);
    
    //     
    setSelectedItem(null);
    setQuantity(1);
  };
  
  //  
  return (
    <TabContainer>
      <TabHeader>
        <TabTitle></TabTitle>
        <CurrencyDisplay>
          <CurrencyItem>
            <CurrencyIcon type="copper" />
            <CurrencyValue>{player?.inventory?.currency?.copper || 0}</CurrencyValue>
          </CurrencyItem>
          <CurrencyItem>
            <CurrencyIcon type="silver" />
            <CurrencyValue>{player?.inventory?.currency?.silver || 0}</CurrencyValue>
          </CurrencyItem>
          <CurrencyItem>
            <CurrencyIcon type="gold" />
            <CurrencyValue>{player?.inventory?.currency?.gold || 0}</CurrencyValue>
          </CurrencyItem>
          <CurrencyItem>
            <CurrencyIcon type="spiritStone" />
            <CurrencyValue>{player?.inventory?.currency?.spiritStones || 0}</CurrencyValue>
          </CurrencyItem>
        </CurrencyDisplay>
      </TabHeader>
      
      <TabMenu>
        <TabButton 
          active={activeTab === 'market'} 
          onClick={() => setActiveTab('market')}
        >
          
        </TabButton>
        <TabButton 
          active={activeTab === 'sell'} 
          onClick={() => setActiveTab('sell')}
        >
          
        </TabButton>
        <TabButton 
          active={activeTab === 'merchants'} 
          onClick={() => setActiveTab('merchants')}
        >
          
        </TabButton>
      </TabMenu>
      
      <TabContent>
        {activeTab === 'market' && (
          <>
            <LeftPanel>
              <SearchBar 
                placeholder=" ..." 
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
              
              <FilterContainer>
                <FilterSelect 
                  value={itemTypeFilter}
                  onChange={(e) => setItemTypeFilter(e.target.value)}
                >
                  <option value="all"> </option>
                  <option value="weapon"></option>
                  <option value="armor"></option>
                  <option value="accessory"></option>
                  <option value="consumable"></option>
                  <option value="pet_food">  </option>
                </FilterSelect>
                
                <FilterSelect 
                  value={rarityFilter}
                  onChange={(e) => setRarityFilter(e.target.value)}
                >
                  <option value="all"> </option>
                  <option value="common"></option>
                  <option value="uncommon"></option>
                  <option value="rare"></option>
                  <option value="epic"></option>
                  <option value="legendary"></option>
                </FilterSelect>
                
                <FilterSelect 
                  value={activeSellerFilter || ''}
                  onChange={(e) => setActiveSellerFilter(e.target.value === '' ? null : e.target.value)}
                >
                  <option value=""> </option>
                  {market?.marketItems && [...new Set(market.marketItems.map(item => item.sellerId))].map(sellerId => {
                    const seller = market.marketItems.find(item => item.sellerId === sellerId);
                    return (
                      <option key={sellerId} value={sellerId}>
                        {seller?.sellerName || ` #${sellerId}`}
                      </option>
                    );
                  })}
                </FilterSelect>
              </FilterContainer>
              
              <ItemList>
                {filteredItems.length > 0 ? (
                  filteredItems.map(item => (
                    <ItemCard 
                      key={item.id} 
                      selected={selectedItem && selectedItem.id === item.id}
                      onClick={() => setSelectedItem(item)}
                    >
                      <ItemIcon>{item.itemType === 'weapon' ? '' : 
                        item.itemType === 'armor' ? '' : 
                        item.itemType === 'accessory' ? '' : 
                        item.itemType === 'consumable' ? '' : 
                        item.itemType === 'pet_food' ? '' : ''}
                      </ItemIcon>
                      <ItemInfo>
                        <ItemName rarity={item.rarity}>{item.name}</ItemName>
                        <ItemPrice currencyType={getCurrencyTypeByRarity(item.rarity)}>
                          {formatPrice(item.price, item.rarity)}
                          {item.discount > 0 && ` (-${item.discount}%)`}
                        </ItemPrice>
                      </ItemInfo>
                    </ItemCard>
                  ))
                ) : (
                  <NoItemsMessage>  </NoItemsMessage>
                )}
              </ItemList>
            </LeftPanel>
            
            <RightPanel>
              {selectedItem ? (
                <>
                  <DetailTitle>{selectedItem.name}</DetailTitle>
                  <ItemDescription>{selectedItem.description}</ItemDescription>
                  
                  <ItemDetails>
                    <DetailRow>
                      <DetailLabel>:</DetailLabel>
                      <DetailValue>
                        {selectedItem.itemType === 'weapon' ? '' : 
                         selectedItem.itemType === 'armor' ? '' : 
                         selectedItem.itemType === 'accessory' ? '' : 
                         selectedItem.itemType === 'consumable' ? '' : 
                         selectedItem.itemType === 'pet_food' ? '  ' :
                         ''}
                      </DetailValue>
                    </DetailRow>
                    <DetailRow>
                      <DetailLabel>:</DetailLabel>
                      <DetailValue>
                        {selectedItem.rarity === 'common' ? '' : 
                         selectedItem.rarity === 'uncommon' ? '' : 
                         selectedItem.rarity === 'rare' ? '' : 
                         selectedItem.rarity === 'epic' ? '' : 
                         selectedItem.rarity === 'legendary' ? '' : 
                         ''}
                      </DetailValue>
                    </DetailRow>
                    <DetailRow>
                      <DetailLabel>:</DetailLabel>
                      <DetailValue>
                        {formatPrice(selectedItem.price, selectedItem.rarity)}
                        {selectedItem.discount > 0 && (
                          <span style={{ color: '#8AFF8A', marginLeft: '8px' }}>
                            (-{selectedItem.discount}%)
                          </span>
                        )}
                      </DetailValue>
                    </DetailRow>
                    <DetailRow>
                      <DetailLabel>:</DetailLabel>
                      <DetailValue>{selectedItem.sellerName}</DetailValue>
                    </DetailRow>
                    <DetailRow>
                      <DetailLabel> :</DetailLabel>
                      <DetailValue>{selectedItem.quantity}</DetailValue>
                    </DetailRow>
                    
                    {/*       */}
                    {selectedItem.itemType === 'pet_food' && (
                      <>
                        {selectedItem.nutritionValue !== undefined && (
                          <DetailRow>
                            <DetailLabel>:</DetailLabel>
                            <DetailValue>{selectedItem.nutritionValue}</DetailValue>
                          </DetailRow>
                        )}
                        {selectedItem.loyaltyBonus !== undefined && (
                          <DetailRow>
                            <DetailLabel> :</DetailLabel>
                            <DetailValue>+{selectedItem.loyaltyBonus}</DetailValue>
                          </DetailRow>
                        )}
                      </>
                    )}
                  </ItemDetails>
                  
                  <QuantityControl>
                    <QuantityButton 
                      onClick={() => setQuantity(prev => Math.max(1, prev - 1))}
                      disabled={quantity <= 1}
                    >-</QuantityButton>
                    <QuantityInput 
                      type="number"
                      min="1"
                      max={selectedItem.quantity}
                      value={quantity}
                      onChange={(e) => {
                        const val = parseInt(e.target.value);
                        if (!isNaN(val) && val > 0 && val <= selectedItem.quantity) {
                          setQuantity(val);
                        }
                      }}
                    />
                    <QuantityButton 
                      onClick={() => setQuantity(prev => Math.min(selectedItem.quantity, prev + 1))}
                      disabled={quantity >= selectedItem.quantity}
                    >+</QuantityButton>
                  </QuantityControl>
                  
                  <ActionButton 
                    primary 
                    onClick={handleBuyItem}
                    disabled={!hasSufficientCurrency(selectedItem.price, selectedItem.rarity)}
                  >
                      {formatPrice(selectedItem.price * quantity, selectedItem.rarity)}
                  </ActionButton>
                </>
              ) : (
                <NoItemsMessage>    </NoItemsMessage>
              )}
            </RightPanel>
          </>
        )}
        
        {activeTab === 'sell' && (
          <>
            <LeftPanel>
              <SearchBar 
                placeholder="  ..." 
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
              
              <FilterContainer>
                <FilterSelect 
                  value={itemTypeFilter}
                  onChange={(e) => setItemTypeFilter(e.target.value)}
                >
                  <option value="all"> </option>
                  <option value="weapon"></option>
                  <option value="armor"></option>
                  <option value="accessory"></option>
                  <option value="consumable"></option>
                </FilterSelect>
                
                <FilterSelect 
                  value={rarityFilter}
                  onChange={(e) => setRarityFilter(e.target.value)}
                >
                  <option value="all"> </option>
                  <option value="common"></option>
                  <option value="uncommon"></option>
                  <option value="rare"></option>
                  <option value="epic"></option>
                  <option value="legendary"></option>
                </FilterSelect>
              </FilterContainer>
              
              <ItemList>
                {player?.inventory?.items && player.inventory.items.length > 0 ? (
                  player.inventory.items
                    .filter(item => 
                      //  
                      (itemTypeFilter === 'all' || item.type === itemTypeFilter) &&
                      (rarityFilter === 'all' || item.rarity === rarityFilter) &&
                      (searchQuery === '' || 
                        (item.name && item.name.toLowerCase().includes(searchQuery.toLowerCase())) ||
                        (item.description && item.description.toLowerCase().includes(searchQuery.toLowerCase())))
                    )
                    .map(item => (
                      <ItemCard 
                        key={item.id} 
                        selected={selectedItem && selectedItem.id === item.id}
                        onClick={() => setSelectedItem(item)}
                      >
                        <ItemIcon>{item.type === 'weapon' ? '' : 
                          item.type === 'armor' ? '' : 
                          item.type === 'accessory' ? '' : 
                          item.type === 'consumable' ? '' : ''}
                        </ItemIcon>
                        <ItemInfo>
                          <ItemName rarity={item.rarity}>{item.name}</ItemName>
                          <div>x{item.quantity || 1}</div>
                        </ItemInfo>
                      </ItemCard>
                    ))
                ) : (
                  <NoItemsMessage>   </NoItemsMessage>
                )}
              </ItemList>
            </LeftPanel>
            
            <RightPanel>
              {selectedItem ? (
                <>
                  <DetailTitle>{selectedItem.name}</DetailTitle>
                  <ItemDescription>{selectedItem.description}</ItemDescription>
                  
                  <ItemDetails>
                    <DetailRow>
                      <DetailLabel>:</DetailLabel>
                      <DetailValue>
                        {selectedItem.type === 'weapon' ? '' : 
                         selectedItem.type === 'armor' ? '' : 
                         selectedItem.type === 'accessory' ? '' : 
                         selectedItem.type === 'consumable' ? '' : 
                         ''}
                      </DetailValue>
                    </DetailRow>
                    <DetailRow>
                      <DetailLabel>:</DetailLabel>
                      <DetailValue>
                        {selectedItem.rarity === 'common' ? '' : 
                         selectedItem.rarity === 'uncommon' ? '' : 
                         selectedItem.rarity === 'rare' ? '' : 
                         selectedItem.rarity === 'epic' ? '' : 
                         selectedItem.rarity === 'legendary' ? '' : 
                         ''}
                      </DetailValue>
                    </DetailRow>
                    {selectedItem.price && (
                      <DetailRow>
                        <DetailLabel> :</DetailLabel>
                        <DetailValue>
                          {formatPrice(Math.floor(selectedItem.price * 0.7), selectedItem.rarity)}
                        </DetailValue>
                      </DetailRow>
                    )}
                    <DetailRow>
                      <DetailLabel>:</DetailLabel>
                      <DetailValue>{selectedItem.quantity || 1}</DetailValue>
                    </DetailRow>
                  </ItemDetails>
                  
                  <QuantityControl>
                    <QuantityButton 
                      onClick={() => setQuantity(prev => Math.max(1, prev - 1))}
                      disabled={quantity <= 1}
                    >-</QuantityButton>
                    <QuantityInput 
                      type="number"
                      min="1"
                      max={selectedItem.quantity || 1}
                      value={quantity}
                      onChange={(e) => {
                        const val = parseInt(e.target.value);
                        if (!isNaN(val) && val > 0 && val <= (selectedItem.quantity || 1)) {
                          setQuantity(val);
                        }
                      }}
                    />
                    <QuantityButton 
                      onClick={() => setQuantity(prev => Math.min(selectedItem.quantity || 1, prev + 1))}
                      disabled={quantity >= (selectedItem.quantity || 1)}
                    >+</QuantityButton>
                  </QuantityControl>
                  
                  <ActionButton 
                    primary 
                    onClick={handleSellItem}
                    disabled={!selectedItem.price}
                  >
                      {selectedItem.price ? 
                      formatPrice(Math.floor(selectedItem.price * 0.7) * quantity, selectedItem.rarity) :
                      " "}
                  </ActionButton>
                </>
              ) : (
                <NoItemsMessage>   </NoItemsMessage>
              )}
            </RightPanel>
          </>
        )}
        
        {activeTab === 'merchants' && (
          <>
            <LeftPanel>
              <SearchBar 
                placeholder=" ..." 
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
              
              <ItemList>
                {market?.marketItems && [...new Set(market.marketItems.map(item => item.sellerId))].map(sellerId => {
                  const merchant = market.marketItems.find(item => item.sellerId === sellerId);
                  
                  if (!merchant) return null;
                  
                  const merchantName = merchant.sellerName || ` #${sellerId}`;
                  
                  //   
                  if (searchQuery && !merchantName.toLowerCase().includes(searchQuery.toLowerCase())) {
                    return null;
                  }
                  
                  return (
                    <ItemCard 
                      key={sellerId} 
                      selected={selectedMerchant && selectedMerchant.id === sellerId}
                      onClick={() => {
                        setSelectedMerchant({
                          id: sellerId,
                          name: merchantName
                        });
                        //       ""
                        setActiveSellerFilter(sellerId);
                      }}
                    >
                      <ItemIcon></ItemIcon>
                      <ItemInfo>
                        <ItemName>{merchantName}</ItemName>
                        <div>: {getRelationshipLevel(sellerId)}</div>
                      </ItemInfo>
                    </ItemCard>
                  );
                })}
              </ItemList>
            </LeftPanel>
            
            <RightPanel>
              {selectedMerchant ? (
                <>
                  <DetailTitle>{selectedMerchant.name}</DetailTitle>
                  
                  <ItemDetails>
                    <DetailRow>
                      <DetailLabel>:</DetailLabel>
                      <DetailValue>{getRelationshipLevel(selectedMerchant.id)}</DetailValue>
                    </DetailRow>
                    <DetailRow>
                      <DetailLabel>:</DetailLabel>
                      <DetailValue>{calculateMerchantDiscount(getRelationshipLevel(selectedMerchant.id))}%</DetailValue>
                    </DetailRow>
                  </ItemDetails>
                  
                  <ActionButton 
                    onClick={() => {
                      setActiveTab('market');
                      setActiveSellerFilter(selectedMerchant.id);
                    }}
                  >
                     
                  </ActionButton>
                </>
              ) : (
                <NoItemsMessage>    </NoItemsMessage>
              )}
            </RightPanel>
          </>
        )}
      </TabContent>
    </TabContainer>
  );
};

export default MarketTab;
