// src/services/database.js
const Sequelize = require('sequelize');
const { initializeDatabaseConnection, unifiedDatabase } = require('./database-connection-manager');

//    sequelize
let sequelize = null;

//      
function withTimeout(promise, timeoutMs, errorMessage) {
  let timeoutId;
  const timeoutPromise = new Promise((_, reject) => {
    timeoutId = setTimeout(() => {
      reject(new Error(errorMessage || '   '));
    }, timeoutMs);
  });
  
  return Promise.race([
    promise,
    timeoutPromise
  ]).finally(() => clearTimeout(timeoutId));
}

// -    sequelize
async function getSequelizeInstance() {
  try {
    if (!sequelize) {
      console.log('  Sequelize  database.js...');
      //       initializeDatabaseConnection
      const result = await withTimeout(
        unifiedDatabase.getSequelizeInstance(),
        15000,
        '  Sequelize  database.js   '
      );
      
      if (!result || !result.db) {
        throw new Error(" :     ");
      }
      
      sequelize = result.db;
      console.log(' Sequelize    database.js');
    }
    return sequelize;
  } catch (error) {
    console.error("     Sequelize:", error.message);
    //     
    throw new Error(`    PostgreSQL: ${error.message}`);
  }
}

//        
const proxyBase = {
  modelManager: {
    getModel: function(name) {
      console.warn(`[PROXY]   ${name}  `);
      return null;
    }
  },
  model: function(name) {
    console.warn(`[PROXY]   ${name}  `);
    return null;
  },
  define: function() {
    console.warn("[PROXY]     ");
    return {};
  },
  authenticate: async function() {
    console.warn("[PROXY]    ");
    //     
    const seq = await getSequelizeInstance();
    return seq.authenticate();
  }
};

//      
const sequelizeProxy = new Proxy(proxyBase, {
  get: function(target, prop) {
    //       ,  
    if (prop in target) {
      if (typeof target[prop] === 'function') {
        return (...args) => {
          console.log(`[PROXY]   ${prop}  `);
          return target[prop](...args);
        };
      }
      return target[prop];
    }
    
    //    modelManager
    if (prop === 'modelManager') {
      return new Proxy(target.modelManager || {}, {
        get: function(targetManager, managerProp) {
          if (managerProp in targetManager) {
            if (typeof targetManager[managerProp] === 'function') {
              return function(...args) {
                console.log(`[PROXY]  modelManager.${managerProp}`);
                return targetManager[managerProp](...args);
              };
            }
            return targetManager[managerProp];
          }
          
          return function(...args) {
            console.log(`[PROXY]  modelManager.${managerProp}   Sequelize`);
            return getSequelizeInstance()
              .then(seq => {
                if (!seq || !seq.modelManager || typeof seq.modelManager[managerProp] !== 'function') {
                  console.error(`[PROXY]  modelManager.${managerProp} `);
                  throw new Error(` modelManager.${managerProp}   Sequelize`);
                }
                return seq.modelManager[managerProp](...args);
              })
              .catch(err => {
                console.error(`[PROXY]     modelManager.${managerProp}:`, err);
                throw err; //   
              });
          };
        }
      });
    }
    
    //       
    return function(...args) {
      console.log(`[PROXY]   ${prop}   Sequelize`);
      return getSequelizeInstance()
        .then(seq => {
          if (!seq || typeof seq[prop] !== 'function') {
            console.error(`[PROXY]  ${prop}    Sequelize`);
            throw new Error(` ${prop}   Sequelize`);
          }
          return seq[prop](...args);
        })
        .catch(err => {
          console.error(`[PROXY]     ${prop}:`, err);
          throw err; //   
        });
    };
  }
});

//   Sequelize,   
module.exports = {
  Model: Sequelize.Model,
  DataTypes: Sequelize.DataTypes,
  sequelize: sequelizeProxy,
  getSequelizeInstance //      
};
