const LearnedTechnique = require('../models/learned-technique');
const Technique = require('../models/technique');
const TechniqueEffect = require('../models/technique-effect');
const User = require('../models/user');

// ,     
const isBrowser = typeof window !== 'undefined';

//        
let browserTechniquesData = [];
let browserLearnedTechniquesData = {};

/**
 *     
 */
class TechniqueService {
  /**
   *   
   * @returns {Promise<Array>} -   
   */
  static async getAllTechniques() {
    try {
      if (isBrowser) {
        //      
        return browserTechniquesData;
      } else {
        //     
        const techniques = await Technique.findAll({
          include: [
            {
              model: TechniqueEffect,
              as: 'effects'
            }
          ]
        });
        
        return techniques;
      }
    } catch (error) {
      //console.error('   :', error);
      throw error;
    }
  }
  
  /**
   *    ID
   * @param {string} techniqueId - ID 
   * @returns {Promise<Object>} - 
   */
  static async getTechniqueById(techniqueId) {
    try {
      const technique = await Technique.findOne({
        where: { techniqueId },
        include: [
          {
            model: TechniqueEffect,
            as: 'effects'
          }
        ]
      });
      
      if (!technique) {
        throw new Error(`  ID ${techniqueId}  `);
      }
      
      return technique;
    } catch (error) {
      //console.error(`     ID ${techniqueId}:`, error);
      throw error;
    }
  }
  
  /**
   *     
   * @param {number} userId - ID 
   * @returns {Promise<Array>} -   
   */
  static async getLearnedTechniques(userId) {
    try {
      if (isBrowser) {
        //      
        if (!browserLearnedTechniquesData[userId]) {
          browserLearnedTechniquesData[userId] = [];
        }
        
        //    
        return browserLearnedTechniquesData[userId];
      } else {
        //     
        //     
        const learnedTechniques = await LearnedTechnique.findAll({
          where: { userId },
          include: [
            {
              model: Technique,
              as: 'technique',
              include: [
                {
                  model: TechniqueEffect,
                  as: 'effects'
                }
              ]
            }
          ]
        });
        
        //    
        return learnedTechniques.map(learned => ({
          id: learned.technique.techniqueId,
          name: learned.technique.name,
          description: learned.technique.description,
          type: learned.technique.type,
          element: learned.technique.element,
          icon: learned.technique.icon,
          level: learned.level,
          experience: learned.experience,
          masteryLevel: learned.masteryLevel,
          energyCost: learned.technique.energyCost,
          damage: learned.technique.damage,
          damageType: learned.technique.damageType,
          healing: learned.technique.healing,
          cooldown: learned.technique.cooldown,
          maxLevel: learned.technique.maxLevel,
          lastUsed: learned.lastUsed,
          effects: learned.technique.effects
        }));
      }
    } catch (error) {
      //console.error('    :', error);
      throw error;
    }
  }
  
  /**
   *   
   * @param {number} userId - ID 
   * @param {string} techniqueId - ID 
   * @returns {Promise<Object>} -  
   */
  static async learnTechnique(userId, techniqueId) {
    try {
      if (isBrowser) {
        //      
        if (!browserLearnedTechniquesData[userId]) {
          browserLearnedTechniquesData[userId] = [];
        }
        
        // ,   
        const technique = browserTechniquesData.find(tech => tech.techniqueId === techniqueId);
        
        if (!technique) {
          //    ,     
          const mockTechnique = {
            techniqueId: techniqueId,
            name: ` ${techniqueId}`,
            description: ' ',
            type: 'attack',
            element: 'fire',
            icon: 'fire_icon',
            energyCost: 10,
            damage: 50,
            damageType: 'fire',
            healing: 0,
            cooldown: 5,
            maxLevel: 10,
            effects: []
          };
          
          browserTechniquesData.push(mockTechnique);
          
          // ,     
          const existingTechnique = browserLearnedTechniquesData[userId].find(
            tech => tech.id === techniqueId
          );
          
          if (existingTechnique) {
            throw new Error('  ');
          }
          
          //     
          const learnedTechnique = {
            id: mockTechnique.techniqueId,
            name: mockTechnique.name,
            description: mockTechnique.description,
            type: mockTechnique.type,
            element: mockTechnique.element,
            icon: mockTechnique.icon,
            level: 1,
            experience: 0,
            masteryLevel: 0,
            energyCost: mockTechnique.energyCost,
            damage: mockTechnique.damage,
            damageType: mockTechnique.damageType,
            healing: mockTechnique.healing,
            cooldown: mockTechnique.cooldown,
            maxLevel: mockTechnique.maxLevel,
            lastUsed: null,
            effects: mockTechnique.effects
          };
          
          browserLearnedTechniquesData[userId].push(learnedTechnique);
          
          return learnedTechnique;
        } else {
          // ,     
          const existingTechnique = browserLearnedTechniquesData[userId].find(
            tech => tech.id === techniqueId
          );
          
          if (existingTechnique) {
            throw new Error('  ');
          }
          
          //     
          const learnedTechnique = {
            id: technique.techniqueId,
            name: technique.name,
            description: technique.description,
            type: technique.type,
            element: technique.element,
            icon: technique.icon,
            level: 1,
            experience: 0,
            masteryLevel: 0,
            energyCost: technique.energyCost,
            damage: technique.damage,
            damageType: technique.damageType,
            healing: technique.healing,
            cooldown: technique.cooldown,
            maxLevel: technique.maxLevel,
            lastUsed: null,
            effects: technique.effects
          };
          
          browserLearnedTechniquesData[userId].push(learnedTechnique);
          
          return learnedTechnique;
        }
      } else {
        //     
        // ,   
        const technique = await Technique.findOne({
          where: { techniqueId },
          include: [
            {
              model: TechniqueEffect,
              as: 'effects'
            }
          ]
        });
        
        if (!technique) {
          throw new Error(`  ID ${techniqueId}  `);
        }
        
        // ,     
        const existingTechnique = await LearnedTechnique.findOne({
          where: {
            userId,
            techniqueId: technique.id
          }
        });
        
        if (existingTechnique) {
          throw new Error('  ');
        }
        
        //     
        const learnedTechnique = await LearnedTechnique.create({
          userId,
          techniqueId: technique.id,
          level: 1,
          experience: 0,
          masteryLevel: 0,
          lastUsed: null
        });
        
        return {
          id: technique.techniqueId,
          name: technique.name,
          description: technique.description,
          type: technique.type,
          element: technique.element,
          icon: technique.icon,
          level: learnedTechnique.level,
          experience: learnedTechnique.experience,
          masteryLevel: learnedTechnique.masteryLevel,
          energyCost: technique.energyCost,
          damage: technique.damage,
          damageType: technique.damageType,
          healing: technique.healing,
          cooldown: technique.cooldown,
          maxLevel: technique.maxLevel,
          effects: technique.effects
        };
      }
    } catch (error) {
      //console.error('   :', error);
      throw error;
    }
  }
  
  /**
   *   
   * @param {number} userId - ID 
   * @param {string} techniqueId - ID 
   * @returns {Promise<Object>} -  
   */
  static async upgradeTechnique(userId, techniqueId) {
    try {
      //  
      const technique = await Technique.findOne({
        where: { techniqueId },
        include: [
          {
            model: TechniqueEffect,
            as: 'effects'
          }
        ]
      });
      
      if (!technique) {
        throw new Error(`  ID ${techniqueId}  `);
      }
      
      //   
      const learnedTechnique = await LearnedTechnique.findOne({
        where: {
          userId,
          techniqueId: technique.id
        }
      });
      
      if (!learnedTechnique) {
        throw new Error('  ');
      }
      
      // ,     
      if (learnedTechnique.level >= technique.maxLevel) {
        throw new Error('   ');
      }
      
      //   
      learnedTechnique.level += 1;
      learnedTechnique.experience = 0;
      await learnedTechnique.save();
      
      return {
        id: technique.techniqueId,
        name: technique.name,
        description: technique.description,
        type: technique.type,
        element: technique.element,
        icon: technique.icon,
        level: learnedTechnique.level,
        experience: learnedTechnique.experience,
        masteryLevel: learnedTechnique.masteryLevel,
        energyCost: technique.energyCost,
        damage: technique.damage,
        damageType: technique.damageType,
        healing: technique.healing,
        cooldown: technique.cooldown,
        maxLevel: technique.maxLevel,
        lastUsed: learnedTechnique.lastUsed,
        effects: technique.effects
      };
    } catch (error) {
      //console.error('    :', error);
      throw error;
    }
  }
  
  /**
   *  
   * @param {number} userId - ID 
   * @param {string} techniqueId - ID 
   * @returns {Promise<Object>} -   
   */
  static async useTechnique(userId, techniqueId) {
    try {
      //  
      const technique = await Technique.findOne({
        where: { techniqueId },
        include: [
          {
            model: TechniqueEffect,
            as: 'effects'
          }
        ]
      });
      
      if (!technique) {
        throw new Error(`  ID ${techniqueId}  `);
      }
      
      //   
      const learnedTechnique = await LearnedTechnique.findOne({
        where: {
          userId,
          techniqueId: technique.id
        }
      });
      
      if (!learnedTechnique) {
        throw new Error('  ');
      }
      
      //    
      learnedTechnique.lastUsed = new Date();
      
      //    
      learnedTechnique.experience += 1;
      
      // ,       
      const masteryThreshold = 100 * (learnedTechnique.masteryLevel + 1);
      if (learnedTechnique.experience >= masteryThreshold) {
        learnedTechnique.masteryLevel += 1;
        learnedTechnique.experience -= masteryThreshold;
      }
      
      await learnedTechnique.save();
      
      //         
      const calculatedEffects = technique.effects.map(effect => {
        const calculatedEffect = { ...effect.toJSON() };
        
        //          
        if (calculatedEffect.damage > 0) {
          calculatedEffect.damage = Math.floor(
            calculatedEffect.damage * (1 + 0.1 * learnedTechnique.level + 0.05 * learnedTechnique.masteryLevel)
          );
        }
        
        if (calculatedEffect.healing > 0) {
          calculatedEffect.healing = Math.floor(
            calculatedEffect.healing * (1 + 0.1 * learnedTechnique.level + 0.05 * learnedTechnique.masteryLevel)
          );
        }
        
        //       
        if (calculatedEffect.duration > 0) {
          calculatedEffect.duration = Math.floor(
            calculatedEffect.duration * (1 + 0.05 * learnedTechnique.masteryLevel)
          );
        }
        
        return calculatedEffect;
      });
      
      //    
      const calculatedDamage = technique.damage > 0
        ? Math.floor(technique.damage * (1 + 0.1 * learnedTechnique.level + 0.05 * learnedTechnique.masteryLevel))
        : 0;
      
      //    
      const calculatedHealing = technique.healing > 0
        ? Math.floor(technique.healing * (1 + 0.1 * learnedTechnique.level + 0.05 * learnedTechnique.masteryLevel))
        : 0;
      
      return {
        id: technique.techniqueId,
        name: technique.name,
        level: learnedTechnique.level,
        experience: learnedTechnique.experience,
        masteryLevel: learnedTechnique.masteryLevel,
        lastUsed: learnedTechnique.lastUsed,
        damage: calculatedDamage,
        damageType: technique.damageType,
        healing: calculatedHealing,
        effects: calculatedEffects
      };
    } catch (error) {
      //console.error('   :', error);
      throw error;
    }
  }
  
  /**
   *        
   * @param {string} techniqueName -  
   * @returns {Promise<Object>} - 
   */
  static async getTechniqueByName(techniqueName) {
    try {
      const technique = await Technique.findOne({
        where: { name: techniqueName },
        include: [
          {
            model: TechniqueEffect,
            as: 'effects'
          }
        ]
      });
      
      if (!technique) {
        throw new Error(`   "${techniqueName}"  `);
      }
      
      return technique;
    } catch (error) {
      //console.error(`      "${techniqueName}":`, error);
      throw error;
    }
  }
}

module.exports = TechniqueService;
