const fs = require('fs');
const path = require('path');
const { getSequelizeInstance } = require('../services/database');
const Sequelize = require('sequelize');

const basename = path.basename(__filename);
const db = {
  Sequelize: Sequelize,
  sequelize: null,
};

async function initializeModels() {
  try {
    const sequelize = await getSequelizeInstance();
    if (!sequelize) {
      throw new Error("    Sequelize");
    }
    db.sequelize = sequelize;

    const modelFiles = fs
      .readdirSync(__dirname)
      .filter(file => {
        return (
          file.indexOf('.') !== 0 &&
          file !== basename &&
          file !== 'fix-models.js' &&
          file.slice(-3) === '.js' &&
          file.indexOf('.test.js') === -1
        );
      });

    // 1.    
    for (const file of modelFiles) {
      try {
        const modelDefinition = require(path.join(__dirname, file));
        // ,      Sequelize
        if (modelDefinition && typeof modelDefinition === 'function' && modelDefinition.prototype instanceof Sequelize.Model) {
          const modelName = modelDefinition.modelName || modelDefinition.name;
          if (modelName) {
            //  ,       init
            if (typeof modelDefinition.init === 'function' && modelDefinition.init.constructor.name === 'AsyncFunction') {
               console.log(`   ${modelName}...`);
               await modelDefinition.init(); //  
               //  init     sequelize.models
               if (sequelize.models[modelName]) {
                  db[modelName] = sequelize.models[modelName];
                  console.log(` ${modelName} .`);
               } else {
                  console.error(` ${modelName}    sequelize.models   init.`);
                  db[modelName] = modelDefinition; //    fallback
               }
            } else if (typeof modelDefinition.init === 'function') {
               //      init (  )
               console.warn(` ${modelName}   init. ...`);
               modelDefinition.init(sequelize); //  sequelize
               db[modelName] = modelDefinition;
               console.log(` ${modelName}  ().`);
            } else {
               console.warn(` ${modelName}    init.`);
               db[modelName] = modelDefinition; //    
            }
          } else {
            console.warn(`  ${file}   modelName  name.`);
          }
        } else {
          console.warn(` ${file}     Sequelize.`);
        }
      } catch (error) {
        console.error(`  /   ${file}:`, error);
      }
    }
    console.log("    .");

    // 2.  
    Object.keys(db).forEach(modelName => {
      //  Sequelize  sequelize
      if (modelName === 'Sequelize' || modelName === 'sequelize' || modelName === 'initializePromise') return;

      if (db[modelName] && db[modelName].associate) {
        console.log(`    ${modelName}...`);
        try {
          db[modelName].associate(db); //   db   
        } catch (assocError) {
          console.error(`   ${modelName}:`, assocError);
        }
      }
    });

    console.log("    .");

  } catch (error) {
    console.error("    :", error);
    db.sequelize = null;
  }
}

//   
db.initializePromise = initializeModels();

module.exports = db;
