import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { useGame } from '../../context/GameContext';
import { debugEquipment } from '../../utils/equipmentDebug';
import { ensureItemHasCalculatedBonuses } from '../../utils/equipmentBonusHelper';

const Container = styled.div`
  display: grid;
  grid-template-columns: 400px 1fr;
  gap: 20px;
`;

const EquipmentDisplay = styled.div`
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 8px;
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: minmax(100px, auto);
  grid-gap: 20px;
  max-width: 100%;
`;

const EquipmentSlot = styled.div`
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #666;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
  cursor: pointer;
  height: 100px;
  
  &.head {
    grid-column: 1 / span 3;
    height: 80px;
  }
  
  &.body {
    grid-column: 2;
    grid-row: 2;
  }
  
  &.left {
    grid-column: 1;
    grid-row: 2;
  }
  
  &.right {
    grid-column: 3;
    grid-row: 2;
  }
  
  &.hands {
    grid-column: 1 / span 3;
    grid-row: 3;
    height: 80px;
  }
  
  &.legs {
    grid-column: 1 / span 3;
    grid-row: 4;
    height: 80px;
  }
  
  &.acc1 {
    grid-column: 1;
    grid-row: 5;
  }
  
  &.acc2 {
    grid-column: 3;
    grid-row: 5;
  }
  
  &.art1 {
    grid-column: 1;
    grid-row: 6;
  }
  
  &.art2 {
    grid-column: 3;
    grid-row: 6;
  }
  
  &.equipped {
    border-color: ${props => props.rarity === 'common' ? '#666' :
      props.rarity === 'uncommon' ? '#2196f3' :
      props.rarity === 'rare' ? '#9c27b0' :
      props.rarity === 'epic' ? '#ff9800' : '#d4af37'};
    box-shadow: 0 0 5px ${props => props.rarity === 'common' ? '#666' :
      props.rarity === 'uncommon' ? '#2196f3' :
      props.rarity === 'rare' ? '#9c27b0' :
      props.rarity === 'epic' ? '#ff9800' : '#d4af37'};
  }
`;

const SlotName = styled.div`
  color: #aaa;
  font-size: 0.8rem;
  margin-bottom: 5px;
`;

const ItemIcon = styled.div`
  width: 40px;
  height: 40px;
  background: ${props => props.type === 'weapon' ? '#f44336' :
    props.type === 'armor' ? '#2196f3' :
    props.type === 'accessory' ? '#9c27b0' :
    props.type === 'artifact' ? '#ff9800' :
    props.type === 'material' ? '#4caf50' :
    props.type === 'book' ? '#795548' : '#666'};
  border-radius: 4px;
`;

const ItemName = styled.div`
  color: ${props => props.rarity === 'common' ? '#fff' :
    props.rarity === 'uncommon' ? '#2196f3' :
    props.rarity === 'rare' ? '#9c27b0' :
    props.rarity === 'epic' ? '#ff9800' : '#d4af37'};
  font-size: 0.9rem;
  margin-top: 5px;
  text-align: center;
`;

const StatsPanel = styled.div`
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 8px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
`;

const UnequipButton = styled.button`
  padding: 10px 15px;
  background: rgba(180, 30, 30, 0.3);
  border: 1px solid #d4af37;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  margin-top: 10px;
  transition: all 0.2s ease;
  
  &:hover:not(:disabled) {
    background: rgba(180, 30, 30, 0.5);
  }
  
  &:disabled {
    opacity: 0.3;
    cursor: not-allowed;
  }
`;

const DropButton = styled.button`
  padding: 10px 15px;
  background: rgba(180, 30, 30, 0.3);
  border: 1px solid #d4af37;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  margin-top: 10px;
  width: 100%;
  transition: all 0.2s ease;
  
  &:hover:not(:disabled) {
    background: rgba(180, 30, 30, 0.5);
  }
  
  &:disabled {
    opacity: 0.3;
    cursor: not-allowed;
  }
`;

const SectionTitle = styled.h3`
  color: #d4af37;
  font-size: 1.2rem;
  margin-bottom: 10px;
  border-bottom: 1px solid rgba(212, 175, 55, 0.3);
  padding-bottom: 5px;
`;

const StatGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
`;

const StatItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  
  span:first-child {
    color: #aaa;
  }
  
  span:last-child {
    color: ${props => props.value > 0 ? '#4caf50' : props.value < 0 ? '#f44336' : '#aaa'};
  }
`;

const FilterButtons = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
`;

const FilterButton = styled.button`
  padding: 8px 15px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #666;
  border-radius: 4px;
  color: #aaa;
  cursor: pointer;
  transition: all 0.2s ease;
  
  &:hover {
    background: rgba(0, 0, 0, 0.5);
  }
  
  &.active {
    background: rgba(212, 175, 55, 0.2);
    border-color: #d4af37;
    color: #d4af37;
  }
`;

const InventoryGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
  gap: 10px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #666;
  border-radius: 8px;
`;

const ItemSlot = styled.div`
  width: 100%;
  aspect-ratio: 1;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid ${props => props.rarity === 'common' ? '#666' :
    props.rarity === 'uncommon' ? '#2196f3' :
    props.rarity === 'rare' ? '#9c27b0' :
    props.rarity === 'epic' ? '#ff9800' : '#d4af37'};
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  cursor: pointer;
  transition: all 0.2s ease;
  
  &:hover {
    transform: scale(1.05);
    box-shadow: 0 0 10px ${props => props.rarity === 'common' ? '#666' :
      props.rarity === 'uncommon' ? '#2196f3' :
      props.rarity === 'rare' ? '#9c27b0' :
      props.rarity === 'epic' ? '#ff9800' : '#d4af37'};
  }
  
  &.equipped {
    border-width: 2px;
    box-shadow: 0 0 5px ${props => props.rarity === 'common' ? '#666' :
      props.rarity === 'uncommon' ? '#2196f3' :
      props.rarity === 'rare' ? '#9c27b0' :
      props.rarity === 'epic' ? '#ff9800' : '#d4af37'};
  }
`;

//         armorType  
const determineArmorType = (item) => {
  if (!item) return null;
  
  //     
  const armorType = item.properties?.armorType || item.armorType;
  if (armorType) return armorType;
  
  //    ,   
  const itemName = item.name.toLowerCase();
  
  if (itemName.includes('') || itemName.includes('') || itemName.includes('')) {
    return 'head';
  }
  
  if (itemName.includes('') || itemName.includes('') || itemName.includes('')) {
    return 'legs';
  }
  
  if (itemName.includes('') || itemName.includes('')) {
    return 'hands';
  }
  
  //      
  return 'body';
};

function EquipmentTab() {
  const { state, actions } = useGame();
  const [equipped, setEquipped] = useState({
    weapon: null,
    headArmor: null,
    bodyArmor: null,
    legArmor: null,
    handArmor: null,
    accessory1: null,
    accessory2: null,
    artifact1: null,
    artifact2: null
  });
  const [filter, setFilter] = useState('all');
  const [selectedSlot, setSelectedSlot] = useState(null);
  const [selectedEquippedItem, setSelectedEquippedItem] = useState(null);
  const [selectedInventoryItem, setSelectedInventoryItem] = useState(null);
  
  //        
  const isMounted = React.useRef(false);
  
  useEffect(() => {
    //       
    if (isMounted.current) {
      return;
    }
    
    console.log(' ');
    isMounted.current = true;
    
    //      
    const items = updateEquippedItems();
    
    //        
    console.log('      ');
    actions.applyEquipmentBonuses(items);
    
    //       
    setTimeout(() => {
      debugEquipment(state);
    }, 500); //  ,  ,     
  }, []); //    -    
  
  //       
  useEffect(() => {
    if (!isMounted.current) {
      return; //   ,   
    }
    
    //      
    updateEquippedItems();
  }, [state.player.inventory.items]);
  
  //       (  )
  //     
  const updateEquippedItems = () => {
    const equippedItems = {};
    
    //    
    const equippedItemsList = state.player.inventory.items.filter(item => item.equipped);
    
    console.log('====    ====');
    console.log(` ${equippedItemsList.length}  `);
    
    //     
    state.player.inventory.items.forEach(item => {
      if (item.equipped) {
        //        
        let itemWithBonuses = ensureItemHasCalculatedBonuses(item);
        if (itemWithBonuses.calculatedBonuses) {
          console.log(`  ${item.name}   `);
        }
        
        switch(item.type) {
          case 'weapon':
            equippedItems.weapon = itemWithBonuses;
            console.log(`  : ${itemWithBonuses.name}`);
            break;
          case 'armor':
            //  armorType  properties,  
            const armorType = item.properties?.armorType || item.armorType;
            const itemName = item.name.toLowerCase();
            
            //     
            if (armorType === 'head' || (!armorType && (itemName.includes('') || itemName.includes('') || itemName.includes('')))) {
              equippedItems.headArmor = itemWithBonuses;
              console.log(`    : ${itemWithBonuses.name}`);
            }
            else if (armorType === 'legs' || (!armorType && (itemName.includes('') || itemName.includes('') || itemName.includes('')))) {
              equippedItems.legArmor = itemWithBonuses;
              console.log(`    : ${itemWithBonuses.name}`);
            }
            else if (armorType === 'hands' || (!armorType && (itemName.includes('') || itemName.includes('')))) {
              equippedItems.handArmor = itemWithBonuses;
              console.log(`    : ${itemWithBonuses.name}`);
            }
            else {
              //    ,    
              equippedItems.bodyArmor = itemWithBonuses;
              console.log(`    : ${itemWithBonuses.name}`);
            }
            break;
          case 'accessory':
            if (!equippedItems.accessory1) {
              equippedItems.accessory1 = itemWithBonuses;
              console.log(`   1: ${itemWithBonuses.name}`);
            } else if (!equippedItems.accessory2) {
              equippedItems.accessory2 = itemWithBonuses;
              console.log(`   2: ${itemWithBonuses.name}`);
            }
            break;
          case 'artifact':
            if (!equippedItems.artifact1) {
              equippedItems.artifact1 = itemWithBonuses;
              console.log(`   1: ${itemWithBonuses.name}`);
            } else if (!equippedItems.artifact2) {
              equippedItems.artifact2 = itemWithBonuses;
              console.log(`   2: ${itemWithBonuses.name}`);
            }
            break;
        }
      }
    });
    
    setEquipped(equippedItems);
    
    //       
    console.log('      ');
    actions.applyEquipmentBonuses(equippedItems);
    
    return equippedItems;
  };
  
  const handleSlotClick = (slotType) => {
    setSelectedSlot(slotType);
    
    //    ,     
    const itemInSlot = equipped[slotType];
    setSelectedEquippedItem(itemInSlot);
    
    //        
    switch(slotType) {
      case 'weapon':
        setFilter('weapon');
        break;
      case 'headArmor':
      case 'bodyArmor':
      case 'legArmor':
      case 'handArmor':
        setFilter('armor');
        break;
      case 'accessory1':
      case 'accessory2':
        setFilter('accessory');
        break;
      case 'artifact1':
      case 'artifact2':
        setFilter('artifact');
        break;
      default:
        setFilter('all');
    }
  };
  
  //    
  const handleUnequipItem = () => {
    if (selectedEquippedItem && selectedSlot) {
      console.log(`  ${selectedEquippedItem.name}   ${selectedSlot}`);
      
      //      unequipItem
      actions.unequipItem(selectedSlot);
      
      //   
      updateEquippedItems();
      
      //     
      setSelectedEquippedItem(null);
    }
  };
  
  //    
  const handleDropItem = () => {
    if (selectedInventoryItem) {
      console.log(`  ${selectedInventoryItem.name}`);
      
      //   ,   
      if (selectedInventoryItem.equipped) {
        //  ,    
        for (const [slot, item] of Object.entries(equipped)) {
          if (item && item.id === selectedInventoryItem.id) {
            console.log(`   ${selectedInventoryItem.name}   ${slot}`);
            actions.unequipItem(slot);
            break;
          }
        }
      }
      
      //   -  ,  reducer    id   payload
      const itemId = selectedInventoryItem.id || selectedInventoryItem.itemId;
      actions.removeItem({ id: itemId }); // :    id   id
      
      //   
      setTimeout(() => {
        updateEquippedItems();
        
        //   
        setSelectedInventoryItem(null);
      }, 100);
    }
  };
  
  const handleItemClick = (item) => {
    //    
    setSelectedInventoryItem(item);
    
    if (selectedSlot) {
      // ,     
      item = ensureItemHasCalculatedBonuses(item);
      
      //     equipItem
      console.log(`  ${item.name}   ${selectedSlot}`);
      actions.equipItem(item);
      
      //  UI  
      updateEquippedItems();
    }
  };
  
  //  
  return (
    <Container>
      <div>
        <EquipmentDisplay>
          <EquipmentSlot 
            className="head" 
            onClick={() => handleSlotClick('headArmor')}
            rarity={equipped.headArmor ? equipped.headArmor.rarity : 'common'}
          >
            <SlotName></SlotName>
            {equipped.headArmor ? (
              <>
                <ItemIcon type="armor" />
                <ItemName rarity={equipped.headArmor.rarity}>{equipped.headArmor.name}</ItemName>
              </>
            ) : (
              <span> </span>
            )}
          </EquipmentSlot>
          
          <EquipmentSlot 
            className="left" 
            onClick={() => handleSlotClick('weapon')}
            rarity={equipped.weapon ? equipped.weapon.rarity : 'common'}
          >
            <SlotName></SlotName>
            {equipped.weapon ? (
              <>
                <ItemIcon type="weapon" />
                <ItemName rarity={equipped.weapon.rarity}>{equipped.weapon.name}</ItemName>
              </>
            ) : (
              <span> </span>
            )}
          </EquipmentSlot>
          
          <EquipmentSlot 
            className="body" 
            onClick={() => handleSlotClick('bodyArmor')}
            rarity={equipped.bodyArmor ? equipped.bodyArmor.rarity : 'common'}
          >
            <SlotName></SlotName>
            {equipped.bodyArmor ? (
              <>
                <ItemIcon type="armor" />
                <ItemName rarity={equipped.bodyArmor.rarity}>{equipped.bodyArmor.name}</ItemName>
              </>
            ) : (
              <span> </span>
            )}
          </EquipmentSlot>
          
          <EquipmentSlot 
            className="right" 
            onClick={() => handleSlotClick('shield')}
            rarity={'common'}
          >
            <SlotName></SlotName>
            <span> </span>
          </EquipmentSlot>
          
          <EquipmentSlot 
            className="hands" 
            onClick={() => handleSlotClick('handArmor')}
            rarity={equipped.handArmor ? equipped.handArmor.rarity : 'common'}
          >
            <SlotName></SlotName>
            {equipped.handArmor ? (
              <>
                <ItemIcon type="armor" />
                <ItemName rarity={equipped.handArmor.rarity}>{equipped.handArmor.name}</ItemName>
              </>
            ) : (
              <span> </span>
            )}
          </EquipmentSlot>
          
          <EquipmentSlot 
            className="legs" 
            onClick={() => handleSlotClick('legArmor')}
            rarity={equipped.legArmor ? equipped.legArmor.rarity : 'common'}
          >
            <SlotName></SlotName>
            {equipped.legArmor ? (
              <>
                <ItemIcon type="armor" />
                <ItemName rarity={equipped.legArmor.rarity}>{equipped.legArmor.name}</ItemName>
              </>
            ) : (
              <span> </span>
            )}
          </EquipmentSlot>
          
          <EquipmentSlot 
            className="acc1" 
            onClick={() => handleSlotClick('accessory1')}
            rarity={equipped.accessory1 ? equipped.accessory1.rarity : 'common'}
          >
            <SlotName> 1</SlotName>
            {equipped.accessory1 ? (
              <>
                <ItemIcon type="accessory" />
                <ItemName rarity={equipped.accessory1.rarity}>{equipped.accessory1.name}</ItemName>
              </>
            ) : (
              <span> </span>
            )}
          </EquipmentSlot>
          
          <EquipmentSlot 
            className="acc2" 
            onClick={() => handleSlotClick('accessory2')}
            rarity={equipped.accessory2 ? equipped.accessory2.rarity : 'common'}
          >
            <SlotName> 2</SlotName>
            {equipped.accessory2 ? (
              <>
                <ItemIcon type="accessory" />
                <ItemName rarity={equipped.accessory2.rarity}>{equipped.accessory2.name}</ItemName>
              </>
            ) : (
              <span> </span>
            )}
          </EquipmentSlot>
          
          <EquipmentSlot 
            className="art1" 
            onClick={() => handleSlotClick('artifact1')}
            rarity={equipped.artifact1 ? equipped.artifact1.rarity : 'common'}
          >
            <SlotName> 1</SlotName>
            {equipped.artifact1 ? (
              <>
                <ItemIcon type="artifact" />
                <ItemName rarity={equipped.artifact1.rarity}>{equipped.artifact1.name}</ItemName>
              </>
            ) : (
              <span> </span>
            )}
          </EquipmentSlot>
          
          <EquipmentSlot 
            className="art2" 
            onClick={() => handleSlotClick('artifact2')}
            rarity={equipped.artifact2 ? equipped.artifact2.rarity : 'common'}
          >
            <SlotName> 2</SlotName>
            {equipped.artifact2 ? (
              <>
                <ItemIcon type="artifact" />
                <ItemName rarity={equipped.artifact2.rarity}>{equipped.artifact2.name}</ItemName>
              </>
            ) : (
              <span> </span>
            )}
          </EquipmentSlot>
        </EquipmentDisplay>
        
        {/*   */}
        <div style={{ marginTop: '20px' }}>
          <SectionTitle></SectionTitle>
          
          <FilterButtons>
            <FilterButton 
              className={filter === 'all' ? 'active' : ''} 
              onClick={() => setFilter('all')}
            >
              
            </FilterButton>
            <FilterButton 
              className={filter === 'weapon' ? 'active' : ''} 
              onClick={() => setFilter('weapon')}
            >
              
            </FilterButton>
            <FilterButton 
              className={filter === 'armor' ? 'active' : ''} 
              onClick={() => setFilter('armor')}
            >
              
            </FilterButton>
            <FilterButton 
              className={filter === 'accessory' ? 'active' : ''} 
              onClick={() => setFilter('accessory')}
            >
              
            </FilterButton>
            <FilterButton 
              className={filter === 'artifact' ? 'active' : ''} 
              onClick={() => setFilter('artifact')}
            >
              
            </FilterButton>
          </FilterButtons>
          
          <InventoryGrid>
            {state.player.inventory.items
              .filter(item => {
                if (filter === 'all') return true;
                return item.type === filter;
              })
              .map((item, index) => (
                <ItemSlot 
                  key={`${item.id}-${index}`}
                  rarity={item.rarity || 'common'}
                  className={item.equipped ? 'equipped' : ''}
                  onClick={() => handleItemClick(item)}
                >
                  <ItemIcon type={item.type || 'material'} />
                  <ItemName rarity={item.rarity || 'common'}>{item.name}</ItemName>
                </ItemSlot>
              ))}
          </InventoryGrid>
        </div>
      </div>
      
      <StatsPanel>
        <SectionTitle> </SectionTitle>
        
        {/*     */}
        {selectedEquippedItem && (
          <div>
            <h4> : {selectedEquippedItem.name}</h4>
            <UnequipButton onClick={handleUnequipItem}>
               
            </UnequipButton>
            <div style={{ marginTop: '10px' }}>
              <DropButton onClick={handleDropItem}>
                
              </DropButton>
            </div>
          </div>
        )}
        
        {/*        */}
        {selectedInventoryItem && !selectedInventoryItem.equipped && (
          <div>
            <h4> : {selectedInventoryItem.name}</h4>
            <div style={{ marginTop: '10px' }}>
              <DropButton onClick={handleDropItem}>
                
              </DropButton>
            </div>
          </div>
        )}
        
        {state.player.equipmentBonuses && (
          <>
            {/*   */}
            <div>
              <h4> </h4>
              <StatGrid>
                {Object.entries(state.player.equipmentBonuses.stats || {}).map(([stat, value]) => (
                  <StatItem key={stat} value={value}>
                    <span>
                      {stat === 'strength' ? '' :
                       stat === 'dexterity' ? '' :
                       stat === 'vitality' ? '' :
                       stat === 'intelligence' ? '' :
                       stat === 'perception' ? '' :
                       stat === 'luck' ? '' : stat}
                    </span>
                    <span>{value > 0 ? '+' : ''}{value}</span>
                  </StatItem>
                ))}
              </StatGrid>
            </div>
            
            {/*   */}
            <div>
              <h4> </h4>
              <StatGrid>
                {Object.entries(state.player.equipmentBonuses.combat || {}).map(([stat, value]) => (
                  <StatItem key={stat} value={value}>
                    <span>
                      {stat === 'physicalDamage' ? ' ' :
                       stat === 'magicDamage' ? ' ' :
                       stat === 'physicalDefense' ? ' ' :
                       stat === 'magicDefense' ? ' ' :
                       stat === 'critChance' ? ' . ' :
                       stat === 'critDamage' ? '  . ' :
                       stat === 'dodgeChance' ? ' ' : stat}
                    </span>
                    <span>
                      {value > 0 ? '+' : ''}{value}
                      {['critChance', 'critDamage', 'dodgeChance'].includes(stat) ? '%' : ''}
                    </span>
                  </StatItem>
                ))}
              </StatGrid>
            </div>
          </>
        )}
      </StatsPanel>
    </Container>
  );
}

export default EquipmentTab;
