import React, { useState } from 'react';
import { useGameContext } from '../../context/GameContext';
import Button from '../ui/Button';
import styles from './MemberList.module.css';

/**
 *      
 * @param {Object[]} members -  
 * @param {string} leaderId - ID  
 * @param {string} currentUserId - ID  
 * @param {string} currentUserRole -     
 */
const MemberList = ({ members, leaderId, currentUserId, currentUserRole }) => {
  const { actions } = useGameContext();
  const [selectedMember, setSelectedMember] = useState(null);
  const [loading, setLoading] = useState(false);
  
  //      
  const getRoleInfo = (role) => {
    switch (role) {
      case 'leader':
        return { icon: '', name: '' };
      case 'officer':
        return { icon: '', name: '' };
      default:
        return { icon: '', name: '' };
    }
  };
  
  //    
  const getSpecIcon = (specialization) => {
    switch (specialization) {
      case 'damage':
        return ''; // 
      case 'tank':
        return ''; // 
      case 'support':
        return ''; // 
      case 'control':
        return ''; // 
      case 'healer':
        return ''; // 
      default:
        return ''; //  
    }
  };
  
  //    
  const getSpecName = (specialization) => {
    switch (specialization) {
      case 'damage':
        return '';
      case 'tank':
        return '';
      case 'support':
        return '';
      case 'control':
        return '';
      case 'healer':
        return '';
      default:
        return '';
    }
  };
  
  // ,     
  const canManageMember = (member) => {
    //    
    if (currentUserRole === 'leader') {
      return true;
    }
    
    //     
    if (currentUserRole === 'officer' && member.role === 'member') {
      return true;
    }
    
    return false;
  };
  
  //      
  const handlePromote = async (member) => {
    if (currentUserRole !== 'leader') {
      actions.addNotification({
        message: '     ',
        type: 'error'
      });
      return;
    }
    
    if (member.role !== 'member') {
      actions.addNotification({
        message: '     ',
        type: 'error'
      });
      return;
    }
    
    setLoading(true);
    
    try {
      //        API   
      /*
      await actions.dispatch({
        type: 'UPDATE_MEMBER_ROLE',
        payload: {
          groupId: member.groupId,
          userId: member.userId,
          newRole: 'officer'
        }
      });
      */
      
      //  
      actions.addNotification({
        message: `${member.user.username}   `,
        type: 'success'
      });
      
      //   
      setSelectedMember(null);
    } catch (error) {
      console.error('   :', error);
      actions.addNotification({
        message: `   : ${error.message}`,
        type: 'error'
      });
    } finally {
      setLoading(false);
    }
  };
  
  //       
  const handleDemote = async (member) => {
    if (currentUserRole !== 'leader') {
      actions.addNotification({
        message: '     ',
        type: 'error'
      });
      return;
    }
    
    if (member.role !== 'officer') {
      actions.addNotification({
        message: '     ',
        type: 'error'
      });
      return;
    }
    
    setLoading(true);
    
    try {
      //        API   
      /*
      await actions.dispatch({
        type: 'UPDATE_MEMBER_ROLE',
        payload: {
          groupId: member.groupId,
          userId: member.userId,
          newRole: 'member'
        }
      });
      */
      
      //  
      actions.addNotification({
        message: `${member.user.username}    `,
        type: 'success'
      });
      
      //   
      setSelectedMember(null);
    } catch (error) {
      console.error('   :', error);
      actions.addNotification({
        message: `   : ${error.message}`,
        type: 'error'
      });
    } finally {
      setLoading(false);
    }
  };
  
  //      
  const handleKick = async (member) => {
    if (!canManageMember(member)) {
      actions.addNotification({
        message: '       ',
        type: 'error'
      });
      return;
    }
    
    if (member.userId === leaderId) {
      actions.addNotification({
        message: '   ',
        type: 'error'
      });
      return;
    }
    
    if (!window.confirm(` ,    ${member.user.username}  ?`)) {
      return;
    }
    
    setLoading(true);
    
    try {
      //        API   
      /*
      await actions.dispatch({
        type: 'REMOVE_MEMBER',
        payload: {
          groupId: member.groupId,
          userId: member.userId
        }
      });
      */
      
      //  
      actions.addNotification({
        message: `${member.user.username}   `,
        type: 'success'
      });
      
      //   
      setSelectedMember(null);
    } catch (error) {
      console.error('   :', error);
      actions.addNotification({
        message: `   : ${error.message}`,
        type: 'error'
      });
    } finally {
      setLoading(false);
    }
  };
  
  //    
  const handleTransferLeadership = async (member) => {
    if (currentUserRole !== 'leader') {
      actions.addNotification({
        message: '     ',
        type: 'error'
      });
      return;
    }
    
    if (member.userId === leaderId) {
      actions.addNotification({
        message: '    ',
        type: 'error'
      });
      return;
    }
    
    if (!window.confirm(` ,     ${member.user.username}?`)) {
      return;
    }
    
    setLoading(true);
    
    try {
      //        API   
      /*
      await actions.dispatch({
        type: 'TRANSFER_LEADERSHIP',
        payload: {
          groupId: member.groupId,
          newLeaderId: member.userId
        }
      });
      */
      
      //  
      actions.addNotification({
        message: `  ${member.user.username}`,
        type: 'success'
      });
      
      //   
      setSelectedMember(null);
    } catch (error) {
      console.error('   :', error);
      actions.addNotification({
        message: `   : ${error.message}`,
        type: 'error'
      });
    } finally {
      setLoading(false);
    }
  };
  
  return (
    <div className={styles.memberList}>
      {members.map(member => {
        const roleInfo = getRoleInfo(member.role);
        const isLeader = member.userId === leaderId;
        const isCurrentUser = member.userId === currentUserId;
        const canManage = canManageMember(member) && !isCurrentUser;
        
        return (
          <div 
            key={member.userId} 
            className={`${styles.memberItem} ${selectedMember?.userId === member.userId ? styles.selected : ''}`}
            onClick={() => setSelectedMember(selectedMember?.userId === member.userId ? null : member)}
          >
            <div className={styles.memberInfo}>
              <div className={styles.nameWrapper}>
                <span className={styles.role}>{roleInfo.icon}</span>
                <span className={styles.username}>
                  {member.user.username}
                  {isCurrentUser && <span className={styles.currentUser}> ()</span>}
                </span>
              </div>
              
              <div className={styles.stats}>
                <span className={styles.level}>
                   {member.user.cultivationLevel}
                </span>
                <span className={styles.specialization}>
                  {getSpecIcon(member.specialization)} {getSpecName(member.specialization)}
                </span>
              </div>
            </div>
            
            {/*    */}
            {selectedMember?.userId === member.userId && (
              <div className={styles.memberActions}>
                {isLeader && currentUserRole === 'leader' && (
                  <span className={styles.leaderNote}> </span>
                )}
                
                {currentUserRole === 'leader' && !isLeader && member.role === 'member' && (
                  <Button 
                    className={styles.promoteButton}
                    onClick={() => handlePromote(member)}
                    disabled={loading}
                  >
                      
                  </Button>
                )}
                
                {currentUserRole === 'leader' && member.role === 'officer' && (
                  <Button 
                    className={styles.demoteButton}
                    onClick={() => handleDemote(member)}
                    disabled={loading}
                  >
                      
                  </Button>
                )}
                
                {currentUserRole === 'leader' && !isLeader && (
                  <Button 
                    className={styles.transferButton}
                    onClick={() => handleTransferLeadership(member)}
                    disabled={loading}
                  >
                     
                  </Button>
                )}
                
                {canManage && (
                  <Button 
                    className={styles.kickButton}
                    onClick={() => handleKick(member)}
                    disabled={loading}
                  >
                      
                  </Button>
                )}
              </div>
            )}
          </div>
        );
      })}
    </div>
  );
};

export default MemberList;
