const { Model, DataTypes } = require('../services/database');
const { unifiedDatabase } = require('../services/database-connection-manager');

//   Sequelize 
let sequelizeInstance = null;
async function getSequelize() {
  if (!sequelizeInstance) {
    const result = await unifiedDatabase.getSequelizeInstance();
    sequelizeInstance = result.db;
  }
  return sequelizeInstance;
}

class TechniqueEffect extends Model {
  static associate(models) {
    //     
    TechniqueEffect.belongsTo(models.Technique, {
      foreignKey: 'technique_id', //  snake_case
      as: 'technique'
    });
    //     (   TechniqueEffectStat)
    // TechniqueEffect.hasMany(models.TechniqueEffectStat, { foreignKey: 'effect_id', as: 'stats' });
  }
}

// :   super   
TechniqueEffect.init = async function() {
  const sequelize = await getSequelize();
  
  //   init  
  return Model.init.call(this, {
    id: { //  id  SQL
      type: DataTypes.INTEGER,
      autoIncrement: true,
      primaryKey: true,
      allowNull: false
    },
    techniqueId: { //  technique_id  SQL
      type: DataTypes.STRING(30), //  
      allowNull: false,
      field: 'technique_id',
      references: {
        model: 'techniques', //    lowercase
        key: 'id'
      },
      onUpdate: 'CASCADE',
      onDelete: 'CASCADE'
    },
    type: { //  type  SQL
      type: DataTypes.STRING(30), //  
      allowNull: false
    },
    name: { //  name  SQL
      type: DataTypes.STRING(100), //  
      allowNull: false
    },
    duration: { //  duration  SQL
      type: DataTypes.INTEGER,
      defaultValue: 0
    },
    damage: { //  damage  SQL
      type: DataTypes.INTEGER
      // defaultValue 
    },
    damageType: { //  damage_type  SQL
      type: DataTypes.STRING(20),
      field: 'damage_type'
    },
    healing: { //  healing  SQL
      type: DataTypes.INTEGER
      // defaultValue 
    }
    //  stats 
  }, {
    sequelize,
    modelName: 'TechniqueEffect',
    tableName: 'technique_effects', // :    snake_case
    timestamps: false               //  SQL   created_at, updated_at
  });
};

//      
(async () => {
  try {
    await TechniqueEffect.init();
    console.log('TechniqueEffect  ');
  } catch (error) {
    console.error('   TechniqueEffect:', error);
    console.error(error.stack);
  }
})();

module.exports = TechniqueEffect;
