/**
 *   ,      
 * @param {number} level -   
 * @returns {number} -   
 */
function calculateExpForLevel(level) {
  return Math.floor(100 * Math.pow(1.5, level - 1));
}

/**
 *          
 * @param {number} stat -   
 * @param {number} level -  
 * @returns {number} -   
 */
function calculateStatBonus(stat, level) {
  return Math.floor(stat * (1 + (level - 1) * 0.05));
}

/**
 *       
 * @param {Object} pet -  
 * @returns {Object} -    
 */
function calculateCombatBonus(pet) {
  return {
    attack: Math.floor((pet.strength || 0) * 0.8 + (pet.intelligence || 0) * 0.2),
    defense: Math.floor((pet.vitality || 0) * 0.7 + (pet.strength || 0) * 0.3),
    speed: Math.floor((pet.agility || 0) * 0.9 + (pet.intelligence || 0) * 0.1),
    critChance: Math.min(0.5, ((pet.agility || 0) * 0.01 + (pet.intelligence || 0) * 0.005)),
    healthBonus: Math.floor((pet.vitality || 0) * 5),
    energyBonus: Math.floor((pet.spirit || 0) * 3)
  };
}

/**
 *         
 * @param {Object} pet -   
 * @param {Array} abilities -  
 * @param {Array} stats -   
 * @param {Array} growthRates -      
 * @returns {Object} -     
 */
function convertPetFromDB(pet, abilities = [], stats = {}, growthRates = {}) {
  return {
    id: pet.id,
    name: pet.name,
    description: pet.description,
    type: pet.type,
    element: pet.element,
    rarity: pet.rarity,
    unlockCondition: pet.unlock_condition,
    maxLevel: pet.max_level || 100,
    evolutionPath: pet.evolution_path || [],
    abilities: abilities.map(ability => ({
      name: ability.name,
      description: ability.description,
      unlockLevel: ability.unlock_level,
      cooldown: ability.cooldown,
      energyCost: ability.energy_cost,
      effects: JSON.parse(ability.effects || '{}')
    })),
    baseStats: stats,
    growthRates: growthRates,
    icon: pet.icon
  };
}

/**
 *        
 * @returns {Promise<Array>} -   
 */
async function loadSpiritPets() {
  try {
    //   Sequelize
    const seq = await getSequelizeInstance();
    
    //      
    const petsData = await unifiedDatabase.query(
      'SELECT * FROM spirit_pets',
      { type: seq.QueryTypes.SELECT }
    );
    
    if (petsData.length === 0) {
      return [];
    }
    
    //  ID 
    const petIds = petsData.map(pet => pet.id);
    
    //    
    const abilitiesQuery = `
      SELECT * FROM spirit_pet_abilities 
      WHERE pet_id IN (${petIds.map(id => `'${id}'`).join(',')})
    `;
    const abilitiesData = await unifiedDatabase.query(
      abilitiesQuery,
      { type: seq.QueryTypes.SELECT }
    );
    
    //     
    const statsQuery = `
      SELECT * FROM spirit_pet_stats 
      WHERE pet_id IN (${petIds.map(id => `'${id}'`).join(',')})
    `;
    const statsData = await unifiedDatabase.query(
      statsQuery,
      { type: seq.QueryTypes.SELECT }
    );
    
    //     
    const growthRatesQuery = `
      SELECT * FROM spirit_pet_growth_rates 
      WHERE pet_id IN (${petIds.map(id => `'${id}'`).join(',')})
    `;
    const growthRatesData = await unifiedDatabase.query(
      growthRatesQuery,
      { type: seq.QueryTypes.SELECT }
    );
    
    //    ID 
    const abilitiesByPetId = {};
    abilitiesData.forEach(ability => {
      if (!abilitiesByPetId[ability.pet_id]) {
        abilitiesByPetId[ability.pet_id] = [];
      }
      abilitiesByPetId[ability.pet_id].push(ability);
    });
    
    //    ID 
    const statsByPetId = {};
    statsData.forEach(stat => {
      if (!statsByPetId[stat.pet_id]) {
        statsByPetId[stat.pet_id] = {};
      }
      statsByPetId[stat.pet_id][stat.stat_name] = stat.stat_value;
    });
    
    //     ID 
    const growthRatesByPetId = {};
    growthRatesData.forEach(rate => {
      if (!growthRatesByPetId[rate.pet_id]) {
        growthRatesByPetId[rate.pet_id] = {};
      }
      growthRatesByPetId[rate.pet_id][rate.stat_name] = rate.growth_rate;
    });
    
    //     
    const formattedPets = petsData.map(pet => 
      convertPetFromDB(
        pet,
        abilitiesByPetId[pet.id] || [],
        statsByPetId[pet.id] || {},
        growthRatesByPetId[pet.id] || {}
      )
    );
    
    //     
    spiritPets = formattedPets;
    
    return formattedPets;
  } catch (error) {
    console.error('       :', error);
    //      
    return spiritPets;
  }
}

/**
 *      ID
 * @param {string} id - ID 
 * @returns {Promise<Object|null>} -   null,   
 */
async function getSpiritPetByIdAsync(id) {
  try {
    //   Sequelize
    const seq = await getSequelizeInstance();
    
    //     
    const petData = await unifiedDatabase.query(
      'SELECT * FROM spirit_pets WHERE id = :id',
      { 
        replacements: { id },
        type: seq.QueryTypes.SELECT 
      }
    );
    
    if (petData.length === 0) {
      return null;
    }
    
    //  ,      
    const [abilitiesData, statsData, growthRatesData] = await Promise.all([
      unifiedDatabase.query(
        'SELECT * FROM spirit_pet_abilities WHERE pet_id = :id',
        { 
          replacements: { id },
          type: seq.QueryTypes.SELECT 
        }
      ),
      unifiedDatabase.query(
        'SELECT * FROM spirit_pet_stats WHERE pet_id = :id',
        { 
          replacements: { id },
          type: seq.QueryTypes.SELECT 
        }
      ),
      unifiedDatabase.query(
        'SELECT * FROM spirit_pet_growth_rates WHERE pet_id = :id',
        { 
          replacements: { id },
          type: seq.QueryTypes.SELECT 
        }
      )
    ]);
    
    //     
    const stats = {};
    statsData.forEach(stat => {
      stats[stat.stat_name] = stat.stat_value;
    });
    
    //      
    const growthRates = {};
    growthRatesData.forEach(rate => {
      growthRates[rate.stat_name] = rate.growth_rate;
    });
    
    //    
    return convertPetFromDB(
      petData[0], 
      abilitiesData,
      stats,
      growthRates
    );
  } catch (error) {
    console.error(`      ID ${id}:`, error);
    //      
    return getSpiritPetById(id);
  }
}

/**
 *        ID (  )
 * @param {string} id - ID 
 * @returns {Object|null} -   null,   
 */
