import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { useGame } from '../../context/GameContext';

const Container = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
              url('/assets/images/character-creation-bg.jpg');
  background-size: cover;
  background-position: center;
`;

const CreationPanel = styled.div`
  width: 800px;
  min-height: 600px;
  background: rgba(30, 30, 30, 0.95);
  border: 1px solid #d4af37;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0 0 20px rgba(212, 175, 55, 0.5);
  color: #f0f0f0;
  display: flex;
  flex-direction: column;
`;

const Title = styled.h1`
  font-family: 'Ma Shan Zheng', cursive;
  text-align: center;
  color: #d4af37;
  margin-bottom: 30px;
  font-size: 2rem;
  text-shadow: 0 0 5px rgba(212, 175, 55, 0.5);
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
  gap: 20px;
`;

const Row = styled.div`
  display: flex;
  gap: 20px;
  justify-content: space-between;
`;

const Column = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const Label = styled.label`
  color: #d4af37;
  font-size: 1rem;
`;

const Input = styled.input`
  width: 100%;
  padding: 10px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 4px;
  color: #f0f0f0;
  font-size: 1rem;
  
  &:focus {
    outline: none;
    box-shadow: 0 0 5px rgba(212, 175, 55, 0.5);
  }
`;

const Select = styled.select`
  width: 100%;
  padding: 10px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 4px;
  color: #f0f0f0;
  font-size: 1rem;
  
  &:focus {
    outline: none;
    box-shadow: 0 0 5px rgba(212, 175, 55, 0.5);
  }
  
  option {
    background: #1a1a1a;
  }
`;

const StatsContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
`;

const StatRow = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
`;

const StatLabel = styled.span`
  width: 120px;
  color: #d4af37;
`;

const StatValue = styled.span`
  width: 40px;
  text-align: center;
`;

const StatButton = styled.button`
  width: 30px;
  height: 30px;
  background: #d4af37;
  border: none;
  border-radius: 4px;
  color: #000;
  font-weight: bold;
  cursor: pointer;
  
  &:disabled {
    background: #666;
    cursor: not-allowed;
  }
  
  &:hover:not(:disabled) {
    background: #f4cf47;
  }
`;

const Description = styled.textarea`
  width: 100%;
  height: 100px;
  padding: 10px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 4px;
  color: #f0f0f0;
  font-size: 1rem;
  resize: none;
  
  &:focus {
    outline: none;
    box-shadow: 0 0 5px rgba(212, 175, 55, 0.5);
  }
`;

const Button = styled.button`
  padding: 12px;
  background: #d4af37;
  color: #000;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
  margin-top: 20px;
  
  &:hover {
    background: #f4cf47;
    box-shadow: 0 0 10px rgba(212, 175, 55, 0.7);
  }
  
  &:disabled {
    background: #666;
    cursor: not-allowed;
  }
`;

const ErrorMessage = styled.div`
  color: #ff6b6b;
  text-align: center;
  margin-top: 10px;
`;

const PointsRemaining = styled.div`
  text-align: center;
  margin-top: 10px;
  color: #d4af37;
  font-size: 1.1rem;
`;

function CharacterCreationPage() {
  const navigate = useNavigate();
  const { actions } = useGame();
  
  const [formData, setFormData] = useState({
    name: '',
    gender: 'male',
    region: 'central',
    background: 'commoner',
    description: '',
  });
  
  const [stats, setStats] = useState({
    strength: 10,
    intellect: 10,
    spirit: 10,
    agility: 10,
    health: 10,
  });
  
  const [error, setError] = useState('');
  const [pointsRemaining, setPointsRemaining] = useState(10);
  
  const regions = {
    central: ' ',
    mountain: ' ',
    coastal: ' ',
    desert: ' ',
    forest: ' ',
  };
  
  const backgrounds = {
    commoner: '',
    noble: '',
    merchant: '',
    warrior: '',
    scholar: '',
  };
  
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };
  
  const handleStatChange = (stat, change) => {
    const newValue = stats[stat] + change;
    
    if (change > 0 && pointsRemaining <= 0) return;
    if (newValue < 10 || newValue > 20) return;
    
    setStats(prev => ({
      ...prev,
      [stat]: newValue
    }));
    
    setPointsRemaining(prev => prev - change);
  };
  
  const handleSubmit = (e) => {
    e.preventDefault();
    setError('');
    
    // 
    if (!formData.name.trim()) {
      setError(',   ');
      return;
    }
    
    if (pointsRemaining > 0) {
      setError(',    ');
      return;
    }
    
    //  
    actions.updatePlayer({
      name: formData.name,
      gender: formData.gender,
      region: formData.region,
      background: formData.background,
      description: formData.description,
    });
    
    actions.updatePlayerStats(stats);
    
    //       
    const startingItems = getStartingItems(formData.background);
    startingItems.forEach(item => actions.addItem(item));
    
    //   
    navigate('/game');
  };
  
  const getStartingItems = (background) => {
    const items = [
      //    
      {
        id: 'basic_cultivation_manual',
        name: '   ',
        type: 'book',
        quality: 'common',
        description: '    .'
      },
      {
        id: 'qi_gathering_pill',
        name: '  ',
        type: 'consumable',
        quality: 'common',
        quantity: 3,
        description: '   .'
      }
    ];
    
    //      
    switch (background) {
      case 'noble':
        items.push({
          id: 'jade_pendant',
          name: ' ',
          type: 'accessory',
          quality: 'uncommon',
          description: ' ,   .'
        });
        break;
        
      case 'warrior':
        items.push({
          id: 'training_sword',
          name: ' ',
          type: 'weapon',
          quality: 'common',
          description: ',     .'
        });
        break;
        
      case 'scholar':
        items.push({
          id: 'ancient_scroll',
          name: ' ',
          type: 'book',
          quality: 'uncommon',
          description: '    .'
        });
        break;
        
      case 'merchant':
        items.push({
          id: 'spirit_stones',
          name: ' ',
          type: 'currency',
          quality: 'common',
          quantity: 10,
          description: '   .'
        });
        break;
    }
    
    return items;
  };
  
  return (
    <Container>
      <CreationPanel>
        <Title> </Title>
        <Form onSubmit={handleSubmit}>
          <Row>
            <Column>
              <Label> </Label>
              <Input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleInputChange}
                placeholder=" "
              />
              
              <Label></Label>
              <Select
                name="gender"
                value={formData.gender}
                onChange={handleInputChange}
              >
                <option value="male"></option>
                <option value="female"></option>
              </Select>
            </Column>
            
            <Column>
              <Label> </Label>
              <Select
                name="region"
                value={formData.region}
                onChange={handleInputChange}
              >
                {Object.entries(regions).map(([key, value]) => (
                  <option key={key} value={key}>{value}</option>
                ))}
              </Select>
              
              <Label></Label>
              <Select
                name="background"
                value={formData.background}
                onChange={handleInputChange}
              >
                {Object.entries(backgrounds).map(([key, value]) => (
                  <option key={key} value={key}>{value}</option>
                ))}
              </Select>
            </Column>
          </Row>
          
          <Label> </Label>
          <Description
            name="description"
            value={formData.description}
            onChange={handleInputChange}
            placeholder="   ..."
          />
          
          <StatsContainer>
            <PointsRemaining>
                : {pointsRemaining}
            </PointsRemaining>
            
            {Object.entries(stats).map(([stat, value]) => (
              <StatRow key={stat}>
                <StatLabel>
                  {stat === 'strength' && ''}
                  {stat === 'intellect' && ''}
                  {stat === 'spirit' && ''}
                  {stat === 'agility' && ''}
                  {stat === 'health' && ''}
                </StatLabel>
                <StatButton
                  type="button"
                  onClick={() => handleStatChange(stat, -1)}
                  disabled={value <= 10}
                >
                  -
                </StatButton>
                <StatValue>{value}</StatValue>
                <StatButton
                  type="button"
                  onClick={() => handleStatChange(stat, 1)}
                  disabled={value >= 20 || pointsRemaining <= 0}
                >
                  +
                </StatButton>
              </StatRow>
            ))}
          </StatsContainer>
          
          {error && <ErrorMessage>{error}</ErrorMessage>}
          
          <Button type="submit">
               
          </Button>
        </Form>
      </CreationPanel>
    </Container>
  );
}

export default CharacterCreationPage; 