/**
 *     CultivationService     
 *             
 */
import { isServerEnvironment } from '../sequelize-config';

//  API-   
import CultivationServiceAPI from './cultivation-api'; //  API ( )

//    
function isTrueServerEnvironment() {
  return (
    isServerEnvironment &&
    typeof process !== 'undefined' &&
    process.versions &&
    process.versions.node
  );
}

//       
async function getCultivationService() {
  if (isTrueServerEnvironment()) {
    try {
      //    
      //    webpack      
      const serverModule = await import('./cultivation-service');
      return serverModule.default || serverModule;
    } catch (error) {
      console.error('    CultivationService:', error);
      console.warn(' API  -  ');
      return CultivationServiceAPI;
    }
  }
  //     API 
  return CultivationServiceAPI;
}

//   ,      
const CultivationService = new Proxy({}, {
  get: function(target, prop) {
    return async function(...args) {
      const service = await getCultivationService();
      if (typeof service[prop] === 'function') {
        return service[prop](...args);
      } else {
        throw new Error(` ${prop}    CultivationService`);
      }
    };
  }
});

export default CultivationService;