import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { useGame } from '../../context/GameContext';
import apiService from '../../services/api';

const LoginContainer = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
`;

const LoginPanel = styled.div`
  width: 400px;
  background: rgba(30, 30, 30, 0.9);
  border: 1px solid #d4af37;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0 0 20px rgba(212, 175, 55, 0.5);
  color: #f0f0f0;
`;

const Title = styled.h1`
  font-family: 'Ma Shan Zheng', cursive;
  text-align: center;
  color: #d4af37;
  margin-bottom: 30px;
  font-size: 2.5rem;
  text-shadow: 0 0 5px rgba(212, 175, 55, 0.5);
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
`;

const InputGroup = styled.div`
  margin-bottom: 20px;
`;

const Label = styled.label`
  display: block;
  margin-bottom: 5px;
  color: #d4af37;
`;

const Input = styled.input`
  width: 100%;
  padding: 10px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #d4af37;
  border-radius: 4px;
  color: #f0f0f0;
  font-size: 1rem;
  
  &:focus {
    outline: none;
    box-shadow: 0 0 5px rgba(212, 175, 55, 0.5);
  }
`;

const Button = styled.button`
  padding: 12px;
  background: #d4af37;
  color: #000;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
  margin-top: 10px;
  
  &:hover {
    background: #f4cf47;
    box-shadow: 0 0 10px rgba(212, 175, 55, 0.7);
  }
  
  &:disabled {
    background: #666;
    cursor: not-allowed;
  }
`;

const SwitchMode = styled.p`
  text-align: center;
  margin-top: 20px;
  color: #aaa;
  
  span {
    color: #d4af37;
    cursor: pointer;
    
    &:hover {
      text-decoration: underline;
    }
  }
`;

const ErrorMessage = styled.div`
  color: #ff6b6b;
  margin-top: 15px;
  text-align: center;
`;

const LoadingContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 20px;
`;

const LoadingText = styled.p`
  color: #d4af37;
  font-size: 1.2rem;
  margin-top: 10px;
`;

function LoginPage() {
  const [isLogin, setIsLogin] = useState(true);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isPageLoading, setIsPageLoading] = useState(true);
  
  const navigate = useNavigate();
  const { state, actions } = useGame();
  
  useEffect(() => {
    // ,   
    const checkAuth = async () => {
      try {
        const isAuthenticated = await apiService.checkAuth();
        
        if (isAuthenticated) {
          //   ,    
          const gameState = await apiService.loadGameState();
          
          if (gameState) {
            //   
            actions.loadGame(gameState);
            
            // ,   
            if (gameState.player && gameState.player.name) {
              console.log(' ,   :', gameState.player.name);
              navigate('/game');
            } else {
              console.log('  ,    ');
              navigate('/character-creation');
            }
          } else {
            //    ,    
            console.log('  ,    ');
            navigate('/character-creation');
          }
        }
      } catch (error) {
        console.error('   :', error);
      } finally {
        setIsPageLoading(false);
      }
    };
    
    checkAuth();
  }, [navigate, actions]);
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    
    // 
    if (!username || !password) {
      setError(',   ');
      return;
    }
    
    if (!isLogin && password !== confirmPassword) {
      setError('  ');
      return;
    }
    
    setIsLoading(true);
    
    try {
      if (isLogin) {
        //   
        console.log('   :', username);
        await apiService.login(username, password);
        
        //   
        console.log('  ');
        const gameState = await apiService.loadGameState();
        
        if (gameState) {
          //   
          console.log('    ');
          actions.loadGame(gameState);
          
          // ,   
          if (gameState.player && gameState.player.name) {
            console.log(' ,   :', gameState.player.name);
            navigate('/game');
          } else {
            console.log('  ,    ');
            navigate('/character-creation');
          }
        } else {
          //    ,    
          console.log('  ,    ');
          navigate('/character-creation');
        }
      } else {
        // 
        console.log(' :', username);
        await apiService.register(username, password);
        
        //    
        console.log('     ');
        navigate('/character-creation');
      }
    } catch (error) {
      console.error('  :', error);
      setError(error.message);
    } finally {
      setIsLoading(false);
    }
  };
  
  if (isPageLoading) {
    return (
      <LoginContainer>
        <LoginPanel>
          <Title>  </Title>
          <LoadingContainer>
            <LoadingText> ...</LoadingText>
          </LoadingContainer>
        </LoginPanel>
      </LoginContainer>
    );
  }
  
  return (
    <LoginContainer>
      <LoginPanel>
        <Title>  </Title>
        <Form onSubmit={handleSubmit}>
          <InputGroup>
            <Label> </Label>
            <Input 
              type="text" 
              value={username} 
              onChange={(e) => setUsername(e.target.value)}
              placeholder="  "
              disabled={isLoading}
            />
          </InputGroup>
          
          <InputGroup>
            <Label></Label>
            <Input 
              type="password" 
              value={password} 
              onChange={(e) => setPassword(e.target.value)}
              placeholder=" "
              disabled={isLoading}
            />
          </InputGroup>
          
          {!isLogin && (
            <InputGroup>
              <Label> </Label>
              <Input 
                type="password" 
                value={confirmPassword} 
                onChange={(e) => setConfirmPassword(e.target.value)}
                placeholder=" "
                disabled={isLoading}
              />
            </InputGroup>
          )}
          
          {error && <ErrorMessage>{error}</ErrorMessage>}
          
          <Button type="submit" disabled={isLoading}>
            {isLoading ? '...' : (isLogin ? '' : '')}
          </Button>
        </Form>
        
        <SwitchMode>
          {isLogin ? ' ? ' : '  ? '}
          <span onClick={() => !isLoading && setIsLogin(!isLogin)}>
            {isLogin ? '' : ''}
          </span>
        </SwitchMode>
      </LoginPanel>
    </LoginContainer>
  );
}

export default LoginPage;
