import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import styled from 'styled-components';

//   
import { initDebugTools } from './utils/debugTools';
import DebugTools from './components/debug/DebugTools';

//  
import LoginPage from './components/pages/LoginPage';
import CharacterCreationPage from './components/pages/CharacterCreationPage';
import GamePage from './components/pages/GamePage';

//  
import GameTimeUpdater from './components/common/GameTimeUpdater';

//      
import { GameContextProvider as OriginalGameContextProvider } from './context/GameContext';
import { integrateRelationshipSyncer } from './utils/sectRelationshipSyncer';

//   GameContextProvider   
const GameContextProvider = integrateRelationshipSyncer(OriginalGameContextProvider);

const AppContainer = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #1a1a1a;
`;

const LoadingScreen = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: #111;
  color: #f0f0f0;
  font-family: '"Ma Shan Zheng", cursive';
  font-size: 2rem;
  flex-direction: column;
  text-align: center;
`;

const ErrorScreen = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: #111;
  color: #ff6b6b;
  font-family: '"Ma Shan Zheng", cursive';
  font-size: 1.5rem;
  flex-direction: column;
  text-align: center;
  padding: 20px;
`;

const ErrorTitle = styled.h1`
  color: #ff6b6b;
  margin-bottom: 20px;
`;

const ErrorMessage = styled.p`
  margin-bottom: 20px;
`;

const RetryButton = styled.button`
  padding: 10px 20px;
  background: #d4af37;
  color: #000;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 20px;
  font-weight: bold;
  
  &:hover {
    background: #f4cf47;
  }
`;

//    
function detectBrowser() {
  const userAgent = navigator.userAgent;
  let browserName;

  if (userAgent.match(/chrome|chromium|crios/i)) {
    browserName = "Chrome";
  } else if (userAgent.match(/firefox|fxios/i)) {
    browserName = "Firefox";
  } else if (userAgent.match(/safari/i)) {
    browserName = "Safari";
  } else if (userAgent.match(/opr\//i)) {
    browserName = "Opera";
  } else if (userAgent.match(/edg/i)) {
    browserName = "Edge";
  } else {
    browserName = "Unknown";
  }

  return browserName;
}

function App() {
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [browserInfo, setBrowserInfo] = useState(null);
  
  useEffect(() => {
    //  
    const browser = detectBrowser();
    setBrowserInfo(browser);
    console.log(' :', browser);
    
    //   Chrome,    localStorage
    if (browser === 'Chrome') {
      try {
        // ,    
        const savedState = localStorage.getItem('gameState');
        if (savedState) {
          try {
            // ,   
            const parsedState = JSON.parse(savedState);
            
            //       Chrome
            if (parsedState && typeof parsedState === 'object') {
              // ,     
              if (!parsedState.player) parsedState.player = {};
              if (!parsedState.player.social) parsedState.player.social = {};
              if (!parsedState.player.social.relationships) parsedState.player.social.relationships = {};
              if (!parsedState.player.progress) parsedState.player.progress = {};
              if (!parsedState.player.progress.discoveries) parsedState.player.progress.discoveries = {};
              
              //   
              localStorage.setItem('gameState', JSON.stringify(parsedState));
              console.log('   Chrome');
            }
          } catch (parseError) {
            console.error('   :', parseError);
            //   ,  
            localStorage.removeItem('gameState');
            console.log('  ');
          }
        }
      } catch (err) {
        console.error('    localStorage:', err);
      }
    }
    
    //   
    const timer = setTimeout(() => {
      try {
        setIsLoading(false);
      } catch (err) {
        console.error('   :', err);
        setError('    . ,   .');
        setIsLoading(false);
      }
    }, 2000);
    
    return () => clearTimeout(timer);
  }, []);
  
  //   
  useEffect(() => {
    //      store,
    // ..   React Context  Redux
    initDebugTools();
    
    //  ,  ,      ,
    //       
    if (typeof window !== 'undefined') {
      window.showTimeHelp = () => {
        console.group('      :');
        console.log('1.    : getGameTime()');
        console.log('2.        ');
        console.log('3.  : gameDebug.getTime()');
        console.log('4.       : getGameTime().formattedTime');
        console.log('5.     : getGameTime().daytimeName');
        console.groupEnd();
      };
      
      //     
      setTimeout(() => {
        console.info('       : showTimeHelp()');
      }, 3000);
    }
  }, []);
  
  const handleRetry = () => {
    window.location.reload();
  };
  
  if (isLoading) {
    return (
      <LoadingScreen>
        <h1>  </h1>
        <p>  ...</p>
        {browserInfo && <p style={{ fontSize: '1rem', marginTop: '10px' }}>: {browserInfo}</p>}
      </LoadingScreen>
    );
  }
  
  if (error) {
    return (
      <ErrorScreen>
        <ErrorTitle> </ErrorTitle>
        <ErrorMessage>{error}</ErrorMessage>
        {browserInfo && <p>: {browserInfo}</p>}
        <RetryButton onClick={handleRetry}> </RetryButton>
      </ErrorScreen>
    );
  }
  
  return (
    <GameContextProvider>
      {/* GameTimeUpdater      */}
      <GameTimeUpdater debug={true} />
      {/*   */}
      <DebugTools enabled={true} />
      <Router>
        <AppContainer>
          <Routes>
            <Route path="/" element={<LoginPage />} />
            <Route path="/character-creation" element={<CharacterCreationPage />} />
            <Route path="/game" element={<GamePage />} />
          </Routes>
        </AppContainer>
      </Router>
    </GameContextProvider>
  );
}

export default App;
